import { ui } from "../../ui/layaMaxUI";
import CardManager from "./CardManager";
import FightCellRuntime from "./FightCellRuntime";

export default class OutCellRuntime extends ui.outCellUI {

    original: OutCellRuntime = null;

    originalIndex: number = -1;

    isTweening: boolean = false;

    cellArray: FightCellRuntime[] = null;

    private _cardData: any = null;

    _cost: number = 10;

    _cd: number = 8;

    increaseSpeed: number = 0;
    fightData: any = null;
    onAwake() {
        this.cost = 10;
    }

    onEnable(): void {
    }

    onDisable(): void {

    }

    public set dataSource(data: any) {
        if (!data) return
        this.bg.disabled = false;
        if (this.cellArray && this.cellArray.length > 0) {
            for (let i = 0; i < this.cellArray.length; i++) {
                const cell = this.cellArray[i];
                if (cell.dataSource && cell.dataSource.id > 0) {
                    if (cell.dataSource.id == data.id) {
                        this.bg.disabled = true;
                        break;
                    }
                }
            }
        }
        this._cardData = data;

        if (data.id == 0) {
            this.bg.visible = false;
            return;
        } else {
            this.bg.visible = true;
        }
        this.setRaceIcon(data.type);
        let color = CardManager.instance.getColorByEXP(data.exp);
        this.setColor(color);
        let fightLevel = CardManager.instance.getFightLevelByEXP(data.exp);
        this.fightData = CardManager.instance.getFightData(data.datatype, fightLevel);
        this.propList.array = [{ icon: { skin: 'compose/Ui_Attribute1.png' }, prop: { text: this.fixed(this.fightData.atk) } }, { icon: { skin: 'compose/Ui_Attribute2.png' }, prop: { text: this.fixed(this.fightData.hp) } }, { icon: { skin: 'compose/Ui_Attribute3.png' }, prop: { text: this.fixed(Math.floor((2000 - this.fightData.atkgap) / 10)) } }, { icon: { skin: 'compose/Ui_Attribute4.png' }, prop: { text: this.fixed(this.fightData.speed) } }]
        this.propList.refresh();
        if (data.skin != '') {
            let nameArray = data.skin.split('-');
            if (data.type == 1) {
                this.foot1.visible = true;
                this.head.skin = `part/${nameArray[0]}1.png`
                this.foot1.skin = `part/${nameArray[2]}2.png`
                this.body.skin = `part/${nameArray[1]}3.png`
                this.foot.skin = `part/${nameArray[2]}4.png`

            } else {
                this.foot1.visible = false;
                this.head.skin = `part/${nameArray[0]}1.png`
                this.body.skin = `part/${nameArray[1]}2.png`
                this.foot.skin = `part/${nameArray[2]}3.png`
            }
        }
    }

    setRaceIcon(type: number) {
        let iconSkin = '';
        if (type == 1) {
            iconSkin = 'card/Ui_LightP.png';
        } else if (type == 2) {
            iconSkin = 'card/Ui_LightY.png';
        } else if (type == 3) {
            iconSkin = 'card/Ui_LightG.png';
        }
        this.flagIcon.skin = iconSkin;
    }

    fixed(value: number) {
        if (value % 1 != 0) {
            return value.toFixed(1);
        } else {
            return `${value}`;
        }
    }

    setColor(color: string) {
        let iconSkin = '';
        let blackSkin = '';
        this.cardLight.visible = false;
        if (color == 'white') {
            iconSkin = 'card/Ui_CardBg0.png';
            blackSkin = 'card/Ui_CardBlack0.png';
        } else if (color == 'green') {
            iconSkin = 'card/Ui_CardBg1.png';
            blackSkin = 'card/Ui_CardBlack1.png';
            this.cardLight.skin = 'card/Ui_Cardlight1.png';
            this.cardLight.visible = true;
        } else if (color == 'blue') {
            iconSkin = 'card/Ui_CardBg2.png';
            blackSkin = 'card/Ui_CardBlack2.png';
            this.cardLight.skin = 'card/Ui_Cardlight2.png';
            this.cardLight.visible = true;
        } else if (color == 'purple') {
            iconSkin = 'card/Ui_CardBg3.png';
            blackSkin = 'card/Ui_CardBlack3.png';
            this.cardLight.skin = 'card/Ui_Cardlight3.png';
            this.cardLight.visible = true;
        }
        this.cardBlack.skin = blackSkin;
        this.bg.skin = iconSkin;
    }

    public get dataSource() {
        return this._cardData;
    }

    public clone() {
        let cell = new OutCellRuntime();
        cell.original = this;
        cell.width = this.width;
        cell.height = this.height;
        cell.dataSource = JSON.parse(JSON.stringify(this._cardData));
        return cell;
    }

    public get cost() {
        return this._cost;
    }

    public set cost(value: number) {
        this._cost = value;
    }

    setOutFight(target: OutCellRuntime | FightCellRuntime, index: number) {
        this.originalIndex = index;
        this.isTweening = true;
        this._cardData = target.dataSource;
        this.visible = false
        let list: Laya.List = <Laya.List>this.parent.parent;
        let pos = list.localToGlobal(new Laya.Point(this.x, this.y), false, this.scene.parent.scene);
        let destWidth = list.scaleX * this.scaleX * this.width;
        let destHeight = list.scaleY * this.scaleY * this.height;
        Laya.Tween.to(target, { width: destWidth, height: destHeight, x: pos.x, y: pos.y }, 300, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
            this.isTweening = false;
            this.visible = true;
            target.destroy();
            this.dataSource = this._cardData;
            this.bg.disabled = false;
        }));
    }

    setEmpty() {
        this.originalIndex = -1;
        this._cardData = CardManager.instance.getCardData(-1);
        this.dataSource = this._cardData;
    }

    setNew(card: FightCellRuntime) {
        let point = this.localToGlobal(new Laya.Point, false)
        Laya.Tween.to(card, { scaleX: 1, scaleY: 1, x: point.x, y: point.y }, 300, Laya.Ease.sineOut, new Laya.Handler(this, () => {
            let list = this.parent.parent as Laya.List;
            list.array[list.array.length - 1] = card.dataSource;
            this.dataSource = card.dataSource;
            card.destroy();
        }))
    }

    openInfo(bool: boolean, type: number) {
        this.selectBg.visible = bool;
        this.info.visible = bool;
        if (!bool) {
            this.next.visible = false;
            this.out.visible = false;
        } else {
            this.showBg.play(0, false);
            if (type == 0) {
                this.next.visible = true;
                this.out.visible = false;
            } else if (type == 1) {
                this.next.visible = false;
                this.out.visible = true;
            }
        }
    }
}