import GEvent from "../../GameFrame/event/GEvent";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import { ui } from "../../ui/layaMaxUI";
import { EventTypeEnum } from "../GEnum";
import DivisionRuntime from "./DivisionRuntime";

export default class UpDivisionRuntime extends ui.upDivisionUI {
    divisionRecordLocal: LocalStorageData;
    goldLocal: LocalStorageData;
    division: number;
    onMouseEvent() {
        this.goldLocal.value += Number(this.moneyAdd.text);
        let big = Math.ceil(this.division / 3);
        let record = this.divisionRecordLocal.value;
        record[big - 1] = 1;
        this.divisionRecordLocal.value = record;
        let houseLocal = DataManager.get<LocalStorageData>(`house`);
        let newCardIdLocal = DataManager.get<LocalStorageData>('newCardId');
        let houseArr: any[] = houseLocal.value;
        for (let i = 0; i < 3; i++) {
            let data: any;
            if (i == 0) {
                data = this.fightCell1.dataSource;
            } else if (i == 1) {
                data = this.fightCell2.dataSource;
            } else if (i == 2) {
                data = this.fightCell3.dataSource;
            }
            data.id = newCardIdLocal.value;
            newCardIdLocal.value++;
            houseArr.push(data);
            houseLocal.value = houseArr;
        }
        GEvent.event(EventTypeEnum.CardHouseChange);
        this.initReward();
    }
    onAwake() {
        this.divisionRecordLocal = DataManager.get<LocalStorageData>('divisionRecord');
        this.goldLocal = DataManager.get<LocalStorageData>('gold');
        this.receiveBtn.on(Laya.Event.CLICK, this, this.onMouseEvent);
    }

    initReward() {
        let big = Math.ceil(this.division / 3);
        let record = this.divisionRecordLocal.value;
        if (record[big - 1] == 1) {
            this.receiveBtn.skin = 'rank/Btn_received.png'
            this.receiveBtn.mouseEnabled = false;
        } else {
            this.receiveBtn.skin = 'rank/Btn_receive.png'
            this.receiveBtn.mouseEnabled = true;
        }
        let pvpConfig = DataManager.get<any>('pvp');
        this.moneyAdd.text = pvpConfig[this.division - 1].rankRewardCoin;
        let cardId: number[] = JSON.parse(pvpConfig[this.division - 1].rankRewardCard);
        let unit = DataManager.get<any[]>('unit');
        let index = 0;
        for (let i = 0; i < cardId.length; i++) {
            let id = cardId[i];
            for (let i = 0; i < unit.length; i++) {
                let unitData = unit[i]
                if (id == unitData.id) {
                    let skin = `${unitData.name}-${unitData.name}-${unitData.name}`;
                    let cardData = { id: unitData.id + 1, type: unitData.type, skin: skin, attackDelay: unitData.atkgap, hp: unitData.hp, attackDistance: unitData.atkRange, def: unitData.def, moveSpeed: unitData.speed, color: 'green', cost: unitData.cost };
                    if (index == 0) {
                        this.fightCell1.dataSource = cardData;
                    } else if (index == 1) {
                        this.fightCell2.dataSource = cardData;
                    } else if (index == 2) {
                        this.fightCell3.dataSource = cardData;
                    }
                    index++;
                }
            }
        }
    }

    setDivision(division: number) {
        this.division = division;
        (<DivisionRuntime>this.curDivision).division = this.division;
        (<DivisionRuntime>this.lastDivision).division = this.division - 1;
        this.initReward();
    }
}