import { ui } from "../../ui/layaMaxUI";
import RankCellRuntime from "./RankCellRuntime";

export default class RankRuntime extends ui.rankUI {
    rankCellArr: RankCellRuntime[] = [];
    onAwake() {
        this.initRank();
    }
    initRank() {
        this.rankPanel.vScrollBarSkin = '';
        let dataArr = [
            { name: '我是个人的名字', score: 1000, division: 1 },
            { name: '我是个人的名字', score: 1000, division: 1 },
            { name: '我是个人的名字', score: 1000, division: 1 },
            { name: '我是个人的名字', score: 1000, division: 1 },
            { name: '我是个人的名字', score: 1000, division: 1 },
            { name: '我是个人的名字', score: 1000, division: 1 },
            { name: '我是个人的名字', score: 1000, division: 1 },
            { name: '我是个人的名字', score: 1000, division: 1 },
            { name: '我是个人的名字', score: 1000, division: 1 },
            { name: '我是个人的名字', score: 1000, division: 1 },
        ]
        let recordY = 15;
        let gap = 5;
        for (let i = 0; i < dataArr.length; i++) {
            let data = dataArr[i];
            let cell = new RankCellRuntime();
            if (i == 0) {
                cell.scale(1, 1);
            } else if (i == 1) {
                cell.scale(0.96, 0.96);
            } else {
                cell.scale(0.91, 0.91);
            }
            if (i == 0) {
                cell.isSelf = true;
            } else {
                cell.isSelf = false;
            }
            cell.y = recordY;
            recordY = recordY + cell.height * cell.scaleY + gap;
            cell.dataSource = data;
            cell.index = i + 1;
            this.rankPanel.addChild(cell);
            this.rankCellArr.push(cell);
        }
    }
}