import UserDataManager from "./UserDataManager";

export default class NetManager {
    private static _instance: NetManager = null;
    /**
     * 资源管理中心
     */
    public static init() {
        if (NetManager._instance == null) {
            NetManager._instance = new NetManager;
        }
    }
    private constructor() {
    }
    public static send(url: string, method: string, data: any, callBack: Function) {
        let request = new Laya.HttpRequest();
        this.completeCb = callBack;
        request.once(Laya.Event.COMPLETE, this, this.onRequestComplete, request.data);
        request.once(Laya.Event.ERROR, this, (e: any) => {
            console.error(e);
        });
        request.send(url, data, method, "text");
        console.log("send", url, data, method);
    }
    private static completeCb: Function = null;
    public static onRequestComplete(data: any) {
        if (this.completeCb) this.completeCb(JSON.parse(data));
    }
    public static getHttpUrl(type: number, data: any) {
        let timestamp = Date.now();
        let sign = window["md5"]("4949" + timestamp);
        if (type == 1) {
            return `https://mp.weixunyou.cn/app/index.php?i=49&t=flj001&v=2.0.0&from=wxapp&c=entry&a=wxapp&do=user_openid&m=flj_shop&sign=${sign}&timestamp=${timestamp}&wx_code=${data.code}`;
        } else if (type == 2) {
            let openid = UserDataManager.instance.wxopenid;
            let unionid = UserDataManager.instance.wxunionid ? UserDataManager.instance.wxunionid : "";
            let pid = "";
            return `https://mp.weixunyou.cn/app/index.php?i=49&t=flj001&v=2.0.0&from=wxapp&c=entry&a=wxapp&do=user_Login&m=flj_shop&sign=${sign}&timestamp=${timestamp}&wx_openid=${openid}&wx_unionid=${unionid}&nick_name=%E8%92%8B%E5%9B%BD%E8%BE%89%2017315353999&user_headimg=https%3A%2F%2Fthirdwx.qlogo.cn%2Fmmopen%2Fvi_32%2FDYAIOgq83eqlp8icrgWBCvwuLkcydJ44t7KLC19teFoS7nHXm71PoSJnm2lI91IWKGP6vdZQuN3OYx0XPLjdvLg%2F132&pid=${pid}`;
        }
    }
}