import { AllPlatformAdMgr } from "../../GameFrame/advert/AllPlatformAdMgr";
import GEvent from "../../GameFrame/event/GEvent";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import ResManager from "../../gframe-work/manager/ResManager";
import { ui } from "../../ui/layaMaxUI";
import { EventTypeEnum, SceneName } from "../GEnum";
import LotteryResultRuntime from "./LotteryResultRuntime";

export default class LotteryRuntime extends ui.lotteryUI {
    rotateAngle: number = 0;
    rotating: boolean = false;
    tweenTarget = { rotation: 0 };
    lotteryData = null;
    closeHandler: Laya.Handler = null;
    onAwake() {
        this.lotteryData = [
            { type: 'package', desc: '钻石卡包', value: [3], per: 0.01 },
            { type: 'gold', desc: '+1999', value: 1999, per: 0.01 },
            { type: 'gold', desc: '+188', value: 188, per: 0.2 },
            { type: 'gold', desc: '+288', value: 288, per: 0.08 },
            { type: 'card', desc: '卡牌 X3', value: 3, per: 0.05 },
            { type: 'gold', desc: '+588', value: 588, per: 0.05 },
            { type: 'gold', desc: '+88', value: 88, per: 0.3 },
            { type: 'card', desc: '卡牌 X1', value: 1, per: 0.3 }
        ];
        this.light.visible = false;
        if (DataManager.get<LocalStorageData>('freeLottery').value == 0) {
            this.freeGoBtn.visible = true;
            this.videoGoBtn.visible = false;
        } else {
            this.freeGoBtn.visible = false;
            this.videoGoBtn.visible = true;
        }
        this.freeGoBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['freeGo']);
        this.videoGoBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['videoGo']);
        this.backBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['back']);
    }

    onMouseEvent(name: string, e: Laya.Event) {
        if (this.rotating) return;
        ResManager.playSound('Click.mp3');
        if (name == 'freeGo') {
            this.freeGoBtn.visible = false;
            this.videoGoBtn.visible = true;
            DataManager.get<LocalStorageData>('freeLottery').value = 1;
            this.startLottery();
        } else if (name == 'videoGo') {
            AllPlatformAdMgr.Instance.showVideoAd(() => {
                this.startLottery();
            })
        } else if (name == 'back') {
            GEvent.event(EventTypeEnum.MainShowOpen, true);
            this.close();
            if (this.closeHandler) this.closeHandler.run();
        }
    }
    calculateResult() {
        let totalPer = 0;
        let random = Math.random();
        for (let i = 0; i < this.lotteryData.length; i++) {
            let data = this.lotteryData[i];
            totalPer += data.per;
            if (random < totalPer) {
                console.log(data);
                return i;
            }
        }
    }

    onDestroy() {
        Laya.timer.clearAll(this);
    }

    startLottery() {
        ResManager.playSound('roate.mp3');
        this.rotating = true;
        this.light.visible = false;
        this.tweenTarget.rotation = this.rewardPanel.rotation;
        let index = this.calculateResult();
        let angle = index * 45;
        let lastIndex = this.calculateIndex();
        Laya.Tween.to(this.tweenTarget, {
            rotation: this.rewardPanel.rotation + 360 * 3, update: Laya.Handler.create(this, () => {
                this.rewardPanel.rotation = this.tweenTarget.rotation;
                let nowIndex = this.calculateIndex();
                if (nowIndex != lastIndex && !this.pointer.isPlaying) {
                    this.pointer.play(0, false);
                    lastIndex = nowIndex;
                }
            }, null, false)
        }, 3000, Laya.Ease.sineIn, Laya.Handler.create(this, () => {
            this.tweenTarget.rotation = this.rewardPanel.rotation;
            let curAngle = this.rewardPanel.rotation % 360;
            let angleDiff = 0;
            if (curAngle > angle) {
                angleDiff = 360 + angle - curAngle;
            } else {
                angleDiff = angle - curAngle;
            }
            Laya.Tween.to(this.tweenTarget, {
                rotation: this.rewardPanel.rotation + 360 * 5 + angleDiff, update: Laya.Handler.create(this, () => {
                    this.rewardPanel.rotation = this.tweenTarget.rotation;
                    let nowIndex = this.calculateIndex();
                    if (nowIndex != lastIndex && !this.pointer.isPlaying) {
                        this.pointer.play(0, false);
                        lastIndex = nowIndex;
                    }
                }, null, false)
            }, 5000, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
                this.light.visible = true;
                this.rewardPanel.rotation = this.rewardPanel.rotation % 360;
                this.rotating = false;
                let data = this.lotteryData[index];
                Laya.Scene.load(SceneName.lotteryResult, Laya.Handler.create(this, (lotteryResult: LotteryResultRuntime) => {
                    lotteryResult.open(false);
                    lotteryResult.setData(data);
                }))
            }))
        }))
    }

    calculateIndex() {
        let angel = this.rewardPanel.rotation % 360;
        if (angel >= (22.5 + 45 * 0) && angel < (22.5 + 45 * 1)) {
            return 1
        } else if (angel >= (22.5 + 45 * 1) && angel < (22.5 + 45 * 2)) {
            return 2
        } else if (angel >= (22.5 + 45 * 2) && angel < (22.5 + 45 * 3)) {
            return 3
        } else if (angel >= (22.5 + 45 * 3) && angel < (22.5 + 45 * 4)) {
            return 4
        } else if (angel >= (22.5 + 45 * 4) && angel < (22.5 + 45 * 5)) {
            return 5
        } else if (angel >= (22.5 + 45 * 5) && angel < (22.5 + 45 * 6)) {
            return 6
        } else if (angel >= (22.5 + 45 * 6) && angel < (22.5 + 45 * 7)) {
            return 7
        } else {
            return 0;
        }
    }
}