import GEvent from "../../GameFrame/event/GEvent";

export default class BreakPointManager {
    private static _BreakPointManager: BreakPointManager;
    public static init() {
        if (!BreakPointManager._BreakPointManager) {
            // app onShow
            // if ('undefined' != typeof wx) {
            //     (<any>wx).aldLevel.onInitLevel({
            //         levelId: '1',  // 等级id 必传
            //         levelName: '第一级',  // 等级名称 必传
            //     });
            // }
            BreakPointManager._BreakPointManager = new BreakPointManager;
        }
    }
    sameTime: number = 0;
    lossLevel: number = 0;
    private constructor() {
        GEvent.on('BreakPoint', this, this.onBreakPoint);
    }
    onBreakPoint(name: string, value: any) {
        if (name == '关卡进度') {
            BreakPointManager.upload(value, `第${value}关`, name, 0, 0, name);
        } else if (name == '关卡失败') {
            BreakPointManager.upload(value, `第${value}关`, name, 0, 0, name);
            if (this.lossLevel == 0) {
                this.lossLevel = value;
                BreakPointManager.upload(value, `第${value}关`, '第一次失败', 0, 0, '第一次失败');
            } else {
                if (value == this.lossLevel) {
                    this.sameTime++;
                    if (this.sameTime >= 3) {
                        BreakPointManager.upload(value, `第${value}关`, '3次以上失败', 0, 0, '3次以上失败');
                    }
                } else {
                    this.sameTime = 0;
                }
                this.lossLevel = value;
            }
        } else if (name == '关卡开始') {
            //关卡开始
            if ('undefined' != typeof wx) {
                (<any>wx).aldStage.onStart({
                    stageId: value,     //关卡ID 该字段必传
                    stageName: `第${value}关`, //关卡名称  该字段必传
                })
            }
        } else if (name == '关卡结束') {
            //关卡结束
            if ('undefined' != typeof wx) {
                (<any>wx).aldStage.onEnd({
                    stageId: value,     //关卡ID 该字段必传
                    stageName: `第${value}关`, //关卡名称  该字段必传
                })
            }
        }
    }
    static upload(stageId: string, stageName: string, itemName: string, itemCount: number, itemMoney: number, desc: string) {
        if ('undefined' != typeof wx) {
            // 升级
            (<any>wx).aldLevel.onSetLevel({
                levelId: stageId,  // 等级id 必传
                levelName: stageName,  // 等级名称 必传
            });
            // 等级支付-支付成功
            (<any>wx).aldLevel.onPaySuccess({
                levelId: stageId,  // 等级id 必传
                levelName: stageName,  // 等级名称 必传
                params: {
                    amount: 100,  // paySuccess-支付金额 可选
                    desc: desc  // paySuccess-支付描述 可选
                }
            });
        }
    }
}