import GEvent from "../../GameFrame/event/GEvent";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import ResManager from "../../gframe-work/manager/ResManager";
import UserDataManager from "../../gframe-work/manager/UserDataManager";
import { ui } from "../../ui/layaMaxUI";
import GameDataManager from "../data/GameDataManager";
import { EventTypeEnum } from "../GEnum";
import GiftCellRuntime from "./GiftCellRuntime";

export default class GiftRuntime extends ui.giftUI {
    private _curShowGift: number = -1;
    isSelect: boolean = false;
    commonSelectId: number = 0;
    fightSelectId: number = 0;
    diamondLocal: LocalStorageData = null;
    moneyLocal: LocalStorageData = null;
    onAwake() {
        this.diamondLocal = DataManager.get<LocalStorageData>('diamond');
        this.moneyLocal = DataManager.get<LocalStorageData>('gold');
        this.commonGiftPanel.vScrollBarSkin = null;
        this.fightGiftPanel.vScrollBarSkin = null;
        GameDataManager.instance.initGiftData();
        this.showGift(0);
        this.fightGiftTitle.on(Laya.Event.CLICK, this, this.onClick);
        this.commonGiftTitle.on(Laya.Event.CLICK, this, this.onClick);
        this.upgradeBtn.on(Laya.Event.CLICK, this, this.onClick);
        GEvent.on(EventTypeEnum.GiftCellClick, this, this.onGiftCellClick);
    }
    onGiftCellClick(giftData: any) {
        if (this._curShowGift == 1) {
            this.fightSelectId = giftData.id - 18;
        } else {
            this.commonSelectId = giftData.id;
        }
        this.updateGiftUI(this._curShowGift);
        this.updateGiftProp(this._curShowGift);
    }
    onDestroy() {
        GEvent.off(EventTypeEnum.GiftCellClick, this, this.onGiftCellClick);
    }
    onClick(e: Laya.Event) {
        if (e.target == this.fightGiftTitle) {
            this.tweenTitleBg(1);
            this.showGift(1);
        } else if (e.target == this.commonGiftTitle) {
            this.tweenTitleBg(0);
            this.showGift(0);
        } else if (e.target == this.upgradeBtn) {
            let goldCost = Number(this.moneyCost.text);
            let diamondCost = Number(this.diamondCost.text);
            if (goldCost > this.moneyLocal.value) {
                GEvent.event(EventTypeEnum.SendTip, [['金币不足!']]);
                return;
            }
            if (diamondCost > this.diamondLocal.value) {
                GEvent.event(EventTypeEnum.SendTip, [['钻石不足!']]);
                return;
            }
            this.moneyLocal.value -= goldCost;
            this.diamondLocal.value -= diamondCost;
            GameDataManager.instance.upgradeGift(this._curShowGift, null);
            let userGiftData = UserDataManager.instance.commonGiftData;
            let userSelectId = this.commonSelectId;
            if (this._curShowGift == 1) {
                userGiftData = UserDataManager.instance.fightGiftData;
                userSelectId = this.fightSelectId;
            }
            if (userSelectId != userGiftData[0]) {
                if (this._curShowGift == 1) {
                    this.fightSelectId = userGiftData[0];
                } else {
                    this.commonSelectId = userGiftData[0];
                }
            }
            this.updateGiftUI(this._curShowGift);
            this.updateGiftProp(this._curShowGift);
        }
    }
    tweenTitleBg(dir: number) {
        Laya.MouseManager.enabled = false;
        let targetX = 59;
        if (dir == 1) targetX = 384;
        Laya.Tween.to(this.titleBg, { x: targetX }, 500, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
            Laya.MouseManager.enabled = true;
        }));
    }
    showGift(type: number) {
        if (type == this._curShowGift) return;
        this._curShowGift = type;
        if (type == 1) {
            this.fightGiftPanel.visible = true;
            this.commonGiftPanel.visible = false;
            this.upgradeRace.visible = true;
            this.fightSelectId = UserDataManager.instance.fightGiftData[0];
        } else {
            this.fightGiftPanel.visible = false;
            this.commonGiftPanel.visible = true;
            this.upgradeRace.visible = false;
            this.commonSelectId = UserDataManager.instance.commonGiftData[0];
        }
        this.updateGiftUI(this._curShowGift);
        this.updateGiftProp(this._curShowGift);
    }
    updateGiftUI(type: number) {
        let originalX = 360, originalY = 115;
        let cellX = 2, cellY = 1;
        let width = 180, height = 192;
        let maxGiftId = 18;
        let offset = 0;
        let cellBox = this.commonCellBox;
        let lineBox = this.commonLineBox;
        let userGiftData = UserDataManager.instance.commonGiftData;
        let userSelectId = this.commonSelectId;
        if (type == 1) {
            cellBox = this.fightCellBox;
            lineBox = this.fightLineBox;
            offset = 18;
            userGiftData = UserDataManager.instance.fightGiftData;
            userSelectId = this.fightSelectId;
        }
        let curStage = UserDataManager.instance.curStage;
        let curId = userGiftData[0];
        let curLevel = userGiftData[1];
        let giftConfig = ResManager.getRes<any>("talent.json");
        let unlock = true;
        for (let i = 0; i < maxGiftId; i++) {
            let giftData = giftConfig[i + offset];
            let cellPos: number[] = JSON.parse(giftData.pos);
            let cellName = `cell${cellPos[0]}_${cellPos[1]}`;
            let cell = cellBox.getChildByName(cellName) as GiftCellRuntime;
            if (!cell) {
                cell = new GiftCellRuntime();
                cell.dataSource = giftData;
                cell.name = cellName;
                cell.x = (cellPos[0] - cellX) * width + originalX;
                cell.y = (cellPos[1] - cellY) * height + originalY;
                cellBox.addChild(cell);
            }
            let smallLevel = 0;
            let bigLevel = 0;
            if (giftData.unlockLevel) {
                smallLevel = giftData.unlockLevel % 1000;
                bigLevel = Math.floor((giftData.unlockLevel - smallLevel) / 1000);
                let normalizeLevel1 = (bigLevel - 1) * 10 + smallLevel;
                let normalizeLevel2 = (curStage[0] - 1) * 10 + curStage[1];
                if (normalizeLevel2 < normalizeLevel1) {
                    unlock = false;
                }
            }
            if (giftData.id <= curId + offset) {
                if (giftData.id == curId + offset) {
                    cell.setState(unlock ? 1 : 0, curLevel, false, unlock, giftData.id == (userSelectId + offset));
                } else {
                    cell.setState(unlock ? 1 : 0, giftData.levels, false, unlock, giftData.id == (userSelectId + offset));
                }
            } else {
                let isNew = false;
                if (i + 1 == curId + 1) {
                    isNew = true;
                }
                if (giftData.unlockLevel == curId + 1) {
                    isNew = true;
                }
                cell.setState(0, 0, isNew, unlock, giftData.id == (userSelectId + offset));
            }
        }
        for (let i = 0; i < maxGiftId; i++) {
            let giftData = giftConfig[i + offset];
            let cellPos: number[] = JSON.parse(giftData.pos);
            if (giftData.ifNext) {
                let isSameType = this.isSameTypeNode(cellPos);
                let isCenterNode = cellPos[0] % 2 == 0;
                let lineName: string = null;
                if (isSameType) {
                    lineName = `line${cellPos[0]}_${cellPos[1]}`;
                } else {
                    lineName = `line${2}_${cellPos[1]}`;
                }
                let lineImage = lineBox.getChildByName(lineName) as Laya.Image;
                if (!lineImage) {
                    lineImage = new Laya.Image();
                    lineImage.name = lineName;
                    lineImage.anchorX = 0.5;
                    if (!isSameType && !isCenterNode) {
                        lineImage.anchorY = 1;
                        lineImage.scaleY = -1;
                    }
                    if (isSameType) {
                        lineImage.x = (cellPos[0] - cellX) * width + originalX;
                        lineImage.y = (cellPos[1] - cellY) * height + originalY;
                        lineImage.width = 6; lineImage.height = 192;
                    } else {
                        lineImage.width = 361; lineImage.height = 192;
                        lineImage.x = (2 - cellX) * width + originalX;
                        lineImage.y = (cellPos[1] - cellY) * height + originalY;
                    }
                    lineBox.addChild(lineImage);
                }
                if (!isSameType) {
                    if (isCenterNode) {
                        let cell = cellBox.getChildByName(`cell${cellPos[0]}_${cellPos[1]}`) as GiftCellRuntime;
                        if (cell.isFullLevel) {
                            lineImage.skin = "gift/Ui_Line3.png";
                        } else {
                            lineImage.skin = "gift/Ui_Line4.png";
                        }
                    } else {
                        let cell1 = cellBox.getChildByName(`cell${1}_${cellPos[1]}`) as GiftCellRuntime;
                        let cell2 = cellBox.getChildByName(`cell${3}_${cellPos[1]}`) as GiftCellRuntime;
                        if (cell1.isFullLevel && cell2.isFullLevel) {
                            lineImage.skin = "gift/Ui_Line3.png";
                        } else {
                            lineImage.skin = "gift/Ui_Line4.png";
                        }
                    }
                } else {
                    let cell = cellBox.getChildByName(`cell${cellPos[0]}_${cellPos[1]}`) as GiftCellRuntime;
                    if (cell.isFullLevel) {
                        lineImage.skin = "gift/Ui_Line1.png";
                    } else {
                        lineImage.skin = "gift/Ui_Line2.png";
                    }
                }
            }
        }
    }
    onSelect() {
        this.isSelect = true;
        GEvent.event(EventTypeEnum.NextChange, 'hide');
    }
    isSameTypeNode(pos: number[]) {
        let singleNodeY = [1, 4, 5, 8, 9, 12];
        let downY = pos[1] + 1;
        if ((pos[0] % 2 != 0 && singleNodeY.indexOf(downY) == -1) ||
            (pos[0] % 2 == 0 && singleNodeY.indexOf(downY) != -1)) {
            return true;
        } else {
            return false;
        }
    }
    updateGiftProp(type: number) {
        let offset = 0;
        let selectId = this.commonSelectId;
        let userGiftData = UserDataManager.instance.commonGiftData;
        if (type == 1) {
            offset = 18;
            selectId = this.fightSelectId;
            userGiftData = UserDataManager.instance.fightGiftData;
        }
        let giftConfig: any[] = ResManager.getRes<any>("talent.json");
        let giftId = userGiftData[0];
        let giftLevel = userGiftData[1];
        let selectData = giftConfig[selectId + offset - 1];
        let selectPropInfo = GameDataManager.instance.getPropInfo(selectData.attType);
        let descArr = ["能量获取", "建筑生命值", "建筑生命回复", "击杀额外能量", "游戏获得奖励"];
        if (selectPropInfo.propArr == GameDataManager.instance.fightGiftProp) {
            descArr = ["攻击力", "生命值", "移动速度", "攻击速度"];
        } else if (selectPropInfo.propArr == GameDataManager.instance.fightGiftTimeProp) {
            descArr = ["攻击力提升持续时间", "生命值提升持续时间", "移动速度提升持续时间", "攻击速度提升持续时间"];
        }
        let cost: number[] = JSON.parse(selectData.cost);
        this.moneyCost.text = cost[1] + "";
        this.diamondCost.text = cost[3] + "";
        this.giftName.text = selectData.name;
        let level = 0;
        if (selectId < giftId) {
            this.upgradeBtn.visible = false;
            level = selectData.levels;
        } else if (selectId == giftId) {
            this.upgradeBtn.visible = true;
            level = giftLevel;
            let smallLevel = 0;
            let bigLevel = 0;
            let curStage = UserDataManager.instance.curStage;
            if (selectData.unlockLevel) {
                smallLevel = selectData.unlockLevel % 1000;
                bigLevel = Math.floor((selectData.unlockLevel - smallLevel) / 1000);
                let normalizeLevel1 = (bigLevel - 1) * 10 + smallLevel;
                let normalizeLevel2 = (curStage[0] - 1) * 10 + curStage[1];
                if (normalizeLevel2 < normalizeLevel1) {
                    this.upgradeBtn.visible = false;
                }
            }
        } else {
            this.upgradeBtn.visible = false;
            level = 0;
        }
        this.giftLevel.text = `LV.${level}`;
        let propList = selectPropInfo.propArr[selectPropInfo.y];
        this.propAddList.bottom = 0;
        this.propNameList.bottom = 0;
        this.propValueList.bottom = 0;
        if (selectPropInfo.propArr == GameDataManager.instance.commonGiftProp) {
            this.upgradeRace.visible = false;
            this.propAddList.repeatY = 5;
            this.propNameList.repeatY = 5;
            this.propValueList.repeatY = 5;
        } else {
            this.propAddList.repeatY = 4;
            this.propNameList.repeatY = 4;
            this.propValueList.repeatY = 4;
            this.upgradeRace.visible = true;
            if (selectPropInfo.y == 0) {
                this.upgradeRace.skin = "gift/Ui_TianFuBattleWord6.png";
            } else if (selectPropInfo.y == 1) {
                this.upgradeRace.skin = "gift/Ui_TianFuBattleWord3.png";
            } else if (selectPropInfo.y == 2) {
                this.upgradeRace.skin = "gift/Ui_TianFuBattleWord4.png";
            } else if (selectPropInfo.y == 3) {
                this.upgradeRace.skin = "gift/Ui_TianFuBattleWord5.png";
            }
        }
        let nameListData = [];
        for (let i = 0; i < descArr.length; i++) {
            nameListData.push({ "propName": { text: descArr[i] + ":" } });
        }
        this.propNameList.array = nameListData;
        let valueListData = [];
        let addListData = [];
        for (let i = 0; i < propList.length; i++) {
            let textContent = "";
            let contentAdd = "";
            if ((type == 0 && i == 4) || (type == 1 && i == 3)) {
                textContent = Math.floor(propList[i] * 100) + "%";
                contentAdd = "+" + Math.floor(selectData.param * 100) + "%";
            } else if (type == 0 && i == 2) {
                textContent = propList[i] + "/s";
                contentAdd = "+" + selectData.param + "/s";
            } else if (type == 0 && i == 0) {
                textContent = propList[i] + "/m";
                contentAdd = "+" + selectData.param + "/m";
            } else {
                textContent = propList[i] + "";
                contentAdd = "+" + selectData.param + "";
            }
            valueListData.push({ "propValue": { text: textContent } })
            if (i == selectPropInfo.x) {
                addListData.push({ "propAdd": { text: contentAdd } })
                this.giftDesc.text = `${descArr[selectPropInfo.x]}${contentAdd}`;
            } else {
                addListData.push({ "propAdd": { text: "" } })
            }
        }
        this.propValueList.array = valueListData;
        this.propAddList.array = addListData;
        this.propAddList.bottom = 25;
        this.propNameList.bottom = 25;
        this.propValueList.bottom = 25;
    }
}