import { AllPlatformAdMgr } from "../../GameFrame/advert/AllPlatformAdMgr";
import GEvent from "../../GameFrame/event/GEvent";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import ResManager from "../../gframe-work/manager/ResManager";
import UserDataManager from "../../gframe-work/manager/UserDataManager";
import MathTool from "../../gframe-work/tool/MathTool";
import { ui } from "../../ui/layaMaxUI";
import { CardData } from "../data/CardData";
import { EventTypeEnum, SceneName } from "../GEnum";
import CardManager from "./CardManager";
import PackageRuntime from "./PackageRuntime";

export default class LotteryResultRuntime extends ui.lotteryResultUI {
    enemyPoolLocal: LocalStorageData = null;
    curLevelLocal: LocalStorageData = null;
    shopIdLocal: LocalStorageData = null;
    data: any = null;
    cardData: any[] = null;
    onAwake() {
        this.curLevelLocal = DataManager.get<LocalStorageData>('curLevel');
        this.enemyPoolLocal = DataManager.get<LocalStorageData>('enemyPool');
        this.shopIdLocal = DataManager.get<LocalStorageData>('shopId');
        ResManager.playSound('GetItem.mp3');
        AllPlatformAdMgr.Instance.showBannerAd();
    }
    onClosed() {
        AllPlatformAdMgr.Instance.hideBannerAd();
        AllPlatformAdMgr.Instance.initBannerAd();
    }
    setData(data: any) {
        this.data = data;
        this.rewardName.text = data.desc;
        if (data.type == 'gold' || data.type == 'diamond' || data.type == 'power') {
            if (data.type == 'gold') {
                this.moneyImage.skin = 'sign/Ui_money.png';
            } else if (data.type == 'diamond') {
                this.moneyImage.skin = 'sign/Ui_diamond.png';
            } else if (data.type == 'power') {
                this.moneyImage.skin = 'main/Ui_power.png';
            }
            this.moneyImage.visible = true;
            this.secretImage.visible = false;
            this.cardCell1.visible = false;
            this.cardCell2.visible = false;
            this.cardCell3.visible = false;
            this.receiveBtn.visible = false;
            if (data.isDirect) {
                this.receiveVideoBtn.visible = false;
                this.receiveDirectBtn.visible = true;
            } else {
                this.receiveVideoBtn.visible = true;
                this.receiveDirectBtn.visible = false;
                Laya.timer.once(3000, this, () => {
                    this.receiveDirectBtn.visible = true;
                })
            }
        } else if (data.type == 'card') {
            this.receiveBtn.visible = true;
            this.receiveVideoBtn.visible = false;
            this.receiveDirectBtn.visible = false;
            if (this.data.value instanceof Number) {
                this.cardData = [];
                for (let i = 0; i < this.data.value; i++) {
                    this.cardData.push(this.getPackageCard())
                }
            } else {
                this.cardData = this.data.value;
            }
            if (data.value == 1) {
                this.cardCell1.visible = false;
                this.cardCell2.visible = true;
                this.cell2.rotation = 19;
                this.cardCell3.visible = false;
                this.cardCell2.dataSource = this.cardData[0];
            } else {
                this.cardCell1.visible = true;
                this.cardCell2.visible = true;
                this.cell2.rotation = -15;
                this.cardCell3.visible = true;
                this.cardCell1.dataSource = this.cardData[0];
                this.cardCell2.dataSource = this.cardData[1];
                this.cardCell3.dataSource = this.cardData[2];
            }
            this.moneyImage.visible = false;
            this.secretImage.visible = false;
        } else if (data.type == 'package') {
            this.receiveBtn.visible = true;
            this.receiveVideoBtn.visible = false;
            this.receiveDirectBtn.visible = false;
            this.moneyImage.visible = false;
            this.secretImage.visible = true;
            this.cardCell1.visible = false;
            this.cardCell2.visible = false;
            this.cardCell3.visible = false;
            if (data.value.indexOf(1) != -1) {
                this.secretImage.skin = 'shop/Ui_bao1.png';
            } else if (data.value.indexOf(2) != -1) {
                this.secretImage.skin = 'shop/Ui_bao2.png';
            } else if (data.value.indexOf(3) != -1) {
                if (data.value.length == 3) {
                    this.secretImage.skin = 'shop/Ui_kabaothree.png';
                } else {
                    this.secretImage.skin = 'shop/Ui_bao3.png';
                }
            }
        }
    }
    onOpened() {
        this.receiveBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['receive']);
        this.receiveDirectBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['receiveDirect']);
        this.receiveVideoBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['receiveVideo']);
    }
    onMouseEvent(name: string, e: Laya.Event) {
        ResManager.playSound('Click.mp3');
        if (name == 'receive') {
            if (this.data.type == 'card') {
                CardManager.instance.addNewCard(this.cardData);
            } else if (this.data.type == 'package') {
                this.openPackage(this.data.value);
            }
            this.close();
        } else if (name == 'receiveDirect') {
            if (this.data.type == 'gold') {
                let goldLocal = DataManager.get<LocalStorageData>('gold');
                goldLocal.value += this.data.value;
            } else if (this.data.type == 'diamond') {
                let diamondLocal = DataManager.get<LocalStorageData>('diamond');
                diamondLocal.value += this.data.value;
            } else if (this.data.type == 'power') {
                UserDataManager.instance.power = 20;
            }
            if (this.data.from && this.data.from == "task") {
                GEvent.event(EventTypeEnum.ReceiveTaskReward);
            }
            this.close();
        } else if (name == 'receiveVideo') {
            if (this.data.type == 'gold') {
                AllPlatformAdMgr.Instance.showVideoAd(() => {
                    let goldLocal = DataManager.get<LocalStorageData>('gold');
                    goldLocal.value += this.data.value * 3;
                    this.close();
                })

            } else if (this.data.type == 'diamond') {
                AllPlatformAdMgr.Instance.showVideoAd(() => {
                    let diamondLocal = DataManager.get<LocalStorageData>('diamond');
                    diamondLocal.value += this.data.value * 3;
                    this.close();
                })
            } else if (this.data.type == 'power') {
                AllPlatformAdMgr.Instance.showVideoAd(() => {
                    UserDataManager.instance.power = 20;
                    this.close();
                })
            }
        }
    }
    getPackageCard() {
        let card = CardManager.instance.getCardData(MathTool.getRandom(0, 15));
        card.id = 1;
        card.exp = 6;
        return card;
    }

    openPackage(packageTypeArr: number[]) {
        let packageDataArr = [];
        let cardPackConfig = ResManager.getRes<any>("gacha.json");

        for (let i = 0; i < packageTypeArr.length; i++) {
            let cardPackData = cardPackConfig[packageTypeArr[i] - 1];
            let dataArr = this.getCardPackData(cardPackData);
            packageDataArr.push(dataArr);
            CardManager.instance.addNewCard(dataArr);
        }
        Laya.Scene.load(SceneName.package, Laya.Handler.create(this, (packageRuntime: PackageRuntime) => {
            packageRuntime.isModal = true;
            packageRuntime.isShowEffect = false;
            packageRuntime.width = Laya.stage.width;
            packageRuntime.height = Laya.stage.height;
            packageRuntime.open(false);
            packageRuntime.showBook(packageTypeArr, packageDataArr);
        }))
    }
    getCardPackData(packData: any) {
        let cardNum = packData.totalCard;
        let pool1 = packData.pool1;
        let amount1: number[] = JSON.parse(packData.amount1);
        let pool2 = packData.pool2;
        let amount2: number[] = JSON.parse(packData.amount2);
        let pool3 = packData.pool3;
        let dataArr = [];
        for (let i = 0; i < 3; i++) {
            let poolId = 0;
            let poolCardNum = 0;
            if (i == 0) {
                poolId = pool1;
                poolCardNum = amount1[MathTool.getRandom(0, amount1.length - 1)];
            } else if (i == 1) {
                poolId = pool2;
                poolCardNum = amount2[MathTool.getRandom(0, amount2.length - 1)];
            } else if (i == 2) {
                poolId = pool3;
                poolCardNum = cardNum;
            }
            cardNum -= poolCardNum;
            dataArr = dataArr.concat(this.getCardPoolData(poolCardNum, poolId));
        }
        return dataArr;
    }
    getCardPoolData(cardNum: number, poolId: number) {
        let cardPoolConfig = ResManager.getRes<any>("gachapool.json");
        let cardPoolData = cardPoolConfig[this.getCardPoolIndex(poolId)];
        let poolContent: number[] = [...JSON.parse(cardPoolData.content)];
        let unitLevel = cardPoolData.unitLevel;
        let unitIdArr = [];
        let fightLevelArr = [];
        let ifRepetitive = cardPoolData.ifRepetitive;
        for (let i = 0; i < cardNum; i++) {
            let randomIndex = MathTool.getRandom(0, poolContent.length - 1);
            let unitId = poolContent[randomIndex];
            unitIdArr.push(unitId);
            fightLevelArr.push(unitLevel);
            if (!ifRepetitive) {
                poolContent.splice(randomIndex, 1);
            }
        }
        return CardManager.instance.getCardDataArr(unitIdArr, fightLevelArr);
    }
    getCardPoolIndex(poolId: number) {
        let offset = 0, base = 1000;
        let arr = [4, 4, 4];
        for (let i = 0; i < 3; i++) {
            let newbase = (i + 2) * 1000;
            if (poolId > newbase) {
                base = newbase;
                offset += arr[i];
            }
        }
        return poolId % base + offset - 1;
    }
}