import GEvent from "../../GameFrame/event/GEvent";
import ResManager from "../../gframe-work/manager/ResManager";
import UserDataManager from "../../gframe-work/manager/UserDataManager";
import { ui } from "../../ui/layaMaxUI";
import GameDataManager from "../data/GameDataManager";
import { EventTypeEnum, SceneName } from "../GEnum";
import MapCellRuntime from "./MapCellRuntime";
import MapPageRuntime from "./MapPageRuntime";

export default class BigMapRuntime extends ui.bigMapUI {
    private maxChapter: number = 3;
    private showChapter: number;
    private selectLevel: number = 0;
    private pageArr: MapPageRuntime[] = [];
    onAwake() {
        let curStage = UserDataManager.instance.curStage;
        this.showChapter = curStage[0];
        for (let i = 0; i < this.maxChapter; i++) {
            let page = new MapPageRuntime();
            page.x = 360, page.y = 715, page.rotation = -2.5;
            page.scaleX = page.scaleY = 1.1;
            page.zOrder = this.maxChapter - 1 - i;
            page.dataSource = i + 1;
            if (i + 1 >= this.showChapter) {
                page.x = 360;
            } else {
                page.x = -720;
            }
            this.pageBox.addChild(page);
            this.pageArr.push(page);
        }
        this.updateSlideBtn();
    }
    onOpened() {
        let curStage = UserDataManager.instance.curStage;
        this.setSelectLevel(curStage[1]);
        this.backBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.startBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.leftBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.rightBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        GEvent.on(EventTypeEnum.MapCellSelect, this, this.onMapCellSelect);
    }
    onDestroy() {
        GEvent.off(EventTypeEnum.MapCellSelect, this, this.onMapCellSelect);
    }
    onBtnClick(e: Laya.Event) {
        if (this.backBtn == e.target) {
            this.close();
        } else if (this.startBtn == e.target) {
            let powerCost = 5;
            let userPower = UserDataManager.instance.power;
            if (userPower < powerCost) {
                GEvent.event(EventTypeEnum.SendTip, [['体力不足']]);
                return;
            }
            UserDataManager.instance.usePowerTs = Date.now();
            userPower -= powerCost;
            UserDataManager.instance.power = userPower;
            GameDataManager.instance.isPVP = false;
            this.close();
            Laya.Scene.open(SceneName.gameLoad);
        } else if (this.leftBtn == e.target) {
            let lastPage = this.pageArr[this.showChapter - 2];
            let showPage = this.pageArr[this.showChapter - 1];
            this.showChapter--;
            this.updateSlideBtn();
            Laya.MouseManager.enabled = false;
            this.rewardPop.visible = false;
            Laya.Tween.to(lastPage, { x: 360 }, 500, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
                Laya.MouseManager.enabled = true;
                if (this.selectLevel > 0) {
                    let lastCell = showPage.baseBox.getChildByName(`base${this.selectLevel}`) as MapCellRuntime;
                    lastCell.setSelect(false);
                    this.selectLevel = 0;
                }
                if (this.showChapter == UserDataManager.instance.curStage[0]) {
                    this.setSelectLevel(UserDataManager.instance.curStage[1]);
                }
            }));
        } else if (this.rightBtn == e.target) {
            let showPage = this.pageArr[this.showChapter - 1];
            this.showChapter++;
            this.updateSlideBtn();
            Laya.MouseManager.enabled = false;
            this.rewardPop.visible = false;
            Laya.Tween.to(showPage, { x: -720 }, 500, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
                Laya.MouseManager.enabled = true;
                if (this.selectLevel > 0) {
                    let lastCell = showPage.baseBox.getChildByName(`base${this.selectLevel}`) as MapCellRuntime;
                    lastCell.setSelect(false);
                    this.selectLevel = 0;
                }
                if (this.showChapter == UserDataManager.instance.curStage[0]) {
                    this.setSelectLevel(UserDataManager.instance.curStage[1]);
                }
            }));
        }
    }
    setSelectLevel(level: number) {
        if (this.selectLevel == level) return;
        let showPage = this.pageArr[this.showChapter - 1];
        if (this.selectLevel > 0) {
            let lastCell = showPage.baseBox.getChildByName(`base${this.selectLevel}`) as MapCellRuntime;
            lastCell.setSelect(false);
            this.selectLevel = 0;
        }
        this.selectLevel = level;
        let mapCell = showPage.baseBox.getChildByName(`base${level}`) as MapCellRuntime;
        let globalPoint = mapCell.localToGlobal(new Laya.Point(54, 36), false, this);
        let mapConfig: any[] = ResManager.getRes("map.json");
        let normalizedLevel = 10 * (this.showChapter - 1) + level;
        let levelData = mapConfig[normalizedLevel - 1];
        if (levelData.rewardShow !== 0) {
            this.rewardPop.visible = true;
            this.setRewardShowData(JSON.parse(levelData.rewardShow));
            Laya.Tween.to(this.rewardPop, { x: globalPoint.x, y: globalPoint.y }, 300, Laya.Ease.sineOut);
        } else {
            this.rewardPop.visible = false;
        }
        let bottomRec = this.buffFrame.bottom;
        Laya.MouseManager.enabled = false;
        Laya.Tween.to(this.buffFrame, { bottom: -320 }, 200, Laya.Ease.sineIn, Laya.Handler.create(this, () => {
            this.setBuffFrameData(levelData);
            this.setRewardFirstData(JSON.parse(levelData.firstReward));
            this.setRewardNormalData(JSON.parse(levelData.reward));
            Laya.timer.once(200, this, () => {
                Laya.Tween.to(this.buffFrame, { bottom: bottomRec }, 200, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
                    Laya.MouseManager.enabled = true;
                }));
            })
        }));
        mapCell.setSelect(true);
        GameDataManager.instance.playStage = [this.showChapter, this.selectLevel];
    }
    onMapCellSelect(id: number) {
        this.setSelectLevel(id);
    }
    getRewardDataArr(reward: number[], received: boolean = false) {
        let dataArr = [];
        for (let i = 0; i < reward.length / 2; i++) {
            let rewardId = reward[i * 2];
            let rewardNum = reward[i * 2 + 1];
            let imageSkin = "";
            if (rewardId == 1) {
                imageSkin = "main/Ui_Gold.png";
            } else if (rewardId == 2) {
                imageSkin = "main/Ui_Diamonds.png";
            } else if (rewardId == 3) {
                imageSkin = "main/Ui_power.png";
            } else if (rewardId >= 50000 && rewardId <= 50019) {
                imageSkin = "map/Ui_WenHaoCard.png";
                rewardNum = 1;
            } else if (rewardId >= 1101 && rewardId <= 1706) {
                imageSkin = "map/Ui_WenHaoCard.png";
            }
            dataArr.push({ "rewardImage": { skin: imageSkin }, "rewardNum": { text: "X" + rewardNum }, "receivedCover": { visible: received } });
        }
        return dataArr;
    }
    setRewardShowData(reward: number[]) {
        let normalizedLevel = 10 * (this.showChapter - 1) + this.selectLevel;
        let received = false;
        if (UserDataManager.instance.starRecord[normalizedLevel]) {
            received = true;
        }
        let dataArr = this.getRewardDataArr(reward, received);
        this.rewardPopList.repeatX = dataArr.length;
        this.rewardPopList.centerX = -1;
        this.rewardPopList.array = dataArr;
        this.rewardPopList.centerX = 0;
    }
    setRewardFirstData(reward: number[]) {
        let dataArr = this.getRewardDataArr(reward);
        let starRecord = UserDataManager.instance.starRecord;
        let playNormalizeLevel = (this.showChapter - 1) * 10 + this.selectLevel;
        let starRec = starRecord[playNormalizeLevel - 1];
        let received = true;
        if (starRec == undefined) {
            received = false;
        }
        for (let i = 0; i < dataArr.length; i++) {
            dataArr[i]["rewardBg"] = { skin: `map/Ui_FrameGift${i + 1}.png` };
            dataArr[i]["receivedCover"] = { visible: received };
        }
        this.firstRewardList.repeatX = dataArr.length;
        this.firstRewardList.centerX = -1;
        this.firstRewardList.array = dataArr;
        this.firstRewardList.centerX = 0;
    }
    setRewardNormalData(reward: number[]) {
        let dataArr = this.getRewardDataArr(reward);
        for (let i = 0; i < dataArr.length; i++) {
            dataArr[i]["rewardBg"] = { skin: `map/Ui_FrameGift${i + 1}.png` };
        }
        this.normalRewardList.repeatX = dataArr.length;
        this.normalRewardList.centerX = -1;
        this.normalRewardList.array = dataArr;
        this.normalRewardList.centerX = 0;
    }
    setBuffFrameData(data: any) {
        let buffConfig = ResManager.getRes("buff.json");
        let buffDataArr: any[] = [];
        let buffIdArr = [data.buff1, data.buff2];
        for (let i = 0; i < buffIdArr.length; i++) {
            let buffId = buffIdArr[i];
            if (buffId > 0) {
                let buffData = buffConfig[buffId - 1];
                let desc = buffData.description;
                if (buffData.description.indexOf("*") != -1) {
                    for (let i = 0; i < 2; i++) {
                        let param = buffData["param" + (i + 1)];
                        if ((buffData.type == 2 && i == 1) ||
                            (buffData.type == 5 || buffData.type == 6 || buffData.type == 7) ||
                            (buffData.type == 8 && i == 0)
                        ) {
                            param = Math.floor(param * 100) + "%";
                        }
                        desc = desc.replace("*", param);
                    }
                }
                buffDataArr.push({
                    "buffImage": { skin: `map/Ui_Buff${buffData.type}.png` },
                    "buffName": { skin: `map/Ui_BuffName${buffData.type}.png` },
                    "buffDesc": { text: desc },
                });
            }
        }
        this.buffList.array = buffDataArr;
    }
    updateSlideBtn() {
        if (this.showChapter == 1) {
            this.leftBtn.visible = false;
            this.rightBtn.visible = true;
        } else if (this.showChapter == this.maxChapter) {
            this.leftBtn.visible = true;
            this.rightBtn.visible = false;
        } else {
            this.leftBtn.visible = true;
            this.rightBtn.visible = true;
        }
    }
}