import { ActorBase } from "../../GameFrame/actor/ActorBase";
import GEvent from "../../GameFrame/event/GEvent";
import AnimatorStateScript from "../../gframe-work/actor/AnimatorStateScript";
import ResManager from "../../gframe-work/manager/ResManager";
import LayaTool from "../../gframe-work/tool/LayaTool";
import { RoleData } from "../data/GameData";
import GameDataManager from "../data/GameDataManager";
import { EventTypeEnum } from "../GEnum";
import ProgressRuntime from "./ProgressRuntime";
import RoleBase from "./RoleBase";
import WeaponBase from "./WeaponBase";

class TowerAnimationState {
    public static bron: string = "TowerBron";
    public static stand: string = "TowerStand";
}
export default class CannonBase extends ActorBase {
    type: number = 0;
    protected targetEnemyArray: RoleBase[] = null;
    protected targetTowerArray: CannonBase[] = null;
    protected roadArray: CannonBase[] = null;
    protected roadIndex: number = 0;
    dataClone: RoleData = null;
    attackTarget: RoleBase | CannonBase = null;
    public camera: Laya.Camera = null;
    private cameraOut: Laya.Vector4 = new Laya.Vector4;
    public bloodProgress: ProgressRuntime = null;
    public isTransparent: boolean = false;
    public isPlaced: boolean = false;
    attackDelayRecord: number = 0;
    attackDistanceScale: number = 0.7;
    public launchPoint: Laya.Sprite3D = null;
    //0tand,1attack
    towerState: number = 0;
    public weaponSign: string = '123';
    onAwake() {
        super.onAwake();
        this.launchPoint = this.gameObject.getChildByName("LaunchPoint1") as Laya.Sprite3D;
    }
    init(cannonData: RoleData) {
        super.init(cannonData);
        this.isPlaced = false;
        this.dataClone = cannonData.clone();
        this.changeActorState(TowerAnimationState.stand, "");
        if (this.bloodProgress) {
            if (this.type == 0) {
                this.bloodProgress.white.skin = 'game/Ui_White.png';
            } else if (this.type == 1) {
                this.bloodProgress.white.skin = 'game/Ui_Red.png';
            }
            this.bloodProgress.fullValue = cannonData.hp;
            this.bloodProgress.progress = 1;
            this.bloodProgress.visible = false;
            this.bloodProgress.scaleX = this.bloodProgress.scaleY = 0.3;
        }
        this.weaponSign = "MissileTower1";
        if (this.type == 1) {
            this.weaponSign = "MissileTower2";
        }
        this.attackDelayRecord = 0;
    }
    onUpdate() {
        if (!this.isPlaced || this.isDeath || GameDataManager.instance.isGameOver || GameDataManager.instance.isGamePause) return;
        this.updateTowerState();
        this.runAttackDelay();
        this.findTarget();
        this.attackSelectTarget();
        super.onUpdate();
    }
    runAttackDelay() {
        if (this.attackDelayRecord > 0) {
            this.attackDelayRecord -= Laya.timer.delta;
            if (this.attackDelayRecord < 0) {
                this.attackDelayRecord = 0;
            }
        }
    }
    findTarget() {
        if (!this.targetEnemyArray) return;
        if (!this.attackTarget || this.attackTarget.isDeath) {
            let distance = 1000000;
            let targetArr = [this.targetEnemyArray, this.targetTowerArray];
            for (let i = 0; i < targetArr.length; i++) {
                const targetRoad = targetArr[i];
                let attackTarget: RoleBase | CannonBase = null;
                for (let j = 0; j < targetRoad.length; j++) {
                    let target = targetRoad[j];
                    let dis = Math.abs(this.gameObject.transform.position.z - target.gameObject.transform.position.z);
                    if (dis < distance) {
                        distance = dis;
                        attackTarget = target;
                    }
                }
                if (distance <= (<RoleData>this.actorData).attackDistance * this.attackDistanceScale) {
                    this.attackTarget = attackTarget;
                    this.towerState = 1;
                    return;
                }
            }
        }
    }
    attackSelectTarget() {
        if (this.towerState == 1) {
            if (this.attackTarget && this.attackTarget.destroyed == false && this.attackDelayRecord == 0) {
                this.createWeapon();
                this.attackDelayRecord = (<RoleData>this.actorData).attackDelay;
            }
        }
    }
    createWeapon() {
        if (GameDataManager.instance.isGameOver || GameDataManager.instance.isGamePause) return;
        let weapon: WeaponBase = Laya.Pool.getItem(this.weaponSign);
        if (!weapon) {
            let weaponModel: Laya.Sprite3D = ResManager.getRes<any>(`${this.weaponSign}.lh`).clone();
            weapon = weaponModel.addComponent(WeaponBase);
            this.gameObject.parent.addChild(weaponModel);
        } else {
            this.gameObject.parent.addChild(weapon.gameObject);
        }
        weapon.gameObject.transform.position = this.launchPoint.transform.position.clone();
        if (this.attackTarget.gameObject.transform.position.z <= this.gameObject.transform.position.z) {
            weapon.gameObject.transform.localRotationEuler = new Laya.Vector3(0, 180, 0);
        } else {
            weapon.gameObject.transform.localRotationEuler = new Laya.Vector3(0, 0, 0);
        }
        let attackValue = (<RoleData>this.actorData).attack;
        weapon.init(false, this.type, attackValue, this.weaponSign, this.roadIndex, (<RoleData>this.actorData).attackDistance, this.targetEnemyArray, this.targetTowerArray, null, true, this.attackTarget);
    }
    updateBloodPositon() {
        if (this.bloodProgress && this.camera) {
            let rolePos = this.gameObject.transform.position.clone();
            rolePos.y += 3;
            //转换坐标
            this.cameraOut = LayaTool.projectPos(this.camera, rolePos);
            //赋值给2D
            this.bloodProgress.pos((this.cameraOut.x - this.bloodProgress.width * this.bloodProgress.scaleX / 2) / Laya.stage.clientScaleX, (this.cameraOut.y - this.bloodProgress.height * this.bloodProgress.scaleY / 2) / Laya.stage.clientScaleY);
        }
    }
    setPlaced(roadIndex: number, roadArray: CannonBase[], targetEnemyArray: RoleBase[], targetCannonArr: CannonBase[]) {
        this.roadIndex = roadIndex;
        this.roadArray = roadArray;
        this.targetEnemyArray = targetEnemyArray;
        this.targetTowerArray = targetCannonArr;
        this.gameObject.active = true;
        this.changeActorState(TowerAnimationState.bron, '');
        this.updateBloodPositon();
        let bronState = this.animator.getControllerLayer(0).getAnimatorState(TowerAnimationState.bron);
        let stateScript = bronState.addScript(AnimatorStateScript) as AnimatorStateScript;
        stateScript.stateExitHandler = Laya.Handler.create(this, () => {
            this.isPlaced = true;
            roadArray.push(this);
            this.changeActorState(TowerAnimationState.stand, '');
        });
    }
    updateTowerState() {
        this.towerState = 0;
        if (this.attackTarget && !this.attackTarget.destroyed && !this.attackTarget.isDeath) {
            let distance = Math.abs(this.gameObject.transform.position.z - this.attackTarget.gameObject.transform.position.z);
            if (distance <= (<RoleData>this.actorData).attackDistance * this.attackDistanceScale) {
                this.towerState = 1;
            }
        }
    }
    onHurt(value: number) {
        if (this.isDeath || GameDataManager.instance.isGameOver || (GameDataManager.instance.isGamePause)) return;
        if (this.bloodProgress) this.bloodProgress.visible = true;
        this.actorData.hp -= value;
        if (this.actorData.hp <= 0) {
            this.actorData.hp == 0;
            this.isDeath = true;
            this.bloodProgress.visible = false;
            this.onDeath();
        }
        this.bloodProgress.progress = this.actorData.hp / this.dataClone.hp;
    }
    onDeath() {
        let deathBodyName = "TowerDeath1";
        if (this.type == 1) {
            deathBodyName = "TowerDeath2";
        }
        this.gameObject.active = false;
        let original = ResManager.getRes<Laya.Sprite3D>(`${deathBodyName}.lh`);
        let deathBody = Laya.Sprite3D.instantiate(original, this.gameObject.parent, false, this.gameObject.transform.position.clone());
        let index = this.roadArray.indexOf(this);
        if (index >= 0) this.roadArray.splice(index, 1);
        Laya.timer.once(2000, this, () => {
            let tweenTarget = { y: deathBody.transform.position.y };
            Laya.Tween.to(tweenTarget, {
                y: -2, update: Laya.Handler.create(this, () => {
                    if (deathBody) {
                        let newPos: Laya.Vector3 = deathBody.transform.position.clone();
                        newPos.y = tweenTarget.y;
                        deathBody.transform.position = newPos;
                    }
                }, null, false)
            }, 1000, Laya.Ease.linearNone, Laya.Handler.create(this, () => {
                Laya.timer.once(500, this, () => {
                    this.gameObject.destroy();
                    deathBody.destroy();
                })
            }));
        })
    }
    addExplode(explodeName: string) {
        let explode = ResManager.getRes<any>(explodeName).clone();
        let clearNode = Laya.stage.getChildByName('effect').getChildByName('clearNode');
        clearNode.addChild(explode);
        let explodePos = this.gameObject.transform.position.clone();
        if (this.gameObject.name == 'Player3') {
            explodePos.y = 4.2
        } else if (this.gameObject.name == 'Player2') {
            explodePos.y = 1
        } else if (this.gameObject.name == 'Player1') {
            explodePos.y = 1
        } else {
            explodePos.y = 1
        }
        explode.transform.position = explodePos;
        Laya.timer.once(3000, this, () => {
            explode.destroy();
        })
    }
    onDestroy() {
        super.onDestroy();
        Laya.timer.clearAll(this);
        if (this.bloodProgress) this.bloodProgress.destroy();
    }
}