export default class CameraRuntime extends Laya.Script3D {
    animator: Laya.Animator = null;
    curState: string = 'None';
    onAwake() {
        this.animator = this.owner.getComponent(Laya.Animator);
        this.changeState('CameraStand');
    }

    changeState(state: string) {
        if (this.curState == state) return
        if (state == 'CameraStand') {

        } else if (state == 'CameraShock') {

        }
        this.animator.play(state, 0, 0);
        this.curState = state;
    }

    Ani_End() {
        this.changeState('CameraStand');
    }
}