import UserDataManager from "../../gframe-work/manager/UserDataManager";
import { ui } from "../../ui/layaMaxUI";

export default class MagicCardRuntime extends ui.magicCardUI {
    public set dataSource(data: any) {
        this._dataSource = data;
        if (!data) return;
        this.magicImage.skin = `card/Ui_Magic${data.magicType}.png`;
        this.selectLight.visible = data.isSelect;
        let haveNum = UserDataManager.instance.magicCardNum[data.magicType - 1];
        let unlock = UserDataManager.instance.magicCardUnlock[data.magicType - 1];
        if (unlock) {
            this.cardNum.visible = true;
            this.lockImage.visible = false;
            this.cardNum.text = "X" + haveNum;
        } else {
            this.cardNum.visible = false;
            this.lockImage.visible = true;
        }
    }

    public get dataSource() {
        return this._dataSource;
    }
}