import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import ResManager from "../../gframe-work/manager/ResManager";
import UserDataManager from "../../gframe-work/manager/UserDataManager";
import { ui } from "../../ui/layaMaxUI";
import GameDataManager from "../data/GameDataManager";
import { SceneName } from "../GEnum";
import CardManager from "./CardManager";
import GameRuntime from "./GameRuntime";

export default class GameLoadRuntime extends ui.gameLoadUI {
    private moveSpeed: number = 1;
    onOpened() {
        let curStage = UserDataManager.instance.curStage;
        if (curStage[0] == 1 && curStage[1] == 2) {
            UserDataManager.instance.useMagicType = [1];
        }
        GameDataManager.instance.updateMagicData();
        Laya.timer.loop(60, this, this.onFrameLoop);
        let enemyValue = [];
        let groupValue = DataManager.get<LocalStorageData>('group1').value;
        let enemyPool: number[] = DataManager.get<LocalStorageData>('enemyPool').value;
        for (let i = 0; i < enemyPool.length; i++) {
            let enemyId = enemyPool[i];
            enemyValue.push(CardManager.instance.getCardData(enemyId));
        }
        let dragonId = [16, 17, 18, 19];
        for (let i = 0; i < dragonId.length; i++) {
            let id = dragonId[i];
            enemyValue.push(CardManager.instance.getCardData(id))
        }
        let helpDataArr = GameDataManager.instance.helpUnitDataArr;
        let names = this.getMonsterNames(groupValue).concat(this.getMonsterNames(helpDataArr), this.getMonsterNames(enemyValue), [
            "SceneInfo.lh",
            "MainCamera.lh",
            "SelectLight.lh",
            "SelectLightSkill.lh",
            "BronEffect.lh",
            "Skill_FireBall.lh",
            "AddCrystal.lh",
            "AddCrystal10.lh",
            "AddCrystal20.lh",
            "AddCrystal30.lh",
            "MissilePoint1.lh",
            "MissileSizu1.lh",
            "MissileSizu2.lh",
            "MissileShuangzu1.lh",
            "MissileDragon1.lh",
            "MissileDragon2.lh",
            "MissileDragon3.lh",
            "ExplodeDragon1.lh",
            "ExplodeDragon2.lh",
            "ExplodeDragon3.lh",
            "ExplodeShuangzu1.lh",
            "ExplodeSizu1.lh",
            "Rune1.lh",
            "Rune2.lh",
            "Rune3.lh",
            "Rune4.lh",
            "RuneBuff1.lh",
            "RuneBuff2.lh",
            "RuneBuff3.lh",
            "RuneLight1.lh",
            "RuneLight2.lh",
            "RuneLight3.lh",
            "CastleFire1.lh",
            "CastleFire2.lh",
            "Castle1_Boom.lh",
            "Castle2_Boom.lh",
            "Weather_Electric.lh",
            "Weather_Flash.lh",
            "Weather_Fog.lh",
            "Weather_Rain.lh",
            "Weather_Sun1.lh",
            "Weather_Sun2.lh",
            "Weather_Sun3.lh",
            "Weather_Wind.lh",
            "SelectLightSkill1.lh",
            "SelectLightSkill2.lh",
            "SelectLightSkill3.lh",
            "SelectLightSkill4.lh",
            "SelectLightSkill5.lh",
            "Skill1.lh",
            "Skill2.lh",
            "Skill3.lh",
            "Skill3Buff.lh",
            "FX_TowerBron.lh",
            "MissileTower1.lh",
            "MissileTower2.lh",
            "Tower1.lh",
            "Tower2.lh",
            "TowerDeath1.lh",
            "TowerDeath2.lh",
            "FX_Flash.lh",
            "SelectLightSkill5_1.lh",
            "SelectLightSkill5_2.lh",
            "SelectLightSkill5_Range.lh",
            "Skill6.lh",
            "Skill6Buff.lh",
            "NoEntry.lh"
        ])
        if (GameDataManager.instance.isPVP) {
            names.push("LevelVS.ls");
        } else {
            names.push(`Level${GameDataManager.instance.playStage[0]}.ls`);
        }
        GameDataManager.instance.updateMapBuffData();
        ResManager.create(names, this.onProgress.bind(this)).then(() => {
            Laya.Scene.load(SceneName.play, Laya.Handler.create(this, (gameRuntime: GameRuntime) => {
                gameRuntime.open(true);
            }))
        })
    }

    onProgress(res: { type: string, progress: number, location?: string, name?: string, totalBytesWritten?: number, totalBytesExpectedToWrite?: number }) {
        if (res.type == "download") {
            this.descLabel.text = `下载资源${res.name}中`;
        } else if (res.type == "create") {
            this.descLabel.text = `创建资源中`;
        }
        this.bar.width = 520 * res.progress;
        if (res.progress == 0) {
            this.bar.width = 520 * res.progress;
        } else {
            Laya.Tween.to(this.bar, { width: 520 * res.progress }, 500, Laya.Ease.sineOut);
        }
    }

    onDestroy() {
        Laya.timer.clearAll(this);
    }

    onFrameLoop() {
        this.bg.x -= this.moveSpeed;
        this.bg.y -= this.moveSpeed;
        if (this.bg.x <= -168) {
            this.bg.x = 0;
            this.bg.y = 0;
        }
    }
    getMonsterNames(carddatas: any[]) {
        let names = [];
        let record = [];
        for (let i = 0; i < carddatas.length; i++) {
            const carddata = carddatas[i];
            let skin = carddata.skin;
            let skinarr = skin.split('-');
            for (let j = 0; j < skinarr.length; j++) {
                const skinname = skinarr[j];
                if (record.indexOf(skinname) == -1) {
                    record.push(skinname);
                    if (carddata.type == 1) {
                        names.push(`Player2_${skinname}1.lh`)
                        names.push(`Player2_${skinname}2.lh`)
                        names.push(`Player2_${skinname}3.lh`)
                        names.push(`Player2.lh`);
                    } else if (carddata.type == 2) {
                        names.push(`Player1_${skinname}1.lh`)
                        names.push(`Player1_${skinname}2.lh`)
                        names.push(`Player1_${skinname}3.lh`)
                        names.push(`Player1.lh`);
                    } else if (carddata.type == 3) {
                        names.push(`Player3_${skinname}1.lh`)
                        names.push(`Player3_${skinname}2.lh`)
                        names.push(`Player3_${skinname}3.lh`)
                        names.push(`Player3.lh`);
                    }
                }
            }
        }
        return names;
    }
}