import GEvent from "../../GameFrame/event/GEvent";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import ResManager from "../../gframe-work/manager/ResManager";
import UserDataManager from "../../gframe-work/manager/UserDataManager";
import { EventTypeEnum } from "../GEnum";

export default class CardManager {
    public standNameArr = ["Bear", "Chicken", "Monkey", "Panda", "Crocodile", "Tortoise", "Sloth", "Elephant"];
    public climbNameArr = ["Wolf", "Pig", "Tiger", "Hippo", "Lion", "Leopard", "Rhinoceros", "Cow"];
    public flyNameArr = ["DesertDragon", "FireDragon", "IceDragon", "PoisonDragon"];
    private static _instance: CardManager;
    public static get instance() {
        if (!CardManager._instance) {
            CardManager._instance = new CardManager();
        }
        return CardManager._instance;
    }
    private constructor() { }
    /**
    * id转显示的皮肤
    * @param id 图鉴id
    */
    idToSkin(id: string) {
        let idArr: string[] = id.split("-");
        let nameArr: string[] = [];
        let type: number = 0;
        for (let i = 0; i < idArr.length; i++) {
            let id = Number(idArr[i]);
            if (id <= this.standNameArr.length) {
                nameArr.push(this.standNameArr[id - 1]);
                if (!type) type = 2;
            } else if (id <= this.climbNameArr.length) {
                nameArr.push(this.climbNameArr[id - 8 - 1]);
                if (!type) type = 1;
            } else if (id <= this.flyNameArr.length) {
                nameArr.push(this.flyNameArr[id - 16 - 1]);
                if (!type) type = 3;
            }
        }
        return { skin: nameArr.join("-"), type: type };
    }
    /**
    * 皮肤转图鉴的id
    * @param skin 皮肤
    */
    skinToId(skin: string) {
        let nameArr: string[] = skin.split("-");
        let idArr: number[] = [];
        let type: number = 0;
        for (let i = 0; i < nameArr.length; i++) {
            let index = -1;
            if ((index = this.standNameArr.indexOf(nameArr[i])) != -1) {
                idArr.push(index + 1);
                if (!type) type = 2;
            } else if ((index = this.climbNameArr.indexOf(nameArr[i])) != -1) {
                idArr.push(index + 1 + 8);
                if (!type) type = 1;
            } else if ((index = this.flyNameArr.indexOf(nameArr[i])) != -1) {
                idArr.push(index + 1 + 16);
                if (!type) type = 3;
            }
        }
        return { id: idArr.join("-"), type: type };
    }
    /**
     * 更新图鉴数据
     * @param cardData  新卡牌数据
     */
    updateHandbookRecord(cardData: any) {
        let idObj = this.skinToId(cardData.skin);
        if (UserDataManager.instance.handBookRecord.indexOf(idObj.id) == -1) {
            UserDataManager.instance.handBookRecord.push(idObj.id);
            UserDataManager.instance.newUnlockRecord.push(idObj.id);
        }
    }
    /**
     * 获取法术卡牌数据
     * @param magicType 
     * @param magicLevel 
     */
    getMagicCardData(magicType: number, magicLevel: number) {
        let magicConfig = ResManager.getRes<any>("spell.json");
        let magicNum = 6;
        let magicDataIndex = magicNum * (magicType - 1) + magicLevel - 1;
        return magicConfig[magicDataIndex];
    }
    getCardDataArr(unitIdArr: number[], fightLevelArr: number[]) {
        let monsterDataArr = [];
        for (let i = 0; i < unitIdArr.length; i++) {
            let cardData = this.getCardData(unitIdArr[i]);
            cardData.exp = this.getBaseExpByFightLevel(fightLevelArr[i]);
            monsterDataArr.push(cardData);
        }
        return monsterDataArr;
    }
    getFightData(dataType: number, fightLevel: number) {
        let fightConfig = ResManager.getRes<any>("fightLevel.json");
        return fightConfig[(dataType - 1) * 50 + fightLevel - 1];
    }
    getCardColor(fightLevel: number) {
        let color = "";
        if (fightLevel <= 5) {
            color = "white";
        } else if (fightLevel <= 15) {
            color = "green";
        } else if (fightLevel <= 30) {
            color = "blue";
        } else if (fightLevel <= 50) {
            color = "purple";
        }
        return color;
    }
    getCardData(unitId: number) {
        if (unitId == -1) return { id: 0, skin: "", type: 0, datatype: 0, exp: 0 };
        let unitConfig = ResManager.getRes<any>("unit.json");
        let unitData = unitConfig[unitId];
        let cardData = {
            id: 0,
            skin: `${unitData.name}-${unitData.name}-${unitData.name}`,
            type: unitData.type,
            datatype: unitData.datatype,
            exp: 1
        };
        return cardData;
    }
    getColorByEXP(exp: number) {
        let color = "";
        if (exp < 9) {
            color = "white";
        } else if (exp < 108) {
            color = "green";
        } else if (exp < 1620) {
            color = "blue";
        } else {
            color = "purple";
        }
        return color;
    }
    getFightLevelByEXP(exp: number) {
        let levelArr = [1, 6, 16, 31, 50];
        let expArr = [1, 9, 108, 1620, 24300];
        let baseExp = expArr[expArr.length - 2];
        let baseLevel = levelArr[levelArr.length - 2];
        let nextExp = expArr[expArr.length - 1];
        let nextLevel = levelArr[expArr.length - 1];
        for (let i = 1; i < expArr.length; i++) {
            let expLine = expArr[i];
            if (exp < expLine) {
                baseLevel = levelArr[i - 1];
                baseExp = expArr[i - 1];
                nextExp = expArr[i];
                nextLevel = levelArr[i];
                break;
            }
        }
        let fightLevel = Math.round((nextLevel - 1 - baseLevel) / (nextExp - 1 - baseExp) * (exp - baseExp) + baseLevel);
        return fightLevel;
    }
    getBaseExpByFightLevel(fightLevel: number) {
        let levelArr = [1, 6, 16, 31, 50];
        let expArr = [1, 9, 108, 1620, 24300];
        let baseLevel = levelArr[levelArr.length - 2];
        let baseExp = expArr[expArr.length - 2];
        for (let i = 1; i < levelArr.length; i++) {
            let levelLine = levelArr[i];
            if (fightLevel <= levelLine) {
                baseLevel = levelArr[i - 1];
                baseExp = expArr[i - 1];
                break;
            }
        }
        return baseExp;
    }
    getExpByFightLevel(fightLevel: number) {
        let levelArr = [1, 6, 16, 31, 50];
        let expArr = [1, 9, 108, 1620, 24300];
        let baseLevel = levelArr[levelArr.length - 2];
        let baseExp = expArr[expArr.length - 2];
        for (let i = 1; i < levelArr.length; i++) {
            let levelLine = levelArr[i];
            let expLine = expArr[i];
            if (fightLevel <= levelLine) {
                baseLevel = levelArr[i - 1];
                baseExp = expArr[i - 1];
                let exp = baseExp + Math.round((expLine - baseExp) / (levelLine - baseLevel) * (fightLevel - baseLevel));
                return exp;
            }
        }
    }
    /**对牌库里面所有的牌分类 */
    splitAllData(): { double: any[], four: any[], fly: any[] } {
        let houseLocal = DataManager.get<LocalStorageData>('house');
        let allCardData: any[] = houseLocal.value;
        let doubleArray = [];
        let fourArray = [];
        let flyArray = [];
        for (let i = 0; i < allCardData.length; i++) {
            const data = allCardData[i];
            if (data.type == 1) {
                fourArray.push(data);
            } else if (data.type == 2) {
                doubleArray.push(data);
            } else if (data.type == 3) {
                flyArray.push(data);
            }
        }
        return { double: doubleArray, four: fourArray, fly: flyArray }
    }
    addNewCard(cardData: any[]) {
        let newCardIdLocal = DataManager.get<LocalStorageData>('newCardId');
        let houseLocal = DataManager.get<LocalStorageData>('house');
        let dataObj = CardManager.instance.splitAllData();
        for (let i = 0; i < cardData.length; i++) {
            let data = cardData[i];
            data.id = newCardIdLocal.value;
            newCardIdLocal.value++;
            if (data.type == 1) {
                dataObj.four.push(data);
            } else if (data.type == 2) {
                dataObj.double.push(data);
            } else if (data.type == 3) {
                dataObj.fly.push(data);
            }
        }
        let newAllData = dataObj.double.concat(dataObj.four, dataObj.fly)
        houseLocal.value = newAllData;
        GEvent.event(EventTypeEnum.CardHouseChange);
    }
    getOriginalCost(skin: string) {
        let unitConfig = ResManager.getRes<any[]>("unit.json");
        let skinArr = skin.split("-");
        let maxCost = 0;
        for (let i = 0; i < 20; i++) {
            let unitData = unitConfig[i];
            let index = skinArr.indexOf(unitData.name);
            if (index != -1) {
                skinArr.splice(index, 1);
                if (unitData.cost > maxCost) {
                    maxCost = unitData.cost;
                }
            }
        }
        return maxCost;
    }
}