import { ui } from "../../ui/layaMaxUI";
import { EventTypeEnum, SceneName } from "../GEnum";
import GEvent from "../../GameFrame/event/GEvent";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import { CardData } from "../data/CardData";
import { AllPlatformAdMgr } from "../../GameFrame/advert/AllPlatformAdMgr";
import BreakPointManager from "./BreakPointManager";
import ResManager from "../../gframe-work/manager/ResManager";
import EventManager from "../../gframe-work/manager/EventManager";
import PlatFormManager from "../../gframe-work/manager/PlatFormManager";
import GuideManager from "./GuideManager";
import CardManager from "./CardManager";
import NetManager from "../../gframe-work/manager/NetManager";
import UserDataManager from "../../gframe-work/manager/UserDataManager";
export default class LoadRuntime extends ui.loadUI {
    onAwake() {
        mpsdk.init(7107, "lszbn").then(openid => {
            console.log('获得openid', openid);
        }).catch(e => {
            //初始化失败，游戏自行处理
            console.log('初始化失败.', e);
        });
        this.openWxShare();
        Laya.MouseManager.multiTouchEnabled = false;
        UIConfig.closeDialogOnSide = false;
        GEvent.init();
        EventManager.init();
        PlatFormManager.init();
        NetManager.init();
        this.registerPlatformEvent();
        ResManager.init().then(() => {
            let fontNameArr = ['ZooFont0.fnt', 'ZooFont1.fnt', 'ZooFont2.fnt', "Green.fnt", "Word.fnt", "TianFu.fnt"];
            ResManager.loadFonts(fontNameArr).then(() => {
                AllPlatformAdMgr.Instance.init();
                DataManager.init();
                BreakPointManager.init();
                this.onManagerInit();
            })
        })
    }

    openWxShare() {
        if (window.wx) {
            (wx as any).showShareMenu({
                withShareTicket: true,
                menus: ['shareAppMessage', 'shareTimeline']
            });
        }
    }

    registerPlatformEvent() {
        if (Laya.Browser.onMiniGame) {
            Laya.MiniAdpter.window.wx.onShow(() => {
                console.log('OnShow');
                GEvent.event(EventTypeEnum.OnShow);
            });
            Laya.MiniAdpter.window.wx.onHide(() => {
                console.log('OnHide');
                GEvent.event(EventTypeEnum.OnHide);
            });
        }
    }

    onProgress(res: { type: string, progress: number, location?: string, name?: string, totalBytesWritten?: number, totalBytesExpectedToWrite?: number }) {
        if (res.type == "download") {
            this.descLabel.text = `下载资源${res.name}中`;
        } else if (res.type == "create") {
            this.descLabel.text = `创建资源中`;
        }
        if (res.progress == 0) {
            this.progressMask.x = (res.progress - 1) * this.progressMask.width;
        } else {
            Laya.Tween.to(this.progressMask, { x: (res.progress - 1) * this.progressMask.width }, 500, Laya.Ease.sineOut);
        }
    }

    onManagerInit() {
        if (window.wx) {
            (<any>wx).login({
                success: (res: any) => {
                    if (res.code) {
                        NetManager.send(NetManager.getHttpUrl(1, res), "get", null, this.onLogin1);
                    } else {
                        console.error('登录失败！' + res.errMsg)
                    }
                }
            })
        }
        let createNames = ["fightLevel.json", "gacha.json", "gachapool.json", "task.json", "pvp.json", "spell.json", "map.json", "buff.json", "unit.json", "talent.json", "Main.ls", "Card.ls", "Show.ls"];
        ResManager.create(createNames, this.onProgress.bind(this)).then(() => {
            this.afterResLoad();
        })
    }

    onLogin1(data: any) {
        console.log(data);
        UserDataManager.instance.wxunionid = data.info.unionId;
        UserDataManager.instance.wxopenid = data.info.openid;
        NetManager.send(NetManager.getHttpUrl(2, null), "get", null, this.onLogin2);
    }
    onLogin2(data: any) {
        console.log(data);
    }

    afterResLoad() {
        let emptyCard = CardManager.instance.getCardData(-1);
        DataManager.setLocal('group1', [emptyCard, emptyCard, emptyCard]);
        DataManager.setLocal('group2', [emptyCard, emptyCard, emptyCard]);
        DataManager.setLocal('group3', [emptyCard, emptyCard, emptyCard]);
        DataManager.setLocal('unlock', 3)
        DataManager.setLocal('curGroup', 1)
        DataManager.setLocal('gold', 800)
        DataManager.setLocal('diamond', 4)
        DataManager.setLocal('curLevel', 1)
        DataManager.setLocal('baseLevel', 1);
        DataManager.setLocal('mineLevel', 1);
        DataManager.setLocal('shopId', 1);
        DataManager.setLocal('wallBaseHp', 500);
        DataManager.setLocal('wallUpHp', 100);
        DataManager.setLocal('mineBaseCreate', 60);
        DataManager.setLocal('mineUpCreate', 10);
        DataManager.setLocal('pvpWinCount', 0);
        DataManager.setLocal('curDivision', 1);
        DataManager.setLocal('pvpCoin', 0);
        DataManager.setLocal('pvpScore', 0);
        DataManager.setLocal('divisionRecord', [0, 0, 0, 0, 0, 0, 0]);
        DataManager.setLocal('everydayDivisionRecord', '');
        DataManager.setLocal('signTimeStap', 0);
        DataManager.setLocal('signDay', 1);
        DataManager.setLocal('signed', 0);
        DataManager.setLocal('freeLottery', 0);
        DataManager.setLocal('refreshShopTimeStap', 0);
        DataManager.setJson('unit', 'unit.json');
        DataManager.setJson('pvp', 'pvp.json');
        mpsdk.Report.reportLogin(DataManager.get<LocalStorageData>("gold").value, DataManager.get<LocalStorageData>("curLevel").value);
        let dataArray = [];
        let monsterId = [0, 1, 2];
        for (let i = 0; i < monsterId.length; i++) {
            let cardData = CardManager.instance.getCardData(monsterId[i]);
            cardData.id = i + 1;
            dataArray.push(cardData);
        }
        DataManager.setLocal('house', dataArray)
        DataManager.setLocal('enemyPool', monsterId)
        DataManager.setLocal('shop', []);
        DataManager.setLocal('newCardId', dataArray.length + 1);
        let atlas = [
            "card.atlas",
            "compose.atlas",
            "count.atlas",
            "game.atlas",
            "gameload.atlas",
            "gift.atlas",
            "guide.atlas",
            "handbook.atlas",
            "lottery.atlas",
            "main.atlas",
            "map.atlas",
            "match.atlas",
            "part.atlas",
            "rank.atlas",
            "shop.atlas",
            "sign.atlas",
            "task.atlas",
            "tip.atlas",
            "try.atlas"
        ];
        ResManager.download(atlas, this.onProgress.bind(this)).then(() => {
            GuideManager.Instance.init();
            Laya.Scene.open(SceneName.main);
        })
    }
}