import GEvent from "../../GameFrame/event/GEvent";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import MathTool from "../../gframe-work/tool/MathTool";
import { ui } from "../../ui/layaMaxUI";
import { EventTypeEnum, SceneName } from "../GEnum";
import DivisionRuntime from "./DivisionRuntime";
import UpDivisionRuntime from "./UpDivisionRuntime";

export default class DivisionRewardRuntime extends ui.divisionRewardUI {
    divisionRecordLocal: LocalStorageData;
    goldLocal: LocalStorageData;
    pvpWinCountLocal: LocalStorageData;
    curDivisionLocal: LocalStorageData;
    everydayDivisionRecordLocal: LocalStorageData;
    pvpScoreLocal: LocalStorageData;
    pvpConfig: any;
    boxArr: Laya.Image[] = [];
    pointArr: Laya.Image[] = [];
    lineArr: Laya.Image[] = [];
    divisionArr: Laya.Image[] = [];
    onAwake() {
        this.goldLocal = DataManager.get<LocalStorageData>('gold');
        this.pvpWinCountLocal = DataManager.get<LocalStorageData>('pvpWinCount');
        this.curDivisionLocal = DataManager.get<LocalStorageData>('curDivision');
        this.pvpScoreLocal = DataManager.get<LocalStorageData>('pvpScore');
        this.divisionRecordLocal = DataManager.get<LocalStorageData>('divisionRecord');
        this.everydayDivisionRecordLocal = DataManager.get<LocalStorageData>('everydayDivisionRecord');
        this.pvpConfig = DataManager.get<any>('pvp');
        this.boxArr = [this.box1, this.box2, this.box3, this.box4, this.box5, this.box6, this.box7]
        this.lineArr = [this.line1, this.line2, this.line3, this.line4, this.line5, this.line6]
        this.pointArr = [this.point1, this.point2, this.point3, this.point4, this.point5, this.point6, this.point7]
        this.divisionArr = [this.division1, this.division2, this.division3, this.division4, this.division5, this.division6, this.division7]
        this.box1.on(Laya.Event.CLICK, this, this.onBoxEvent, [1]);
        this.box2.on(Laya.Event.CLICK, this, this.onBoxEvent, [2]);
        this.box3.on(Laya.Event.CLICK, this, this.onBoxEvent, [3]);
        this.box4.on(Laya.Event.CLICK, this, this.onBoxEvent, [4]);
        this.box5.on(Laya.Event.CLICK, this, this.onBoxEvent, [5]);
        this.box6.on(Laya.Event.CLICK, this, this.onBoxEvent, [6]);
        this.box7.on(Laya.Event.CLICK, this, this.onBoxEvent, [7]);
        this.receiveButton.on(Laya.Event.CLICK, this, this.onMouseEvent, ['receive']);
        this.initRewardProgress();
        this.initDivisionInfo();
        this.initEverydayReward();
    }

    format(fmt: string, date: Date) {
        var o = {
            "M+": date.getMonth() + 1,                 //月份 
            "d+": date.getDate(),                    //日 
            "h+": date.getHours(),                   //小时 
            "m+": date.getMinutes(),                 //分 
            "s+": date.getSeconds(),                 //秒 
            "q+": Math.floor((date.getMonth() + 3) / 3), //季度 
            "S": date.getMilliseconds()             //毫秒 
        };
        if (/(y+)/.test(fmt)) {
            fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
        }
        for (var k in o) {
            if (new RegExp("(" + k + ")").test(fmt)) {
                fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
            }
        }
        return fmt;
    }

    initRewardProgress() {
        let record = this.divisionRecordLocal.value;
        let big = Math.ceil(this.curDivisionLocal.value / 3);
        for (let i = 0; i < 7; i++) {
            let box = this.boxArr[i];
            let division = this.divisionArr[i];
            let line = this.lineArr[i - 1];
            let point = this.pointArr[i];
            if (i < big) {
                point.skin = 'rank/Ui_jindu3.png';
                if (line) {
                    line.skin = 'rank/Ui_jindu4.png';
                }
                box.visible = true;
                division.visible = true;
            } else {
                point.skin = 'rank/Ui_jindu1.png';
                if (line) {
                    line.skin = 'rank/Ui_jindu2.png';
                }
                box.visible = false;
                division.visible = false;
            }
            if (record[i] == 1) {
                box.skin = `rank/Ui_box${i + 1}open.png`;
            } else {
                box.skin = `rank/Ui_box${i + 1}.png`;
            }
        }
    }

    onBoxEvent(index: number, e: Laya.Event) {
        let record = this.divisionRecordLocal.value;
        if (record[index - 1] == 0) {
            Laya.Scene.load(SceneName.upDivision, Laya.Handler.create(this, (upDivisionRuntime: UpDivisionRuntime) => {
                upDivisionRuntime.open(false);
                upDivisionRuntime.setDivision(3 * index - 2);
                upDivisionRuntime.closeHandler = Laya.Handler.create(this, () => {
                    this.initRewardProgress();
                })
            }))
        }
    }

    initDivisionInfo() {
        let nextDivisionData = this.pvpConfig[this.curDivisionLocal.value];
        (<DivisionRuntime>this.divisionIcon).division = this.curDivisionLocal.value;
        (<DivisionRuntime>this.divisionIcon).labelIcon.visible = true;
        this.rankNum.text = '1';
        this.scoreLabel.text = this.pvpScoreLocal.value;
        this.successNeedLabel.text = `胜场${this.pvpWinCountLocal.value}/${nextDivisionData.winCount}`;
    }

    initEverydayReward() {
        let curDivisionData = this.pvpConfig[this.curDivisionLocal.value - 1];
        this.moneyAddLabel.text = curDivisionData.rankRewardCoin.toString();
        let time = this.format("yyyy-MM-dd", new Date());
        if (time != this.everydayDivisionRecordLocal.value) {
            this.everydayDivisionRecordLocal.value = '';
        }
        let big = Math.ceil(this.curDivisionLocal.value / 3);
        if (this.everydayDivisionRecordLocal.value != '') {
            this.receiveButton.skin = 'rank/Btn_received.png'
            this.receiveButton.mouseEnabled = false;
            this.curRewardBox.skin = `rank/Ui_box${big}open.png`;
        } else {
            this.receiveButton.mouseEnabled = true;
            this.receiveButton.skin = 'rank/Btn_receive.png'
            this.curRewardBox.skin = `rank/Ui_box${big}.png`;
        }
        let shopLocal = DataManager.get<LocalStorageData>('shop');
        let shopDataArr: any[] = shopLocal.value;
        for (let i = 0; i < 2; i++) {
            let data = shopDataArr[MathTool.getRandom(0, shopDataArr.length - 1)];
            if (i == 0) {
                this.fightCell1.dataSource = data;
            } else if (i == 1) {
                this.fightCell2.dataSource = data;
            }
        }
    }

    onMouseEvent(name: string, e: Laya.Event) {
        if (name == 'receive') {
            this.receiveEverydayReward();
        }
    }

    receiveEverydayReward() {
        this.goldLocal.value += Number(this.moneyAddLabel.text);
        let time = this.format("yyyy-MM-dd", new Date());
        this.everydayDivisionRecordLocal.value = time;
        let houseLocal = DataManager.get<LocalStorageData>(`house`);
        let newCardIdLocal = DataManager.get<LocalStorageData>('newCardId');
        let houseArr: any[] = houseLocal.value;
        for (let i = 0; i < 2; i++) {
            let data: any;
            if (i == 0) {
                data = this.fightCell1.dataSource;
            } else if (i == 1) {
                data = this.fightCell2.dataSource;
            }
            data.id = newCardIdLocal.value;
            newCardIdLocal.value++;
            houseArr.push(data);
            houseLocal.value = houseArr;
        }
        GEvent.event(EventTypeEnum.CardHouseChange);
        this.initEverydayReward();
    }
}