import GEvent from "../../GameFrame/event/GEvent";
import ResManager from "../../gframe-work/manager/ResManager";
import UserDataManager from "../../gframe-work/manager/UserDataManager";
import { ui } from "../../ui/layaMaxUI";
import { EventTypeEnum } from "../GEnum";

export default class TaskRuntime extends ui.taskUI {
    private curIndex: number = -1;
    private activeValueArr: number[] = null;
    onAwake() {
        this.taskList.vScrollBarSkin = null;
        this.showTask(0);
    }
    onOpened() {
        this.dayTask.on(Laya.Event.CLICK, this, this.onPageEvent);
        this.achieveTask.on(Laya.Event.CLICK, this, this.onPageEvent);
        this.receiveBtn.on(Laya.Event.CLICK, this, this.onPageEvent);
        GEvent.on(EventTypeEnum.ReceiveTaskReward, this, this.receiveTaskReward);
    }
    onPageEvent(e: Laya.Event) {
        if (e.target == this.dayTask) {
            this.titleBg.x = 65;
            this.showTask(0);
        } else if (e.target == this.achieveTask) {
            this.titleBg.x = 373;
            this.showTask(1);
        } else if (e.target == this.receiveBtn) {

        }
    }
    onDestroy() {
        GEvent.off(EventTypeEnum.ReceiveTaskReward, this, this.receiveTaskReward);
    }
    receiveTaskReward() {
        let curIndex = this.curIndex;
        this.curIndex = -1;
        this.showTask(curIndex);
    }
    showTask(taskIndex: number) {
        if (taskIndex == this.curIndex) return;
        this.curIndex = taskIndex;
        let taskConfig = ResManager.getRes<any>('task.json');
        let dataArr = [];
        if (this.curIndex == 0) {
            this.activeBox.visible = true;
            this.activeValueArr = [];
            let dayTaskId = UserDataManager.instance.dayTaskId;
            for (let i = 0; i < dayTaskId.length; i++) {
                let data = taskConfig[dayTaskId[i] % 1000 + 19 - 1];
                dataArr.push(data);
            }
        } else {
            this.activeBox.visible = false;
            for (let i = 0; i < 19; i++) {
                let data = taskConfig[(1001 + i) % 1000 - 1];
                dataArr.push(data);
            }
        }
        this.taskList.array = dataArr;
        if (this.curIndex == 0) {
            this.refreshActiveValue();
        }
    }
    refreshActiveValue() {
        let fullActive = 120;
        let nowActive = 0;
        for (let i = 0; i < this.taskList.array.length; i++) {
            let receive = UserDataManager.instance.dayTaskReceive[i];
            let activeValue = this.taskList.array[i].activeValue;
            if (receive) nowActive += activeValue;
        }
        this.activeNum1.text = `${nowActive}/${fullActive}`;
        this.activeNum2.text = nowActive + "";
        let progress = nowActive / fullActive;
        progress = progress < 1 ? progress : 1;
        this.activeBar.width = progress * 456;
        if (nowActive >= fullActive) {
            this.receiveBtn.disabled = false;
        } else {
            this.receiveBtn.disabled = true;
        }
    }
}