export class CardData {
    id = 0;
    skin = ''
    type = 0;
    hp = 0;
    attack = 0;
    def = 0;
    attackDelay = 0;
    attackDistance = 0;
    moveSpeed = 0;
    color = '';
    cost = 0;

    constructor(data?: any) {
        if (data) {
            this.id = data.id;
            this.skin = data.skin;
            this.type = data.type;
            this.hp = data.hp;
            this.attack = data.attack;
            this.def = data.def;
            this.attackDelay = data.attackDelay;
            this.attackDistance = data.attackDistance;
            this.moveSpeed = data.moveSpeed;
            this.color = data.color;
            this.cost = data.cost;
        }
    }

    clear() {
        this.id = 0;
        this.skin = '';
        this.type = 0;
        this.hp = 0;
        this.attack = 0;
        this.def = 0;
        this.attackDelay = 0;
        this.attackDistance = 0;
        this.moveSpeed = 0;
        this.color = '';
        this.cost = 0;
    }

    clone() {
        return new CardData(this);
    }
}