import GEvent from "../../GameFrame/event/GEvent";
import { ui } from "../../ui/layaMaxUI";
import { EventTypeEnum } from "../GEnum";

export default class NewCardPopRuntime extends ui.newCardPopUI {
    onOpened() {
        this.goBuy.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.goComPose.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.goSelect.on(Laya.Event.CLICK, this, this.onBtnClick);
    }

    onBtnClick(e: Laya.Event) {
        if (e.target == this.goBuy) {
        } else if (e.target == this.goComPose) {
            GEvent.event(EventTypeEnum.JumpPage, 1);
        } else if (e.target == this.goSelect) {
            GEvent.event(EventTypeEnum.JumpPage, 3);
        }
        this.close()
    }
}