import { AllPlatformAdMgr } from "../../GameFrame/advert/AllPlatformAdMgr";
import ResManager from "../../gframe-work/manager/ResManager";
import { ui } from "../../ui/layaMaxUI";
import CardManager from "./CardManager";
import FightCellRuntime from "./FightCellRuntime";

export default class FreeTryRuntime extends ui.freeTryUI {
    onAwake() {
        let tryData = CardManager.instance.getCardData(16);
        tryData["cost"] = 5;
        tryData.id = 1;
        (<FightCellRuntime>this.card).dataSource = tryData;
        this.freeUse.on(Laya.Event.CLICK, this, this.onMouseEvent);
        this.freeNo.on(Laya.Event.CLICK, this, this.onMouseEvent);
    }

    onMouseEvent(e: Laya.Event) {
        if (e.target == this.freeUse) {
            AllPlatformAdMgr.Instance.showVideoAd(() => {
                this.close("yes");
            })
        } else if (e.target == this.freeNo) {
            this.close("no");
        }
    }
}