import { ui } from "../../ui/layaMaxUI";

export default class SignCellRuntime extends ui.signCellUI {
    public setData(day: number, data: any, state: number) {
        if (state == 0) {
            //不可领取
            this.dayFrame.skin = `sign/Ui_frameday${day}.png`;
            this.day.skin = `sign/Ui_day${day}.png`;
            this.canReceiveTag.visible = false;
            this.dayMask.visible = false;
        } else if (state == 1) {
            //可领取
            this.dayFrame.skin = `sign/Ui_frameday${day}2.png`;
            this.day.skin = `sign/Ui_day${day}1.png`;
            this.canReceiveTag.visible = true;
            this.dayMask.visible = false;
        } else if (state == 2) {
            //已领取
            this.dayFrame.skin = `sign/Ui_frameday${day}.png`;
            this.day.skin = `sign/Ui_day${day}.png`;
            this.canReceiveTag.visible = false;
            this.dayMask.visible = true;
            this.dayMask.width = this.dayFrame.width;
            this.dayMask.height = this.dayFrame.height;
        }

        if (data.desc == '普通卡包') {
            this.rewardNameImage.skin = 'sign/Ui_word1.png';
        } else if (data.desc == '钻石卡包') {
            this.rewardNameImage.skin = 'sign/Ui_word2.png';
        } else if (data.desc == '卡牌 X3') {
            this.rewardNameImage.skin = 'sign/Ui_word3.png';
        } else if (data.desc == '+688') {
            this.rewardNameImage.skin = 'sign/Ui_word4.png';
        } else if (data.desc == '+888') {
            this.rewardNameImage.skin = 'sign/Ui_word5.png';
        } else if (data.desc == '钻石卡包 X3') {
            this.rewardNameImage.skin = 'sign/Ui_word6.png';
        }
        if (data.type == 'gold') {
            this.moneyImage.visible = true;
            this.secretImage.visible = false;
            this.cardCell1.visible = false;
            this.cardCell2.visible = false;
            this.cardCell3.visible = false;
            this.packageLight.visible = false;
        } else if (data.type == 'card') {
            if (data.value.length == 1) {
                this.cardCell1.visible = false;
                this.cardCell2.visible = true;
                this.cell2.rotation = 19;
                this.cardCell3.visible = false;
                this.cardCell2.dataSource = data.value[0];
            } else {
                this.cardCell1.visible = true;
                this.cardCell2.visible = true;
                this.cell2.rotation = -15;
                this.cardCell3.visible = true;
                this.cardCell1.dataSource = data.value[0];
                this.cardCell2.dataSource = data.value[1];
                this.cardCell3.dataSource = data.value[2];
            }
            this.packageLight.visible = false;
            this.moneyImage.visible = false;
            this.secretImage.visible = false;
        } else if (data.type == 'package') {
            this.moneyImage.visible = false;
            this.secretImage.visible = true;
            this.cardCell1.visible = false;
            this.cardCell2.visible = false;
            this.cardCell3.visible = false;
            this.packageLight.visible = false;
            if (data.value.indexOf(1) != -1) {
                this.secretImage.skin = 'shop/Ui_bao1.png';
            } else if (data.value.indexOf(2) != -1) {
                this.secretImage.skin = 'shop/Ui_bao2.png';
            } else if (data.value.indexOf(3) != -1) {
                if (data.value.length == 3) {
                    this.packageLight.visible = true;
                    this.secretImage.skin = 'sign/Ui_kabaothree.png';
                } else {
                    this.secretImage.skin = 'shop/Ui_bao3.png';
                }
            }
        }
        this.rewardNameFrame.width = this.dayFrame.width - 70;
    }
}