import { ui } from "../../ui/layaMaxUI";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import GEvent from "../../GameFrame/event/GEvent";
import { EventTypeEnum, SceneName } from "../GEnum";
import ShopRuntime from "./ShopRuntime";
import GuideManager from "./GuideManager";
import { AllPlatformAdMgr } from "../../GameFrame/advert/AllPlatformAdMgr";
import ResManager from "../../gframe-work/manager/ResManager";
import EventManager from "../../gframe-work/manager/EventManager";
import EventType from "../../gframe-work/manager/EventType";
import NewCardPopRuntime from "./NewCardPopRuntime";
import CardManager from "./CardManager";

export default class BuyConfirmRuntime extends ui.buyConfirmUI {

    cost: number;
    goldLocal: LocalStorageData = null;
    houseLocal: LocalStorageData = null;
    newCardIdLocal: LocalStorageData = null;
    shopRuntime: ShopRuntime = null;
    shopLocal: LocalStorageData = null;

    onAwake() {
        this.goldLocal = DataManager.get<LocalStorageData>('gold');
        this.houseLocal = DataManager.get<LocalStorageData>('house');
        this.newCardIdLocal = DataManager.get<LocalStorageData>('newCardId');
        this.shopLocal = DataManager.get<LocalStorageData>('shop');
        this.buyBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['buy']);
        this.closeBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['close']);
        EventManager.on(EventType.NEWGUIDETASK, this, this.onNewGuideTaskEvent);
        AllPlatformAdMgr.Instance.showBannerAd();
    }

    onClosed() {
        AllPlatformAdMgr.Instance.hideBannerAd();
        AllPlatformAdMgr.Instance.initBannerAd();
    }

    onDestroy() {
        EventManager.off(EventType.NEWGUIDETASK, this, this.onNewGuideTaskEvent);
    }

    onNewGuideTaskEvent(task: string) {
        if (task == "点击购买") {
            GuideManager.Instance.showGuide(task, this.buyBtn);
        }
    }

    onMouseEvent(name: string, e: Laya.Event) {
        e.stopPropagation();
        if (name == 'buy') {
            if (this.goldLocal.value >= this.cost) {
                this.goldLocal.value -= this.cost;
                let data = JSON.parse(JSON.stringify(this.cell.dataSource));
                this.removeBuy(data.id);
                CardManager.instance.addNewCard([data]);
                ResManager.playSound('buyCard.mp3');
                GEvent.event(EventTypeEnum.SendTip, [['购买成功!']]);
                GEvent.event(EventTypeEnum.RefreshShop);
                GuideManager.Instance.finishTask("点击购买", 500);
                GEvent.event('BreakPoint', ['购买单卡', data.type]);
                if (GuideManager.Instance.guideFinish) {
                    Laya.Scene.load(SceneName.newCardPop, Laya.Handler.create(this, (newCardPop: NewCardPopRuntime) => {
                        newCardPop.isModal = true;
                        newCardPop.open();
                    }))
                }
            } else {
                ResManager.playSound('Error.mp3');
                GEvent.event(EventTypeEnum.SendTip, [['金币不足!']]);
            }
            this.close();
        } else if (name == 'close') {
            ResManager.playSound('Open.mp3');
            this.close();
        }
    }

    removeBuy(cardId: number) {
        let shopDataArray: any[] = this.shopLocal.value;
        for (let i = 0; i < shopDataArray.length; i++) {
            const shopData = shopDataArray[i];
            if (shopData.id == cardId) {
                shopDataArray.splice(i, 1);
                break;
            }
        }
        this.shopLocal.value = shopDataArray;
    }
}