import { AllPlatformAdMgr } from "../../GameFrame/advert/AllPlatformAdMgr";
import GEvent from "../../GameFrame/event/GEvent";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import { ui } from "../../ui/layaMaxUI";
import { EventTypeEnum, SceneName } from "../GEnum";
import LotteryResultRuntime from "./LotteryResultRuntime";

export default class AddMoneyRuntime extends ui.addMoneyUI {
    type: number = 0;
    setMoneyType(type: number) {
        this.type = type;
        this.goldLabel.visible = (this.type == 1);
        this.diamondLabel.visible = (this.type == 2);
        this.powerLabel.visible = (this.type == 3);
    }
    onAwake() {
        this.getBtn.visible = true;
        this.noBtn.visible = false;
        Laya.timer.once(3000, this, () => {
            this.noBtn.visible = true;
        })
        this.getBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['get']);
        this.noBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['no']);
        AllPlatformAdMgr.Instance.showBannerAd();
    }
    onClosed() {
        AllPlatformAdMgr.Instance.hideBannerAd();
        AllPlatformAdMgr.Instance.initBannerAd();
    }
    onMouseEvent(name: string) {
        if (name == 'get') {
            AllPlatformAdMgr.Instance.showVideoAd(() => {
                let data = { desc: '+688', value: 688, type: 'gold', isDirect: true };
                if (this.type == 1) {
                    data.type = 'gold';
                    data.value = 1000;
                    data.desc = '+1000';
                } else if (this.type == 2) {
                    data.type = 'diamond';
                    data.value = 10;
                    data.desc = '+10';
                } else if (this.type == 3) {
                    data.type = 'power';
                    data.value = 20;
                    data.desc = '+20';
                }
                Laya.Scene.load(SceneName.lotteryResult, Laya.Handler.create(this, (lotteryResult: LotteryResultRuntime) => {
                    lotteryResult.open(false);
                    lotteryResult.setData(data);
                }))
                this.close();
            })
        } else if (name == 'no') {
            this.close();
        }
    }
}