import { ui } from "../../ui/layaMaxUI";

export default class RankCellRuntime extends ui.rankCellUI {
    private _index: number = 0;
    private _isSelf: boolean = false;
    public get dataSource() {
        return this._dataSource;
    }
    public set dataSource(value: any) {
        super.dataSource = value;
        if (!this._dataSource) return;
        this.playerName.text = this._dataSource.name;
        this.playerScore.text = this._dataSource.score;
    }
    public get index() {
        return this._index;
    }
    public set index(value: number) {
        this._index = value;
        if (this._index == 1) {
            this.rankIcon.visible = true;
            this.rankLabel.visible = false;
            this.rankIcon.skin = 'rank/Ui_paihnagfrist.png';
            this.cellBg.skin = 'rank/Ui_paihang1.png';
        } else if (this._index == 2) {
            this.rankIcon.visible = true;
            this.rankLabel.visible = false;
            this.rankIcon.skin = 'rank/Ui_paihangsecond.png';
            this.cellBg.skin = 'rank/Ui_paihang2.png';
        } else if (this._index == 3) {
            this.rankIcon.visible = true;
            this.rankLabel.visible = false;
            this.rankIcon.skin = 'rank/Ui_paihnagthird.png';
            this.cellBg.skin = 'rank/Ui_paihang3.png';
        } else {
            this.rankIcon.visible = false;
            this.rankLabel.visible = true;
            this.rankLabel.text = this._index.toString();
            if (this._index == 4) {
                this.cellBg.skin = 'rank/Ui_paihang4.png';
            } else {
                this.cellBg.skin = 'rank/Ui_paihang6.png';
            }
        }
    }
    public get isSelf() {
        return this._isSelf;
    }
    public set isSelf(value: boolean) {
        this._isSelf = value;
        if (this._isSelf) {
            this.selfLight.visible = true;
        } else {
            this.selfLight.visible = false;
        }
    }
    onAwake() {

    }
}