import GEvent from "../../GameFrame/event/GEvent";
import { ui } from "../../ui/layaMaxUI";
import { EventTypeEnum } from "../GEnum";

export default class MapCellRuntime extends ui.mapCellUI {
    private canClick: boolean = false;
    onAwake() {
        this.on(Laya.Event.CLICK, this, this.onClick);
    }
    public set dataSource(data: { state: number, star: number, level: number, chapter: number, canClick: boolean }) {
        this._dataSource = data;
        this.canClick = data.canClick;
        if (data) {
            this.star1.skin = this.star2.skin = this.star3.skin = "map/Ui_PlaceStar2.png";
            this.stageNum.text = data.level + "";
            if (data.state == 1) {
                this.whiteBase.visible = true;
                this.starBox.visible = true;
                this.balckBase.visible = false;
                this.light.visible = false;
                this.stageNum.text = data.level.toString();
                this.shadow.skin = "map/Ui_Place2.png";
                this.stageNum.y = -25;
                if (data.star >= 1) this.star1.skin = "map/Ui_PlaceStar1.png";
                if (data.star >= 2) this.star2.skin = "map/Ui_PlaceStar1.png";
                if (data.star >= 3) this.star3.skin = "map/Ui_PlaceStar1.png";
            } else {
                this.stageNum.y = 10;
                this.whiteBase.visible = false;
                this.starBox.visible = false;
                this.balckBase.visible = true;
                this.shadow.skin = "map/Ui_Place1.png";
                this.light.visible = false;
            }
        }
    }
    public get dataSource() {
        return this._dataSource;
    }
    onClick() {
        if (!this.canClick) return;
        GEvent.event(EventTypeEnum.MapCellSelect, this._dataSource.level);
    }
    public setSelect(bool: boolean) {
        if (bool) {
            this.light.visible = true;
            this.breath.play(0, true);
        } else {
            this.light.visible = false;
            this.breath.stop();
        }
    }
}