import FortBase from "./FortBase";
import WeaponBase from "./WeaponBase";
import ProgressRuntime from "./ProgressRuntime";
import { ActorBase, ActorState } from "../../GameFrame/actor/ActorBase";
import GEvent from "../../GameFrame/event/GEvent";
import { EventTypeEnum } from "../GEnum";
import { RoleData } from "../data/GameData";
import BronEffectRuntime from "./BronEffectRuntime";
import ResManager from "../../gframe-work/manager/ResManager";
import MathTool from "../../gframe-work/tool/MathTool";
import LayaTool from "../../gframe-work/tool/LayaTool";
import GameDataManager from "../data/GameDataManager";
import CannonBase from "./CannonBase";
import GuideManager from "./GuideManager";

class RoleState {
    public static bron = 'Bron';
}

export default class RoleBase extends ActorBase {

    public moveDir: Laya.Vector3 = new Laya.Vector3;
    public translation: Laya.Vector3 = new Laya.Vector3;
    public animators: Laya.Animator[] = null;
    protected targetEnemyArray: RoleBase[] = null;
    protected targetTowerArray: CannonBase[] = null;
    protected roadArray: RoleBase[] = null;
    protected roadIndex: number = 0;
    public targetFort: FortBase = null;
    public isPlaced: boolean = false;
    public isTransparent: boolean = false;
    public sign: string = '123';
    public weaponPoint1: Laya.Sprite3D = null;
    public weaponPoint2: Laya.Sprite3D = null;
    public weaponEffect: Laya.Sprite3D = null;
    public weaponEffect1: Laya.Sprite3D = null;
    public type: number = 0;
    public bloodProgress: ProgressRuntime = null;
    private dataClone: RoleData = null;
    moveSpeedScale: number = 0.1;
    attackDelayScale: number = 0.1;
    attackDistanceScale: number = 0.7;
    attackDelayRecord: number = 0;
    public camera: Laya.Camera = null;
    private cameraOut: Laya.Vector4 = new Laya.Vector4;
    public attackType: number = 0;
    missilePointParticalArr: Laya.ShuriKenParticle3D[] = [];
    missilePointParticalArr1: Laya.ShuriKenParticle3D[] = [];
    doubleAttack: boolean = false;
    doubleMove: boolean = false;
    useEncourageMagic: boolean = false;
    isRecoverying: boolean = false;
    useCurseMagic: boolean = false;
    //刷新后加成时间
    refreshExtraTime: number = 3000;
    //玩家原始属性攻击力,生命值,移动速度,攻击速度
    roleOriginalValue: number[] = null;
    onAwake() {
        super.onAwake();
        this.getAnimators();
        if (this.owner.name == 'Player1') {
            this.weaponPoint1 = this.gameObject.getChildByName('LaunchPoint1') as Laya.Sprite3D;
            this.weaponPoint2 = this.gameObject.getChildByName('LaunchPoint2') as Laya.Sprite3D;
            this.sign = 'MissileShuangzu1';
            this.weaponEffect = ResManager.getRes<any>('MissilePoint1.lh').clone();
            this.weaponEffect1 = ResManager.getRes<any>('MissilePoint1.lh').clone();
            // let node1 = this.getChildByUrl(this.animators[2].owner, 'Bip001/Bip001 Pelvis/Bip001 Spine/Bip001 Spine1/Bip001 Neck/Bip001 R Clavicle/Bip001 R UpperArm/Bip001 R Forearm/Bip001 R Hand/HandPoint');
            // let node2 = this.getChildByUrl(this.animators[2].owner, 'Bip001/Bip001 Pelvis/Bip001 Spine/Bip001 Spine1/Bip001 Neck/Bip001 L Clavicle/Bip001 L UpperArm/Bip001 L Forearm/Bip001 L Hand/HandPoint');
            let node1 = this.getChildByUrl(this.animators[2].owner, 'Bip001/Bip001 Pelvis/Bip001 Spine/Bip001 Spine1/Bip001 Neck/Bip001 R Clavicle/Bip001 R UpperArm/Bip001 R Forearm/Bip001 R Hand');
            let node2 = this.getChildByUrl(this.animators[2].owner, 'Bip001/Bip001 Pelvis/Bip001 Spine/Bip001 Spine1/Bip001 Neck/Bip001 L Clavicle/Bip001 L UpperArm/Bip001 L Forearm/Bip001 L Hand');
            this.weaponEffect.transform.localPosition = new Laya.Vector3;
            this.weaponEffect.transform.localRotationEuler = new Laya.Vector3;
            this.weaponEffect1.transform.localPosition = new Laya.Vector3;
            this.weaponEffect1.transform.localRotationEuler = new Laya.Vector3;
            node1.addChild(this.weaponEffect);
            node2.addChild(this.weaponEffect1);
        } else if (this.owner.name == 'Player2') {
            this.sign = 'MissileSizu';
        } else if (this.owner.name == 'Player3') {
            if (this.owner.getChildByName('Player3_FireDragon1')) {
                this.sign = 'MissileDragon1';
            } else if (this.owner.getChildByName('Player3_IceDragon1')) {
                this.sign = 'MissileDragon2';
            } else if (this.owner.getChildByName('Player3_PoisonDragon1')) {
                this.sign = 'MissileDragon3';
            } else if (this.owner.getChildByName('Player3_DesertDragon1')) {
                this.sign = 'MissileDragon1';
            }
        }
        if (this.weaponEffect) {
            this.missilePointParticalArr.push(this.weaponEffect.getChildByName('Glow1') as Laya.ShuriKenParticle3D);
            this.missilePointParticalArr.push(this.weaponEffect.getChildByName('Sparkle1') as Laya.ShuriKenParticle3D);
            this.missilePointParticalArr.push(this.weaponEffect.getChildByName('Sparkle2') as Laya.ShuriKenParticle3D);
        }
        if (this.weaponEffect1) {
            this.missilePointParticalArr1.push(this.weaponEffect1.getChildByName('Glow1') as Laya.ShuriKenParticle3D);
            this.missilePointParticalArr1.push(this.weaponEffect1.getChildByName('Sparkle1') as Laya.ShuriKenParticle3D);
            this.missilePointParticalArr1.push(this.weaponEffect1.getChildByName('Sparkle2') as Laya.ShuriKenParticle3D);
        }
        if (this.missilePointParticalArr.length == 3) {
            this.missilePointParticalArr[0].particleSystem.playOnAwake = false;
            this.missilePointParticalArr[1].particleSystem.playOnAwake = false;
            this.missilePointParticalArr[2].particleSystem.playOnAwake = false;
        }
        if (this.missilePointParticalArr1.length == 3) {
            this.missilePointParticalArr1[0].particleSystem.playOnAwake = false;
            this.missilePointParticalArr1[1].particleSystem.playOnAwake = false;
            this.missilePointParticalArr1[2].particleSystem.playOnAwake = false;
        }
    }

    onStart() {
        this.playMissilePartical(false, "Attack1");
        this.playMissilePartical(false, "Attack");
    }

    playMissilePartical(bool: boolean, attackName: string) {
        if (attackName == "Attack1") {
            if (this.missilePointParticalArr.length == 3) {
                if (bool) {
                    this.missilePointParticalArr[0].particleSystem.play();
                    this.missilePointParticalArr[1].particleSystem.play();
                    this.missilePointParticalArr[2].particleSystem.play();
                } else {
                    this.missilePointParticalArr[0].particleSystem.stop();
                    this.missilePointParticalArr[1].particleSystem.stop();
                    this.missilePointParticalArr[2].particleSystem.stop();
                }
            }
        } else {
            if (this.missilePointParticalArr1.length == 3) {
                if (bool) {
                    this.missilePointParticalArr1[0].particleSystem.play();
                    this.missilePointParticalArr1[1].particleSystem.play();
                    this.missilePointParticalArr1[2].particleSystem.play();
                } else {
                    this.missilePointParticalArr1[0].particleSystem.stop();
                    this.missilePointParticalArr1[1].particleSystem.stop();
                    this.missilePointParticalArr1[2].particleSystem.stop();
                }
            }
        }
    }

    onEnable() {
        this.changeActorState(ActorState.stand, '');
    }

    onDisable() {
        this.curState = ActorState.none;
    }

    onUpdate() {
        this.updateBloodPositon();
        if (!this.isPlaced || this.isDeath || GameDataManager.instance.isGameOver || GameDataManager.instance.isGamePause) return;
        this.recoveryBlood();
        this.lostBlood();
        this.runAttackDelay();
        this.runRefreshExtraTime();
        this.findTarget();
        super.onUpdate();
    }

    registerCustomEvent() {
        GEvent.on(EventTypeEnum.BaseExplode, this, this.onBaseExplode);
        GEvent.on(EventTypeEnum.RuneFinish, this, this.onRuneFinish);
        GEvent.on(EventTypeEnum.MagicFinish, this, this.onMagicFinish);
    }

    cancelCustomEvent() {
        GEvent.off(EventTypeEnum.BaseExplode, this, this.onBaseExplode);
        GEvent.off(EventTypeEnum.RuneFinish, this, this.onRuneFinish);
        GEvent.on(EventTypeEnum.MagicFinish, this, this.onMagicFinish);
    }

    onDestroy() {
        super.onDestroy();
        Laya.timer.clearAll(this);
        if (this.bloodProgress) this.bloodProgress.destroy();
    }

    onTriggerEnter(other: Laya.PhysicsComponent) {
        if (other.owner.name.indexOf('Rune') != -1) {
        }
    }

    onRuneFinish(type: number, runeId: number, roadIndex: number) {
        if (type == this.type && roadIndex == this.roadIndex) {
            this.useRune(runeId, false);
        }
    }

    onMagicFinish(magicType: number, type: number) {
        if (magicType == 4) {
            if (this.type == type) {
                this.useMagicBuff(magicType, false);
            }
        } else if (magicType == 6) {
            if (this.type != type) {
                this.useMagicBuff(magicType, false);
            }
        }
    }

    init(actorData: RoleData): void {
        if (this.type == 0 && GuideManager.Instance.guideFinish == false) {
            actorData.attack *= 2;
        }
        super.init(actorData);
        this.dataClone = actorData.clone();
        this.roleOriginalValue = [this.dataClone.attack, this.dataClone.hp, this.dataClone.moveSpeed, this.dataClone.attackDelay];
        this.updateRoleProp();
        if (this.bloodProgress) {
            if (this.type == 0) {
                this.bloodProgress.white.skin = 'game/Ui_White.png';
            } else if (this.type == 1) {
                this.bloodProgress.white.skin = 'game/Ui_Red.png';
            }
            this.bloodProgress.fullValue = actorData.hp;
            this.bloodProgress.progress = 1;
            this.bloodProgress.visible = false;
            this.bloodProgress.scaleX = this.bloodProgress.scaleY = 0.3;
        }
        this.attackDelayRecord = 0;
    }

    onActorStateExit(state: string) {

    }

    onActorStateEnter(state: string) {
        if (state == ActorState.death) {
            let index = this.roadArray.indexOf(this);
            if (index >= 0) this.roadArray.splice(index, 1);
            Laya.timer.once(2000, this, () => {
                let tweenTarget = { y: this.gameObject.transform.position.y };
                Laya.Tween.to(tweenTarget, {
                    y: -2, update: Laya.Handler.create(this, () => {
                        if (this.gameObject) {
                            let newPos: Laya.Vector3 = this.gameObject.transform.position.clone();
                            newPos.y = tweenTarget.y;
                            this.gameObject.transform.position = newPos;
                        }
                    }, null, false)
                }, 1000, Laya.Ease.linearNone, Laya.Handler.create(this, () => {
                    Laya.timer.once(500, this, () => {
                        this.gameObject.destroy();
                    })
                }));
            })
        }
    }

    onActorStateStay(state: string) {
        if (state == ActorState.run) {
            let speed = (<RoleData>this.actorData).moveSpeed * this.moveSpeedScale * Laya.timer.delta / 1000;
            let ratio = 1;
            if (this.doubleMove) {
                ratio += 1;
            }
            if (GameDataManager.instance.randomBuffState[GameDataManager.instance.acidId - 1]) {
                ratio -= GameDataManager.instance.acidSpeedSlowPer;
            }
            if (this.useEncourageMagic) {
                ratio += GameDataManager.instance.encouragePer;
            }
            if (this.useCurseMagic) {
                ratio -= GameDataManager.instance.cursePer;
            }
            if (GameDataManager.instance.randomBuffState[GameDataManager.instance.redSunId - 1] == 1 ||
                GameDataManager.instance.randomBuffState[GameDataManager.instance.blueSunId - 1] == 1 ||
                GameDataManager.instance.randomBuffState[GameDataManager.instance.purpleSunId - 1] == 1
            ) {
                ratio -= GameDataManager.instance.sunProp[(<RoleData>this.actorData).type - 1][1];
            }
            speed *= ratio;
            Laya.Vector3.scale(this.moveDir, speed, this.translation);
            this.gameObject.transform.translate(this.translation);
        }
    }

    findTarget() {
        if (!this.targetEnemyArray || !this.targetFort || !this.targetTowerArray) return;

        let distance = 1000000;
        for (let i = 0; i < this.targetEnemyArray.length; i++) {
            const enemy = this.targetEnemyArray[i];
            let dis = Math.abs(this.gameObject.transform.position.z - enemy.gameObject.transform.position.z);
            if (dis < distance) {
                distance = dis;
            }
        }
        for (let i = 0; i < this.targetTowerArray.length; i++) {
            const tower = this.targetTowerArray[i];
            let dis = Math.abs(this.gameObject.transform.position.z - tower.gameObject.transform.position.z);
            if (dis < distance) {
                distance = dis;
            }
        }
        if (distance <= (<RoleData>this.actorData).attackDistance * this.attackDistanceScale) {
            if (this.attackDelayRecord == 0) {
                let attackNum = 0;
                if (this.owner.name == "Player1") {
                    attackNum = 2;
                } else if (this.owner.name == "Player2") {
                    attackNum = 2;
                } else if (this.owner.name == "Player3") {
                    attackNum = 1;
                }
                this.attackType = MathTool.getRandom(1, attackNum);
                this.changeActorState(ActorState.attack, this.attackType.toString());
            }
            return;
        }

        distance = Math.abs(this.gameObject.transform.position.z - this.targetFort.gameObject.transform.position.z);
        if (distance <= (<RoleData>this.actorData).attackDistance * this.attackDistanceScale) {
            if (this.attackDelayRecord == 0) {
                let attackNum = 0;
                if (this.owner.name == "Player1") {
                    attackNum = 2;
                } else if (this.owner.name == "Player2") {
                    attackNum = 2;
                } else if (this.owner.name == "Player3") {
                    attackNum = 1;
                }
                this.attackType = MathTool.getRandom(1, attackNum);
                this.changeActorState(ActorState.attack, this.attackType.toString());
            }
            return;
        }

        let runNum = 0;
        if (this.owner.name == "Player1") {
            runNum = 3;
        } else if (this.owner.name == "Player2") {
            runNum = 1;
        } else if (this.owner.name == "Player3") {
            runNum = 1;
        }
        this.changeActorState(ActorState.run, MathTool.getRandom(1, runNum).toString());
    }

    onAttack(param1: number, param2: number, param3: string) {
        if (this.gameObject.name != 'Player2') {
            ResManager.playSound('Attack.mp3');
            this.playMissilePartical(false, "Attack1");
            this.createWeapon();
        }
    }

    onAttackEnd(param1: number, param2: number, param3: string) {
        this.attackDelayRecord = (<RoleData>this.actorData).attackDelay;
        let ratio = 1;
        if (this.useEncourageMagic) {
            ratio -= GameDataManager.instance.encouragePer;
        }
        if (this.useCurseMagic) {
            ratio += GameDataManager.instance.cursePer;
        }
        this.attackDelayRecord *= ratio;
        if (!this.isDeath) {
            this.changeActorState(ActorState.stand, '');
        }
    }

    setTransparent(isTransparent: boolean) {
        if (isTransparent == this.isTransparent) return;
        this.isTransparent = isTransparent;
        let meshSprite3Ds: Laya.SkinnedMeshSprite3D[] = [];
        if (this.gameObject == null) {
            console.error(this);
        }
        this.getSkinnedMeshInChildren(this.gameObject, meshSprite3Ds);
        if (isTransparent) {
            for (let i = 0; i < meshSprite3Ds.length; i++) {
                const meshSprite3D = meshSprite3Ds[i];
                (<Laya.UnlitMaterial>meshSprite3D.skinnedMeshRenderer.material).albedoColorA = 0.5;
                (<Laya.UnlitMaterial>meshSprite3D.skinnedMeshRenderer.material).renderMode = 2;
            }
        } else {
            for (let i = 0; i < meshSprite3Ds.length; i++) {
                const meshSprite3D = meshSprite3Ds[i];
                (<Laya.UnlitMaterial>meshSprite3D.skinnedMeshRenderer.material).albedoColorA = 1;
                (<Laya.UnlitMaterial>meshSprite3D.skinnedMeshRenderer.material).renderMode = 0;
            }
        }
    }

    setPlaced(roadIndex: number, roadArray: RoleBase[], targetEnemyArray: RoleBase[], targetTowerArray: CannonBase[], effect: BronEffectRuntime = null) {
        this.roadIndex = roadIndex;
        this.roadArray = roadArray;
        this.targetEnemyArray = targetEnemyArray;
        this.targetTowerArray = targetTowerArray;
        this.setTransparent(false);
        this.gameObject.active = false;
        if (effect) {
            Laya.timer.once(5000, this, () => {
                effect.owner.removeSelf();
                effect.owner.destroy();
            })
            effect.callBack1 = () => {
                if (this) {
                    this.gameObject.active = true;
                    this.changeActorState(RoleState.bron, '');
                    Laya.timer.once(1500, this, () => {
                        this.isPlaced = true;
                    })
                }
            }
        }
    }

    getSkinnedMeshInChildren(node: Laya.Node, meshs: Laya.SkinnedMeshSprite3D[]) {
        for (let i = 0; i < node.numChildren; i++) {
            const element = node.getChildAt(i);
            if (element instanceof Laya.SkinnedMeshSprite3D) meshs.push(element);
            if (element.numChildren > 0) {
                this.getSkinnedMeshInChildren(element, meshs);
            }
        }
    }

    createWeapon() {
        if (GameDataManager.instance.isGameOver || GameDataManager.instance.isGamePause) return;
        let modelName = this.sign;
        if (this.gameObject.name == 'Player2') {
            modelName = this.sign + `${this.attackType}`;
        }
        let weapon: WeaponBase = Laya.Pool.getItem(modelName);
        if (!weapon) {
            let weaponModel: Laya.Sprite3D = ResManager.getRes<any>(`${modelName}.lh`).clone();
            weapon = weaponModel.addComponent(WeaponBase);
            this.gameObject.parent.addChild(weaponModel);
        } else {
            this.gameObject.parent.addChild(weapon.gameObject);
        }
        if (weapon.gameObject.name == 'MissileShuangzu1') {
            Laya.timer.frameOnce(1, this, () => {
                let trail = weapon.gameObject.getChildByName('Trail') as Laya.TrailSprite3D;
                trail.trailFilter.time = 0.2;
            })
        }
        if (this.weaponPoint1 && this.weaponPoint2) {
            if (this.attackType == 1) {
                weapon.gameObject.transform.position = this.weaponPoint1.transform.position.clone();
            } else if (this.attackType == 2) {
                weapon.gameObject.transform.position = this.weaponPoint2.transform.position.clone();
            }
        } else {
            weapon.gameObject.transform.position = this.gameObject.transform.position.clone();
        }
        if (this.type == 0) {
            weapon.gameObject.transform.localRotationEuler = new Laya.Vector3(0, 180, 0);
        } else {
            weapon.gameObject.transform.localRotationEuler = new Laya.Vector3(0, 0, 0);
        }
        let attackValue = (<RoleData>this.actorData).attack;
        let ratio = 1;
        if (this.doubleAttack) {
            ratio += 1;
        }
        if (GameDataManager.instance.randomBuffState[GameDataManager.instance.redSunId - 1] == 1 ||
            GameDataManager.instance.randomBuffState[GameDataManager.instance.blueSunId - 1] == 1 ||
            GameDataManager.instance.randomBuffState[GameDataManager.instance.purpleSunId - 1] == 1
        ) {
            ratio += GameDataManager.instance.sunProp[(<RoleData>this.actorData).type - 1][0];
        }
        attackValue = attackValue * ratio;
        weapon.init(this.gameObject.name == 'Player3', this.type, attackValue, modelName, this.roadIndex, (<RoleData>this.actorData).attackDistance, this.targetEnemyArray, this.targetTowerArray, this.targetFort);
    }

    runAttackDelay() {
        if (this.attackDelayRecord > 0) {
            this.attackDelayRecord -= Laya.timer.delta;
            if (this.attackDelayRecord < 0) {
                this.attackDelayRecord = 0;
            }
        }
    }

    runRefreshExtraTime() {
        if (this.refreshExtraTime > 0) {
            this.refreshExtraTime -= Laya.timer.delta;
            if (this.refreshExtraTime <= 0) {
                this.refreshExtraTime = 0;
                this.updateRoleProp();
                this.bloodProgress.fullValue = this.dataClone.hp;
                this.bloodProgress.progress = this.actorData.hp / this.dataClone.hp;
            }
        }
    }

    onAnimationChange(name: string) {
        if (name == 'Attack1' || name == 'Attack2') {
            this.playMissilePartical(true, name);
            if (this.gameObject.name == 'Player2') {
                Laya.timer.once(300, this, () => {
                    ResManager.playSound('AttackClaw.mp3');
                })
                this.playMissilePartical(false, name);
                this.createWeapon();
            }
        } else {
            this.playMissilePartical(false, name);
        }
        for (let i = 0; i < this.animators.length; i++) {
            const animator = this.animators[i];
            if (animator) animator.play(name, 0, 0);
        }
    }

    onHurt(value: number) {
        if (this.isDeath || GameDataManager.instance.isGameOver || (GameDataManager.instance.isGamePause)) return;
        if (this.bloodProgress) this.bloodProgress.visible = true;
        this.actorData.hp -= value;
        if (this.actorData.hp <= 0) {
            this.actorData.hp == 0;
            this.isDeath = true;
            this.bloodProgress.visible = false;
            this.changeActorState(ActorState.death, '');
            GEvent.event(EventTypeEnum.RoleDeath, [this.type, this.gameObject.transform.position.clone()]);
        }
        this.bloodProgress.progress = this.actorData.hp / this.dataClone.hp;
    }

    updateBloodPositon() {
        if (this.bloodProgress && this.camera) {
            let rolePos = this.gameObject.transform.position.clone();
            rolePos.y += 3;
            //转换坐标
            this.cameraOut = LayaTool.projectPos(this.camera, rolePos);
            //赋值给2D
            this.bloodProgress.pos((this.cameraOut.x - this.bloodProgress.width * this.bloodProgress.scaleX / 2) / Laya.stage.clientScaleX, (this.cameraOut.y - this.bloodProgress.height * this.bloodProgress.scaleY / 2) / Laya.stage.clientScaleY);
        }
    }

    onBaseExplode(type: number) {
        if (!this.isDeath) {
            this.changeActorState(ActorState.stand, '');
        }
    }

    /**
  * 
  * @param parent 
  * @param url 
  */
    getChildByUrl(parent: Laya.Node, url: string): Laya.Node {
        let node: Laya.Node = parent;
        let names = url.split('/')
        for (let i = 0; i < names.length; i++) {
            if (node == null) {
                break
            }
            node = node.getChildByName(names[i])
        }
        return node
    }

    addExplode(explodeName: string) {
        let explode = ResManager.getRes<any>(explodeName).clone();
        let clearNode = Laya.stage.getChildByName('effect').getChildByName('clearNode');
        clearNode.addChild(explode);
        let explodePos = this.gameObject.transform.position.clone();
        if (this.gameObject.name == 'Player3') {
            explodePos.y = 4.2
        } else if (this.gameObject.name == 'Player2') {
            explodePos.y = 1
        } else if (this.gameObject.name == 'Player1') {
            explodePos.y = 1
        }
        explode.transform.position = explodePos;
        Laya.timer.once(3000, this, () => {
            explode.destroy();
        })
    }

    useRune(runeId: number, bool: boolean) {
        if (this.isDeath) return;
        let buffEffectName = `RuneBuff${runeId}`;
        this.openBuffEffect(buffEffectName, bool);
        if (runeId == 1) {
            this.doubleAttack = bool;
        } else if (runeId == 2) {
            this.doubleMove = bool;
        } else if (runeId == 3) {
            this.isRecoverying = bool;
        }
    }

    useMagicBuff(magicType: number, bool: boolean) {
        if (magicType == 4) {
            this.useEncourageMagic = bool;
            this.openBuffEffect("Skill6Buff", bool);
        } else if (magicType == 6) {
            this.useCurseMagic = bool;
        }
    }

    /**
     * 使用buff因子系统
     * @param buffId 
     * @param bool 
     * @param strongData 
     */
    useBuff(buffId: number, bool: boolean) {
        if (this.isDeath) return;
        if (buffId == GameDataManager.instance.weakId) {
            if (bool) this.onHurt(this.actorData.hp / 2);
        } else if (buffId == GameDataManager.instance.thunderId) {
            if (bool) {
                this.onHurt(GameDataManager.instance.thunderDamage);
            }
        }
    }

    recoveryBlood() {
        if (this.isRecoverying) {
            this.actorData.hp += (GameDataManager.instance.recoverySec * Laya.timer.delta / 1000);
            this.actorData.hp = this.actorData.hp < this.dataClone.hp ? this.actorData.hp : this.dataClone.hp;
            this.bloodProgress.progress = this.actorData.hp / this.dataClone.hp;
        }
    }

    lostBlood() {
        if (GameDataManager.instance.randomBuffState[GameDataManager.instance.acidId - 1]) {
            this.onHurt(GameDataManager.instance.acidDamageSec * Laya.timer.delta / 1000);
        }
    }

    getAnimators() {
        this.animators = [];
        for (let i = 0; i < this.gameObject.numChildren; i++) {
            const child = this.gameObject.getChildAt(i);
            let animator = child.getComponent(Laya.Animator);
            if (animator) this.animators.push(animator);
        }
    }

    openBuffEffect(buffName: string, bool: boolean) {
        let runeBuff = this.gameObject.getChildByName(buffName) as Laya.ShuriKenParticle3D;
        if (!runeBuff) {
            runeBuff = ResManager.getRes<any>(buffName + ".lh").clone() as Laya.ShuriKenParticle3D;
            if (this.gameObject.name == 'Player1') {
                runeBuff.transform.position = new Laya.Vector3(0, 3.5, 0);
            } else if (this.gameObject.name == 'Player2') {
                runeBuff.transform.position = new Laya.Vector3(0, 2.5, 0);
            } else if (this.gameObject.name == 'Player3') {
                runeBuff.transform.position = new Laya.Vector3(0, 6, 0);
            }
            this.gameObject.addChild(runeBuff);
        }
        if (runeBuff) {
            if (bool) {
                runeBuff.particleSystem.play();
            } else {
                runeBuff.particleSystem.stop();
            }
        }
    }
    updateRoleProp() {
        let curHp = this.actorData.hp;
        let allId1 = 11001;
        let allId2 = 10001;
        let singleId1 = 31001;
        let singleId2 = 30001;
        if ((<RoleData>this.actorData).type == 2) {
            singleId1 = 21001;
            singleId2 = 20001;
        } else if ((<RoleData>this.actorData).type == 3) {
            singleId1 = 41001;
            singleId2 = 40001;
        }
        let propValue = [0, 0, 0, 0];
        for (let i = 0; i < 4; i++) {
            propValue[i] += this.roleOriginalValue[i];
            if (i != 3) {
                propValue[i] += GameDataManager.instance.getPropInfo(allId1 + i).value;
                propValue[i] += GameDataManager.instance.getPropInfo(singleId1 + i).value;
                if (this.refreshExtraTime > 0) {
                    propValue[i] += GameDataManager.instance.getPropInfo(allId2 + i).value;
                    propValue[i] += GameDataManager.instance.getPropInfo(singleId2 + i).value;
                } else {
                    if (i == 1) {
                        curHp -= GameDataManager.instance.getPropInfo(allId2 + i).value;
                        curHp -= GameDataManager.instance.getPropInfo(allId2 + i).value;
                        curHp = curHp > 0 ? curHp : 0;
                    }
                }
            } else {
                let ratio = 0;
                ratio += GameDataManager.instance.getPropInfo(allId1 + i).value;
                ratio += GameDataManager.instance.getPropInfo(singleId1 + i).value;
                if (this.refreshExtraTime > 0) {
                    ratio += GameDataManager.instance.getPropInfo(allId2 + i).value;
                    ratio += GameDataManager.instance.getPropInfo(singleId2 + i).value;
                }
                let per = 1 - ratio;
                per = per > 0 ? per : 0;
                propValue[i] *= per;
            }
        }
        (<RoleData>this.dataClone).attack = propValue[0];
        (<RoleData>this.actorData).attack = propValue[0];
        (<RoleData>this.dataClone).hp = propValue[1];
        if (this.refreshExtraTime > 0) {
            (<RoleData>this.actorData).hp = propValue[1];
        } else {
            (<RoleData>this.actorData).hp = curHp;
        }
        (<RoleData>this.dataClone).moveSpeed = propValue[2];
        (<RoleData>this.actorData).moveSpeed = propValue[2];
        (<RoleData>this.dataClone).attackDelay = propValue[3];
        (<RoleData>this.actorData).attackDelay = propValue[3];
    }
}