import GEvent from "../../GameFrame/event/GEvent";
import { ui } from "../../ui/layaMaxUI";
import GameDataManager from "../data/GameDataManager";
import { EventTypeEnum } from "../GEnum";

export default class GiftCellRuntime extends ui.giftCellUI {
    onAwake() {
        this.on(Laya.Event.CLICK, this, this.onClick);
    }
    private _curLevel: number = 0;
    public set dataSource(data: any) {
        if (!data) return;
        this._dataSource = data;
        let propInfo = GameDataManager.instance.getPropInfo(data.attType);
        if (propInfo.propArr == GameDataManager.instance.commonGiftProp) {
            this.giftImage.skin = `gift/Ui_TianFuCommon${propInfo.y * 5 + propInfo.x}.png`;
            this.timeLogo.visible = false;
        } else {
            this.giftImage.skin = `gift/Ui_TianFuBattle${propInfo.y + propInfo.x * 4}.png`;
            if (propInfo.propArr == GameDataManager.instance.fightGiftProp) {
                this.timeLogo.visible = false;
            } else {
                this.timeLogo.visible = true;
            }
        }
    }
    public setState(state: number, level: number, isNew: boolean, unlock: boolean, isSelect: boolean) {
        if (state == 0) {
            this.bg.disabled = true;
        } else if (state == 1) {
            this.bg.disabled = false;
        }
        if (level > 0) {
            this.levelBg.visible = true;
            this.giftLevel.text = level + "";
            this._curLevel = level;
        } else {
            this.levelBg.visible = false;
        }
        this.newLogo.visible = isNew;
        this.lockImage.visible = !unlock;
        this.selectLight.visible = isSelect;
    }
    public get isFullLevel() {
        return this._curLevel == this._dataSource.levels;
    }
    onClick() {
        GEvent.event(EventTypeEnum.GiftCellClick, this._dataSource);
    }
}