import ResManager from "../../gframe-work/manager/ResManager"
import GEvent from "../event/GEvent"


/**
 *  本地存储数据类型
 */
enum DataType {
    Number,
    String,
    Json,
}

/**
 * 本地存储的数据
 */
export class LocalStorageData {
    private _type: DataType = DataType.Number
    private _value: string
    private _name: string
    /**
     * 
     * @param name 数据的名称
     * @param type 数据的类型
     * @param defaultVal 数据的默认值
     */
    constructor(name: string, defaultVal: any) {
        this._name = name
        if (typeof defaultVal == 'number') {
            this._type = DataType.Number
        } else if (typeof defaultVal == 'string') {
            this._type = DataType.String
        } else {
            this._type = DataType.Json
        }
        let storageVal = Laya.LocalStorage.getItem(this._name)
        if (storageVal) {
            this._value = storageVal
        } else {
            if (this._type == DataType.Json) {
                this._value = JSON.stringify(defaultVal)
            } else {
                this._value = defaultVal.toString()
            }
            Laya.LocalStorage.setItem(this._name, this._value)
        }
    }

    public get value(): any {
        if (this._type == DataType.Number) {
            return Number(this._value)
        } else if (this._type == DataType.String) {
            return this._value
        } else if (this._type == DataType.Json) {
            return JSON.parse(this._value)
        }
    }

    public get valueStr(): string {
        return this._value
    }


    public set value(v: any) {
        let str = ''
        if (this._type == DataType.Json) {
            str = JSON.stringify(v)
        } else {
            str = v.toString()
        }
        if (this._value == str) return
        this._value = str;
        Laya.LocalStorage.setItem(this._name, this._value)
        GEvent.event('DataChange', this._name);
    }

}

export default class DataManager {
    private static _instance: DataManager = null
    /**
     * 初始化数据管理中心
     */
    public static init() {
        if (DataManager._instance == null) {
            DataManager._instance = new DataManager
        }
    }

    private constructor() {
        DataManager._dataMap = new Map
    }

    /**
     * 数据map
     */
    private static _dataMap: Map<string, any> = null

    static get<T>(name: string): T {
        return DataManager._dataMap.get(name) as T
    }

    /**
     * 设置本地存储的数据
     * @param name 数据的名字
     * @param val 数据的值
     */
    static setLocal(name: string, val: any) {
        let local = DataManager._dataMap.get(name) as LocalStorageData
        if (!local) {
            DataManager._dataMap.set(name, new LocalStorageData(name, val))
        } else {
            local.value = val;
        }
    }

    /**
     * 设置json数据
     * @param name 数据的名字
     * @param val json的地址
     */
    static setJson(name: string, jsonUrl: string) {
        let json = DataManager._dataMap.get(name)
        if (!json) {
            DataManager._dataMap.set(name, ResManager.getRes<any>(jsonUrl))
        }
    }
}