import UserDataManager from "../../gframe-work/manager/UserDataManager";
import { ui } from "../../ui/layaMaxUI";
import CardManager from "./CardManager";

export default class HandBookRuntime extends ui.handBookUI {
    public static selectTabIndex: number = 0;
    public static colorFilter: Laya.ColorFilter = null
    onAwake() {
        var blackMat: Array<number> =
            [
                0, 0, 0, 0, 0, //R
                0, 0, 0, 0, 0, //G
                0, 0, 0, 0, 0, //B
                0, 0, 0, 1, 0, //A
            ];
        HandBookRuntime.colorFilter = new Laya.ColorFilter(blackMat);
        this.cardList.vScrollBarSkin = null;
        this.updateTab(0);
        this.updateList(0);
    }
    onOpened() {
        this.tabList.mouseHandler = Laya.Handler.create(this, this.onMouseHandler, null, false);
    }
    onDestroy() {
        if (HandBookRuntime.colorFilter) HandBookRuntime.colorFilter = null;
    }
    onMouseHandler(e: Laya.Event, index: number) {
        if (e.type == Laya.Event.CLICK && HandBookRuntime.selectTabIndex != index) {
            this.updateTab(index);
            this.updateList(index);
            HandBookRuntime.selectTabIndex = index;
        }
    }
    updateTab(index: number) {
        let tabDataSource = [];
        for (let i = 0; i < 4; i++) {
            let baseName = "handbook/Btn-JiChu";
            if (i == 1) {
                baseName = "handbook/Btn-ShuangZu";
            } else if (i == 2) {
                baseName = "handbook/Btn-SiZu";
            } else if (i == 2) {
                baseName = "handbook/Btn-FeiLong";
            }
            let lightSkin = baseName + "3" + ".png";
            let frameSkin = baseName + "2" + ".png";
            let lightVisible = false;
            if (i == index) {
                frameSkin = baseName + "1" + ".png";
                lightVisible = true;
            }
            tabDataSource.push({ frame: { skin: frameSkin }, light: { skin: lightSkin, visible: lightVisible } })
        }
        this.tabList.dataSource = tabDataSource;
    }
    updateList(index: number) {
        let listDataSource: any[] = [];
        if (index == 0) {
            let nameArr = [...CardManager.instance.standNameArr, ...CardManager.instance.climbNameArr, ...CardManager.instance.flyNameArr];
            for (let i = 0; i < nameArr.length; i++) {
                let data = { type: 0, skin: [nameArr[i], nameArr[i], nameArr[i]].join("-"), isNew: false, isUnlock: false }
                let idStr = CardManager.instance.skinToId(data.skin);
                data.type = idStr.type;
                if (UserDataManager.instance.handBookRecord[idStr.id] == -1) {
                    data.isNew = false;
                    data.isUnlock = false;
                } else {
                    data.isUnlock = true;
                    if (UserDataManager.instance.newUnlockRecord[idStr.id] == -1) {
                        data.isNew = false;
                    } else {
                        data.isNew = true;
                    }
                }
                listDataSource.push(data);
            }
        } else if (index == 1 || index == 2 || index == 3) {
            let nameArr = CardManager.instance.standNameArr;
            if (index == 2) {
                nameArr = CardManager.instance.climbNameArr;
            } else if (index == 3) {
                nameArr = CardManager.instance.flyNameArr;
            }
            for (let i = 0; i < nameArr.length; i++) {
                for (let j = 0; j < nameArr.length; j++) {
                    for (let k = 0; k < nameArr.length; k++) {
                        let data = { skin: [nameArr[i], nameArr[j], nameArr[k]].join("-"), isNew: false, isUnlock: false }
                        let idStr = CardManager.instance.skinToId(data.skin);
                        if (UserDataManager.instance.handBookRecord[idStr.id] == -1) {
                            data.isNew = false;
                            data.isUnlock = false;
                        } else {
                            data.isUnlock = true;
                            if (UserDataManager.instance.newUnlockRecord[idStr.id] == -1) {
                                data.isNew = false;
                            } else {
                                data.isNew = true;
                            }
                        }
                        listDataSource.push(data);
                    }
                }
            }
        }
        this.cardList.dataSource = listDataSource;
    }
}