import UserDataManager from "../../gframe-work/manager/UserDataManager";
import { ui } from "../../ui/layaMaxUI";
import { SceneName } from "../GEnum";
import LotteryResultRuntime from "./LotteryResultRuntime";

export default class TaskCellRuntime extends ui.taskCellUI {
    private record: { index: number, now: number, goal: number, receive: number } = null;
    private reward: number[] = null;
    private goldLen: number = 0
    onAwake() {
        this.goBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
    }
    public set dataSource(data: any) {
        if (!data) return;
        this._dataSource = data;
        this.reward = JSON.parse(data.reward);
        this.record = this.getRecordById(data.id);
        this.desc.text = data.descrption.replace("%", this.record.goal);
        if (this.reward[0] == 2) {
            this.rewardIcon.skin = "main/Ui_Gold.png";
        } else {
            this.rewardIcon.skin = "main/Ui_Diamonds.png";
        }
        if (data.activeValue > 0) {
            this.activeLabel.visible = true;
            this.activeLabel.text = data.activeValue + "";
        } else {
            this.activeLabel.visible = false;
        }
        this.rewardNum.text = this.reward[1].toString();
        if (this.record.goal <= this.record.now) {
            if (this.record.receive) {
                this.goBtn.visible = false;
                this.finishIcon.visible = true;
                this.bg.skin = "task/Ui_Kuang3.png";
            } else {
                this.showGoBtn(true);
            }
        } else {
            this.showGoBtn(false);
        }
    }
    getRecordById(id: number) {
        let recordIndex = id % 1000 - 1;
        let gold = 0, now = 0, receive = 0;
        if (id > 2000) {
            now = UserDataManager.instance.dayTaskRecord[recordIndex];
            gold = this._dataSource.param;
            receive = UserDataManager.instance.dayTaskReceive[recordIndex];
        } else {
            let stage = UserDataManager.instance.achieveTaskStage[recordIndex];
            let goldArr: number[] = JSON.parse(this._dataSource.param);
            this.goldLen = goldArr.length;
            now = UserDataManager.instance.achieveTaskRecord[recordIndex];
            gold = goldArr[stage];
            receive = UserDataManager.instance.achieveTaskReceive[recordIndex];
        }
        return { index: recordIndex, now: now, goal: gold, receive: receive };
    }
    showGoBtn(finish: boolean) {
        if (finish) {
            this.goBtn.skin = "task/Btn_WanCheng.png";
            this.bg.skin = "task/Ui_Kuang2.png";
        } else {
            this.goBtn.skin = "task/Btn_QianWang.png";
            this.bg.skin = "task/Ui_Kuang3.png";
        }
        this.goBtn.visible = true;
        this.finishIcon.visible = false;
        this.progressLabel.text = `${this.record.now}/${this.record.goal}`;
        let progress = this.record.now / this.record.goal;
        progress = progress < 1 ? progress : 1;
        this.progressBar.width = progress * 119;
    }
    onBtnClick() {
        if (this.goBtn.skin == "task/Btn_WanCheng.png") {
            let data = { desc: '+688', value: 688, type: 'gold', isDirect: true, from: "task" };
            if (this.reward[0] == 2) {
                data.type = 'gold';
            } else {
                data.type = 'diamond';
            }
            data.value = this.reward[1];
            data.desc = "+" + this.reward[1];
            Laya.Scene.load(SceneName.lotteryResult, Laya.Handler.create(this, (lotteryResult: LotteryResultRuntime) => {
                lotteryResult.open(false);
                lotteryResult.setData(data);
            }))
        } else {
            let shopId = [1004, 1005, 1006, 1007, 1010, 2004, 2005, 2006, 2007, 2008, 2010];
            let selectId = [1011];
            let pvpId = [1003, 2003];
            let fightId = [1001, 1002, 1013, 1014, 2001, 2002];
            let composeId = [1008, 1009, 1012];
            let giftId = [1015, 1016, 1017, 1018, 1019, 2009];
            if (shopId.indexOf(this._dataSource.id) != -1) {

            } else if (selectId.indexOf(this._dataSource.id) != -1) {

            } else if (pvpId.indexOf(this._dataSource.id) != -1) {

            } else if (fightId.indexOf(this._dataSource.id) != -1) {

            } else if (composeId.indexOf(this._dataSource.id) != -1) {

            } else if (giftId.indexOf(this._dataSource.id) != -1) {

            }
            Laya.Scene.close(SceneName.task);
        }
    }

}