import { ui } from "../../ui/layaMaxUI";
import HandBookRuntime from "./HandBookRuntime";

export default class HandBookCellRuntime extends ui.handBookCellUI {
    public set dataSource(data: any) {
        this._dataSource = data;
        if (data) {
            let nameArray = data.skin.split('-');
            if (data.type == 1) {
                this.foot1.visible = true;
                this.head.skin = `part/${nameArray[0]}1.png`
                this.foot1.skin = `part/${nameArray[2]}2.png`
                this.body.skin = `part/${nameArray[1]}3.png`
                this.foot.skin = `part/${nameArray[2]}4.png`

            } else {
                this.foot1.visible = false;
                this.head.skin = `part/${nameArray[0]}1.png`
                this.body.skin = `part/${nameArray[1]}2.png`
                this.foot.skin = `part/${nameArray[2]}3.png`
            }
            if (data.isNew) {
                this.newFlag.visible = true;
            } else {
                this.newFlag.visible = false;
            }
            if (data.isUnlock) {
                if (this.head.visible) this.head.filters = null;
                if (this.body.visible) this.body.filters = null;
                if (this.foot.visible) this.foot.filters = null;
                if (this.foot1.visible) this.foot1.filters = null;
            } else {
                if (this.head.visible) this.head.filters = [HandBookRuntime.colorFilter];
                if (this.body.visible) this.body.filters = [HandBookRuntime.colorFilter];
                if (this.foot.visible) this.foot.filters = [HandBookRuntime.colorFilter];
                if (this.foot1.visible) this.foot1.filters = [HandBookRuntime.colorFilter];
            }
        }
    }
    onOpened() {
        this.on(Laya.Event.CLICK, this, this.onClick);
    }
    onClick() {
        if (!this._dataSource.isClick) return;
        if (this._dataSource.isNew) {
            
        } else {

        }
    }
}