import GEvent from "../../GameFrame/event/GEvent";
import EventManager from "../../gframe-work/manager/EventManager";
import EventType from "../../gframe-work/manager/EventType";
import ResManager from "../../gframe-work/manager/ResManager";
import LayaTool from "../../gframe-work/tool/LayaTool";
import { ui } from "../../ui/layaMaxUI";
import { EventTypeEnum } from "../GEnum";
import CardManager from "./CardManager";
import GuideManager from "./GuideManager";

export default class ComposeShowRuntime extends ui.composeShowUI {

    modelScene: Laya.Scene3D = null;
    modelCamera: Laya.Camera = null;
    player1Show: Laya.Sprite3D = null;
    player2Show: Laya.Sprite3D = null;
    player3Show: Laya.Sprite3D = null;
    shine: Laya.ShuriKenParticle3D = null;
    isOpen: boolean = false;

    animators: Laya.Animator[] = [];

    isFinish: boolean = false;

    onAwake() {
        this.width = Laya.stage.width;
        this.height = Laya.stage.height;
        this.modelScene = this.scene3dBox.addChild(ResManager.getRes<any>('Show.ls')) as Laya.Scene3D;
        this.modelCamera = this.modelScene.getChildByName('ShowCamera') as Laya.Camera;
        this.modelCamera.clearFlag = Laya.CameraClearFlags.DepthOnly;
        this.modelCamera.enableHDR = false;
        this.shine = this.modelScene.getChildByName('Shine1') as Laya.ShuriKenParticle3D;
        this.player1Show = this.modelScene.getChildByName('Player1Show') as Laya.Sprite3D;
        this.player2Show = this.modelScene.getChildByName('Player2Show') as Laya.Sprite3D;
        this.player3Show = this.modelScene.getChildByName('Player3Show') as Laya.Sprite3D;
        this.modelScene.visible = true;
        this.shine.active = false;
        Laya.timer.frameOnce(45, this, () => {
            this.shine.active = true;
        })
    }

    onDestroy() {
        Laya.timer.clearAll(this);
        EventManager.off(EventType.NEWGUIDETASK, this, this.onNewGuideTaskEvent);
    }

    onOpened() {
        this.useCardBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.scene3dBox.on(Laya.Event.CLICK, this, this.onBtnClick);
        EventManager.on(EventType.NEWGUIDETASK, this, this.onNewGuideTaskEvent);
    }

    onNewGuideTaskEvent(task: string) {
        if (task == "上阵卡牌") {
            GuideManager.Instance.showGuide("上阵卡牌", this.useCardBtn);
        }
    }

    clearModelScene() {
        if (this.modelScene) {
            this.scene3dBox.removeChild(this.modelScene);
            this.player1Show.destroyChildren();
            this.player2Show.destroyChildren();
            this.player3Show.destroyChildren();
        }
    }

    onBtnClick(e: Laya.Event) {
        if (!this.isFinish) return;
        this.clearModelScene();
        if (e.target == this.useCardBtn) {
            this.close();
            GuideManager.Instance.finishTask("上阵卡牌", 600);
            GEvent.event(EventTypeEnum.JumpPage, 3);
        } else if (e.target == this.scene3dBox) {
            this.close();
        }
    }

    setModel(data: any, beforeData: any, skeleton: Laya.Skeleton) {
        this.spineBox.addChild(skeleton);
        let fightLevel = CardManager.instance.getFightLevelByEXP(data.exp);
        let nowData = CardManager.instance.getFightData(data.datatype, fightLevel);
        Laya.timer.frameOnce(60, this, () => {
            this.animators = [];
            let unitArray = data.skin.split('-');
            let shellName = '';
            if (data.type == 2) {
                shellName = 'Player1';
            } else if (data.type == 1) {
                shellName = 'Player2';
            } else if (data.type == 3) {
                shellName = 'Player3';
            }
            this.hpLabel.text = '0';
            this.attackLabel.text = '0';
            this.speedLabel.text = '0';
            this.delayLabel.text = '0';
            ResManager.create([`${shellName}.lh`, `${shellName}_${unitArray[0]}1.lh`, `${shellName}_${unitArray[1]}3.lh`, `${shellName}_${unitArray[2]}2.lh`]).then(() => {
                let rolePrefab0 = ResManager.getRes<any>(`${shellName}.lh`) as Laya.Sprite3D;
                let rolePrefab1 = ResManager.getRes<any>(`${shellName}_${unitArray[0]}1.lh`) as Laya.Sprite3D;
                let rolePrefab2 = ResManager.getRes<any>(`${shellName}_${unitArray[1]}3.lh`) as Laya.Sprite3D;
                let rolePrefab3 = ResManager.getRes<any>(`${shellName}_${unitArray[2]}2.lh`) as Laya.Sprite3D;
                if (rolePrefab0 == null || rolePrefab1 == null || rolePrefab2 == null || rolePrefab3 == null) {
                    return;
                }
                let cloneRole = Laya.Sprite3D.instantiate(rolePrefab0)
                cloneRole.addChild(Laya.Sprite3D.instantiate(rolePrefab1));
                cloneRole.addChild(Laya.Sprite3D.instantiate(rolePrefab2));
                cloneRole.addChild(Laya.Sprite3D.instantiate(rolePrefab3));

                if (cloneRole.name == 'Player3') {
                    cloneRole.transform.position = new Laya.Vector3(0, -3, 0);
                } else {
                    cloneRole.transform.position = new Laya.Vector3(0, 0, 0);
                }

                if (shellName == 'Player1') {
                    this.player1Show.addChild(cloneRole);
                } else if (shellName == 'Player2') {
                    this.player2Show.addChild(cloneRole);
                } else if (shellName == 'Player3') {
                    this.player3Show.addChild(cloneRole);
                }

                let vec4 = LayaTool.projectPos(this.modelCamera, new Laya.Vector3);
                if (vec4) {
                    this.propBg.pos(vec4.x / Laya.stage.clientScaleX - this.propBg.width / 2, vec4.y / Laya.stage.clientScaleY - (<any>this.propBg.parent).y + 25);
                    Laya.timer.frameOnce(1, this, () => {
                        this.propBg.visible = true;
                    });
                }

                for (let i = 0; i < cloneRole.numChildren; i++) {
                    let child = cloneRole.getChildAt(i);
                    let animator = child.getComponent(Laya.Animator);
                    if (animator) {
                        this.animators.push(animator);
                    }
                }

                this.onAnimationChange('Bron');
                let totalFrame = 0;
                let hpAdd = Math.ceil(nowData.hp / 60);
                let attackAdd = Math.ceil(nowData.atk / 60);
                let speedAdd = Math.ceil(nowData.speed / 60);
                let delayAdd = Math.ceil(nowData.atkgap / 60);
                // let hpApprove = MathTool.fixNumber(data.hp - beforeData.hp, 1);
                // let attackApprove = MathTool.fixNumber(data.attack - beforeData.attack, 1);
                // let speedApprove = MathTool.fixNumber(data.moveSpeed - beforeData.moveSpeed, 1);
                // let delayApprove = MathTool.fixNumber(data.attackDelay - beforeData.attackDelay, 1);
                let hpApprove = 0;
                let attackApprove = 0;
                let speedApprove = 0;
                let delayApprove = 0;
                Laya.timer.frameLoop(1, this, () => {
                    if (totalFrame > 70) {
                        return;
                    }
                    totalFrame += 1;
                    let curHp = Number(this.hpLabel.text);
                    let curAttack = Number(this.attackLabel.text);
                    let curSpeed = Number(this.speedLabel.text);
                    let curDelay = Number(this.delayLabel.text);
                    if (curHp < nowData.hp) {
                        this.hpLabel.text = (curHp + hpAdd).toString();
                    } else {
                        this.hpLabel.text = nowData.hp.toString();
                        if (hpApprove > 0) {
                            this.hpAddLabel.text = "+" + hpApprove;
                            this.hpAddLabel.visible = true;
                            this.hpAddLabel.x = this.hpLabel.width + 10;
                        }
                    }

                    if (curAttack < nowData.atk) {
                        this.attackLabel.text = (curAttack + attackAdd).toString();
                    } else {
                        this.attackLabel.text = nowData.atk.toString();
                        if (attackApprove > 0) {
                            this.attackAddLabel.text = "+" + attackApprove;
                            this.attackAddLabel.visible = true;
                            this.attackAddLabel.x = this.attackLabel.width + 10;
                        }
                    }

                    if (curSpeed < nowData.speed) {
                        this.speedLabel.text = (curSpeed + speedAdd).toString();
                    } else {
                        this.speedLabel.text = nowData.speed.toString();
                        if (speedApprove > 0) {
                            this.speedAddLabel.text = "+" + speedApprove;
                            this.speedAddLabel.visible = true;
                            this.speedAddLabel.x = this.speedLabel.width + 10;
                        }
                    }

                    if (curDelay < nowData.atkgap) {
                        this.delayLabel.text = (curDelay + delayAdd).toString();
                    } else {
                        this.delayLabel.text = nowData.atkgap.toString();
                        if (delayApprove > 0) {
                            this.delayAddLabel.text = "+" + delayApprove;
                            this.delayAddLabel.visible = true;
                            this.delayAddLabel.x = this.delayLabel.width + 10;
                        }
                    }
                })
                Laya.timer.once(1500, this, () => {
                    this.onAnimationChange('Stand');
                    this.isFinish = true;
                    GuideManager.Instance.showGuide("合成成功提示语", null);
                })
            })
        })
    }

    onAnimationChange(name: string) {
        for (let i = 0; i < this.animators.length; i++) {
            const animator = this.animators[i];
            if (animator) animator.crossFade(name, 0.2, 0, 0);
        }
    }

    loadSpine() {

    }
}