import { ui } from "../../ui/layaMaxUI";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import GEvent from "../../GameFrame/event/GEvent";
import { EventTypeEnum, SceneName } from "../GEnum";
import { CardData } from "../data/CardData";
import PackageRuntime from "./PackageRuntime";
import { AllPlatformAdMgr } from "../../GameFrame/advert/AllPlatformAdMgr";
import MathTool from "../../gframe-work/tool/MathTool";
import ResManager from "../../gframe-work/manager/ResManager";
import UserDataManager from "../../gframe-work/manager/UserDataManager";
import CardManager from "./CardManager";


export default class ShopRuntime extends ui.shopUI {
    diamondLocal: LocalStorageData = null;
    moneyLocal: LocalStorageData = null;
    baseLevelLocal: LocalStorageData = null;
    mineLevelLocal: LocalStorageData = null;
    shopLocal: LocalStorageData = null;
    enemyPoolLocal: LocalStorageData = null;
    curLevelLocal: LocalStorageData = null;
    shopIdLocal: LocalStorageData = null;
    isSelect: boolean = false;
    refreshTime: number = 1800;
    cardPackTimeLeft: number[] = [0, 0, 0, 0, 0, 0];
    cardPackCd: number[] = [0, 0, 0, 0, 0, 0];
    onAwake() {
        this.slidePanel.vScrollBarSkin = '';
        this.curLevelLocal = DataManager.get<LocalStorageData>('curLevel');
        this.enemyPoolLocal = DataManager.get<LocalStorageData>('enemyPool');
        this.shopLocal = DataManager.get<LocalStorageData>('shop');
        this.shopIdLocal = DataManager.get<LocalStorageData>('shopId');
        this.diamondLocal = DataManager.get<LocalStorageData>('diamond');
        this.moneyLocal = DataManager.get<LocalStorageData>('gold');
        this.baseLevelLocal = DataManager.get<LocalStorageData>('baseLevel');
        this.mineLevelLocal = DataManager.get<LocalStorageData>('mineLevel');
        this.moneyList.mouseHandler = Laya.Handler.create(this, this.onMouseHandler, null, false);
        this.onRefreshShop();
        this.refreshShopTime();
        GEvent.on(EventTypeEnum.RefreshShop, this, this.onRefreshShop);
        for (let i = 0; i < 6; i++) {
            let pack = this.packBox.getChildByName("pack" + (i + 1));
            pack.on(Laya.Event.CLICK, this, this.onCardPackClick);
        }
        this.refreshCardPackUi();
        Laya.timer.loop(1000, this, this.onLoop);
    }
    onCardPackClick(e: Laya.Event) {
        let packId = Number(e.target.name.replace("pack", ""));
        let cardPackConfig = ResManager.getRes<any>("gacha.json");
        let cardPackData = cardPackConfig[packId - 1];
        let leftTime = this.cardPackTimeLeft[packId - 1];
        let openTimeRecord = UserDataManager.instance.cardPackOpenTs;
        if (leftTime == 0) {
            if (cardPackData.price == 0) {
                AllPlatformAdMgr.Instance.showVideoAd(() => {
                    GEvent.event('BreakPoint', ['购买卡包', 1]);
                    this.openPackage([packId]);
                    openTimeRecord[packId - 1] = Date.now();
                    UserDataManager.instance.cardPackOpenTs = openTimeRecord;
                    this.refreshCardPackUi();
                })
            } else {
                let price: number[] = JSON.parse(cardPackData.price);
                if (price[0] == 1) {
                    if (price[1] <= this.moneyLocal.value) {
                        this.moneyLocal.value -= price[1];
                        this.openPackage([packId]);
                        openTimeRecord[packId - 1] = Date.now();
                        UserDataManager.instance.cardPackOpenTs = openTimeRecord;
                        this.refreshCardPackUi();
                    } else {
                        GEvent.event(EventTypeEnum.SendTip, [['金币不足!']]);
                    }
                } else {
                    if (price[1] <= this.diamondLocal.value) {
                        this.diamondLocal.value -= price[1];
                        this.openPackage([packId]);
                        openTimeRecord[packId - 1] = Date.now();
                        UserDataManager.instance.cardPackOpenTs = openTimeRecord;
                        this.refreshCardPackUi();
                    } else {
                        GEvent.event(EventTypeEnum.SendTip, [['钻石不足!']]);
                    }
                }
            }
        } else {

        }
    }
    refreshShopTime() {
        let timeStap = DataManager.get<LocalStorageData>('refreshShopTimeStap');
        let nowTime = Date.now();
        if (timeStap.value == 0 || (nowTime - timeStap.value) >= 1800 * 1000) {
            this.refreshTime = 1800;
            DataManager.get<LocalStorageData>('refreshShopTimeStap').value = Date.now();
            this.refreshShop();
            let shopDataArray = this.shopLocal.value;
            this.houseList.repeatY = Math.ceil(shopDataArray.length / 4);
            this.houseList.array = shopDataArray;
            this.houseList.refresh();
            this.refreshUI();
        } else {
            this.refreshTime = 1800 - Math.floor((nowTime - timeStap.value) / 1000);
        }
        let min = Math.floor(this.refreshTime / 60);
        let sec = this.refreshTime - min * 60;
        this.refreshTip.text = `下次刷新时间${this.format('mm:ss', { min: min, sec: sec })}`;
    }

    onLoop() {
        for (let i = 0; i < this.cardPackTimeLeft.length; i++) {
            let timeLeft = this.cardPackTimeLeft[i];
            if (timeLeft > 0) {
                timeLeft -= 1000;
                if (timeLeft <= 0) {
                    this.cardPackTimeLeft[i] = 0;
                    this.refreshCardPackUi();
                }
            }
        }
        if (this.refreshTime > 0) {
            this.refreshTime--;
            let min = Math.floor(this.refreshTime / 60);
            let sec = this.refreshTime - min * 60;
            this.refreshTip.text = `下次刷新时间${this.format('mm:ss', { min: min, sec: sec })}`;
            if (this.refreshTime <= 0) {
                this.refreshTime = 1800;
                DataManager.get<LocalStorageData>('refreshShopTimeStap').value = Date.now();
                this.refreshShop();
                let shopDataArray = this.shopLocal.value;
                this.houseList.repeatY = Math.ceil(shopDataArray.length / 4);
                this.houseList.array = shopDataArray;
                this.houseList.refresh();
                this.refreshUI();
            }
        }
    }

    onDestroy() {
        Laya.timer.clearAll(this);
        GEvent.off(EventTypeEnum.RefreshShop, this, this.onRefreshShop);
    }

    onMouseHandler(e: Laya.Event, index: number) {
        if (e.type == Laya.Event.CLICK) {
            let data = this.moneyList.array[index];
            let diamondCost = Number(data.diamondNum.text);
            let moneyAdd = 0;
            if (diamondCost == 10) {
                moneyAdd = 1000;
            } else if (diamondCost == 50) {
                moneyAdd = 5000;
            } else if (diamondCost == 200) {
                moneyAdd = 20000;
            }
            if (this.diamondLocal.value >= diamondCost) {
                this.diamondLocal.value -= diamondCost;
                this.moneyLocal.value += moneyAdd;
                GEvent.event(EventTypeEnum.SendTip, [['购买成功!']]);
            } else {
                GEvent.event(EventTypeEnum.SendTip, [['钻石不足!']]);
            }
        }
    }

    onRefreshShop() {
        let shopDataArray = this.shopLocal.value;
        if (shopDataArray.length == 0) {
            DataManager.get<LocalStorageData>('refreshShopTimeStap').value = Date.now();
            this.refreshShop();
            shopDataArray = this.shopLocal.value;
        }
        this.houseList.repeatY = Math.ceil(shopDataArray.length / 4);
        this.houseList.array = shopDataArray;
        this.houseList.refresh();

        let moneyListData = [{ moneyIcon: { skin: 'shop/small.png' }, diamondNum: { text: 10 } }, { moneyIcon: { skin: 'shop/middle.png' }, diamondNum: { text: 50 } }, { moneyIcon: { skin: 'shop/big.png' }, diamondNum: { text: 200 } }]
        this.moneyList.array = moneyListData;
        this.moneyList.refresh();

        this.refresh.on(Laya.Event.CLICK, this, this.onMouseEvent, ['refresh']);
        this.refreshUI();
    }

    onMouseEvent(name: string, e: Laya.Event) {
        if (e.type == Laya.Event.CLICK) {
            if (name == 'refresh') {
                AllPlatformAdMgr.Instance.showVideoAd(() => {
                    this.refreshShop();
                    let shopDataArray = this.shopLocal.value;
                    this.houseList.repeatY = Math.ceil(shopDataArray.length / 4);
                    this.houseList.array = shopDataArray;
                    this.houseList.refresh();
                    this.refreshUI();
                })
            }
        }
    }

    openPackage(packageTypeArr: number[]) {
        let packageDataArr = [];
        let cardPackConfig = ResManager.getRes<any>("gacha.json");

        for (let i = 0; i < packageTypeArr.length; i++) {
            let cardPackData = cardPackConfig[packageTypeArr[i] - 1];
            let dataArr = this.getCardPackData(cardPackData);
            packageDataArr.push(dataArr);
            CardManager.instance.addNewCard(dataArr);
        }
        Laya.Scene.load(SceneName.package, Laya.Handler.create(this, (packageRuntime: PackageRuntime) => {
            packageRuntime.isModal = true;
            packageRuntime.isShowEffect = false;
            packageRuntime.width = Laya.stage.width;
            packageRuntime.height = Laya.stage.height;
            packageRuntime.open(false);
            packageRuntime.showBook(packageTypeArr, packageDataArr);
        }))
    }
    refreshShop() {
        let cardArray = [];
        for (let i = 0; i < 12; i++) {
            cardArray.push(this.getCard());
        }
        this.shopLocal.value = cardArray;
    }
    getCard() {
        let curStage = UserDataManager.instance.curStage;
        let normallizedLevel = (curStage[0] - 1) * 10 + curStage[1];
        let fightLevel = normallizedLevel + 3;
        let enemyPool = this.enemyPoolLocal.value;
        let monsterIndex = MathTool.getRandom(0, enemyPool.length - 1);
        let card = CardManager.instance.getCardData(enemyPool[monsterIndex]);
        card.exp = CardManager.instance.getBaseExpByFightLevel(fightLevel);
        card.id = this.shopIdLocal.value;
        this.shopIdLocal.value++;
        return card;
    }
    refreshUI() {
        let uiArr = [this.title1, this.packBox, this.title2, this.refreshBox, this.houseList, this.title3, this.moneyList]
        let y = 0;
        let gap = 10;
        for (let i = 0; i < uiArr.length; i++) {
            let ui = uiArr[i];
            ui.y = y;
            y = y + ui.height * ui.scaleY + gap;
        }
    }
    onSelect() {
        this.isSelect = true;
        GEvent.event(EventTypeEnum.NextChange, 'hide');
    }
    format(fmt: string, date: { min: number, sec: number }) {
        var o = {
            "m+": date.min,                 //分 
            "s+": date.sec,                 //秒 
        };
        for (var k in o) {
            if (new RegExp("(" + k + ")").test(fmt)) {
                fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
            }
        }
        return fmt;
    }
    getCardPackData(packData: any) {
        let cardNum = packData.totalCard;
        let pool1 = packData.pool1;
        let amount1: number[] = JSON.parse(packData.amount1);
        let pool2 = packData.pool2;
        let amount2: number[] = JSON.parse(packData.amount2);
        let pool3 = packData.pool3;
        let dataArr = [];
        for (let i = 0; i < 3; i++) {
            let poolId = 0;
            let poolCardNum = 0;
            if (i == 0) {
                poolId = pool1;
                poolCardNum = amount1[MathTool.getRandom(0, amount1.length - 1)];
            } else if (i == 1) {
                poolId = pool2;
                poolCardNum = amount2[MathTool.getRandom(0, amount2.length - 1)];
            } else if (i == 2) {
                poolId = pool3;
                poolCardNum = cardNum;
            }
            cardNum -= poolCardNum;
            dataArr = dataArr.concat(this.getCardPoolData(poolCardNum, poolId));
        }
        return dataArr;
    }
    getCardPoolData(cardNum: number, poolId: number) {
        let cardPoolConfig = ResManager.getRes<any>("gachapool.json");
        let cardPoolData = cardPoolConfig[this.getCardPoolIndex(poolId)];
        let poolContent: number[] = [...JSON.parse(cardPoolData.content)];
        let unitLevel = cardPoolData.unitLevel;
        let unitIdArr = [];
        let fightLevelArr = [];
        let ifRepetitive = cardPoolData.ifRepetitive;
        for (let i = 0; i < cardNum; i++) {
            let randomIndex = MathTool.getRandom(0, poolContent.length - 1);
            let unitId = poolContent[randomIndex];
            unitIdArr.push(unitId);
            fightLevelArr.push(unitLevel);
            if (!ifRepetitive) {
                poolContent.splice(randomIndex, 1);
            }
        }
        return CardManager.instance.getCardDataArr(unitIdArr, fightLevelArr);
    }
    getCardPoolIndex(poolId: number) {
        let offset = 0, base = 1000;
        let arr = [4, 4, 4];
        for (let i = 0; i < 3; i++) {
            let newbase = (i + 2) * 1000;
            if (poolId > newbase) {
                base = newbase;
                offset += arr[i];
            }
        }
        return poolId % base + offset - 1;
    }
    refreshCardPackUi() {
        let cardPackConfig = ResManager.getRes<any>("gacha.json");
        let nowTime = Date.now();
        let userCardPackTs = UserDataManager.instance.cardPackOpenTs;
        for (let i = 0; i < cardPackConfig.length; i++) {
            let cardPackData = cardPackConfig[i];
            this.cardPackCd[i] = cardPackData.cooldown;
            let pack = this.packBox.getChildByName("pack" + (i + 1)) as Laya.Image;
            let refreshTimeLabel = pack.getChildByName("refreshTimeLabel") as Laya.Label;
            let moneyNum = pack.getChildByName("moneyNum") as Laya.Label;
            if (cardPackData.price == 0) {
                moneyNum.visible = false;
            } else {
                let price: number[] = JSON.parse(cardPackData.price);
                let moneyImage = moneyNum.getChildByName("moneyImage") as Laya.Image;
                moneyNum.visible = true;
                moneyNum.text = price[1] + "";
                if (price[0] == 1) {
                    moneyImage.skin = "main/Ui_Gold.png";
                } else {
                    moneyImage.skin = "main/Ui_Diamonds.png";
                }
            }
            let timeLeft = cardPackData.cooldown * 60 * 1000 - (nowTime - userCardPackTs[i]);
            timeLeft = timeLeft > 0 ? timeLeft : 0;
            this.cardPackTimeLeft[i] = timeLeft;
            if (cardPackData.cooldown == 0) {
                refreshTimeLabel.visible = false;
                pack.disabled = false;
            } else {
                if (timeLeft > 0) {
                    let hour = Math.floor(timeLeft / 3600000);
                    let minutes = Math.floor(timeLeft % 3600000 / 60000);
                    refreshTimeLabel.text = `${hour}小时${minutes}分`;
                    refreshTimeLabel.visible = true;
                    pack.disabled = true;
                } else {
                    refreshTimeLabel.visible = false;
                    pack.disabled = false;
                }
            }
        }
    }
}