import { ui } from "../../ui/layaMaxUI";
import GameDataManager from "../data/GameDataManager";
import CardManager from "./CardManager";
enum CellState {
    normal,
    cd
}
export default class GameCellRuntime extends ui.gameCellUI {
    original: GameCellRuntime = null;
    originalIndex: number = -1;
    curState: CellState = CellState.normal;
    isTweening: boolean = false;
    private _cardData: any = null;
    private _realStoneNum: number = 0;
    private playRemindAni: boolean = false;
    private _magicType: number = 0;
    _cost: number = 0;
    _increaseCost: number = 0;
    _originalCost: number = 0;
    _cdLeft: number = 0;
    _originalCd: number = 0;
    onAwake() {
        Laya.timer.frameLoop(1, this, this.onFrameLoop);
    }
    onFrameLoop() {
        if (this._cdLeft > 0) {
            this._cdLeft -= Laya.timer.delta / 1000;
            if (this._cdLeft <= 0) {
                this._cdLeft = 0;
            }
        }
    }
    onDestroy() {
        Laya.timer.clear(this, this.onFrameLoop);
    }
    public set dataSource(data: any) {
        if (!data) return;
        this._magicType = data.magicType;
        if (!this._magicType) {
            this.magicImage.visible = false;
            this.bg.visible = true;
            if (data.skin != '') {
                let nameArray = data.skin.split('-');
                if (data.type == 1) {
                    this.foot1.visible = true;
                    this.head.skin = `part/${nameArray[0]}1.png`
                    this.foot1.skin = `part/${nameArray[2]}2.png`
                    this.body.skin = `part/${nameArray[1]}3.png`
                    this.foot.skin = `part/${nameArray[2]}4.png`
                } else {
                    this.foot1.visible = false;
                    this.head.skin = `part/${nameArray[0]}1.png`
                    this.body.skin = `part/${nameArray[1]}2.png`
                    this.foot.skin = `part/${nameArray[2]}3.png`
                }
            }
            let color = CardManager.instance.getColorByEXP(data.exp);
            let iconSkin = '';
            if (color == 'white') {
                iconSkin = 'card/Ui_CardBg0.png';
            } else if (color == 'green') {
                iconSkin = 'card/Ui_CardBg1.png';
            } else if (color == 'blue') {
                iconSkin = 'card/Ui_CardBg2.png';
            } else if (color == 'purple') {
                iconSkin = 'card/Ui_CardBg3.png';
            }
            if (data.cost) {
                this._originalCost = data.cost;
            } else {
                this._originalCost = CardManager.instance.getOriginalCost(data.skin);
            }
            this._increaseCost = 1;
            this.cost = this._originalCost;
            this.bg.skin = iconSkin;
        } else {
            let magicData = GameDataManager.instance.magicData[this._magicType];
            this.magicImage.visible = true;
            this.magicImage.skin = `card/Ui_Magic${this._magicType}.png`
            this.bg.visible = false;
            this._originalCost = magicData.intialEnergy;
            this._increaseCost = magicData.increaseEnergy;
            this._originalCd = magicData.collDown;
            this.cost = this._originalCost;
        }
        this._cardData = data;
    }

    public set realStoneNum(value: number) {
        this._realStoneNum = value;
        let per = this._realStoneNum / this._cost;
        if (per < 1) {
            this.maskFrame.visible = true;
            this.bg.disabled = true;
            this.magicImage.disabled = true;
            this.cardMask.visible = true;
            this.playRemindAni = false;
            this.cardMask.graphics.clear();
            this.cardMask.graphics.drawPie(75, 101, 200, -90 + 360 * per, 270, '#ff0000', '#ff0000', 0);
        } else {
            if (!this.playRemindAni) {
                if (this.isMagic) {
                    this.remind1.play(0, false);
                } else {
                    this.remind.play(0, false);
                }
                this.playRemindAni = true;
            }
            this.bg.disabled = false;
            this.magicImage.disabled = false;
            this.cardMask.visible = false;
            this.maskFrame.visible = false;
        }
    }

    public get dataSource() {
        return this._cardData;
    }

    public clone() {
        let cell = new GameCellRuntime();
        cell.original = this;
        cell.width = this.width;
        cell.height = this.height;
        cell.dataSource = JSON.parse(JSON.stringify(this._cardData));
        return cell;
    }

    changeState(state: CellState) {
        if (state == this.curState) return;
        if (state == CellState.normal) {

        } else if (state == CellState.cd) {
            Laya.timer.once(this._cardData.cd * 1000, this, () => {
                this.changeState(CellState.normal);
            })
        }
        this.curState = state;
    }

    public get cost() {
        return this._cost;
    }

    public set cost(value: number) {
        this._cost = value
        if (this._cardData) {
            this._cardData.cost = value;
        }
        this.costLabel.text = this._cost.toString();
    }

    setOutFight(target: GameCellRuntime, index: number) {
        this.originalIndex = index;
        this.isTweening = true;
        this._cardData = target.dataSource;
        this.visible = false
        let pos = (<Laya.List>this.parent).localToGlobal(new Laya.Point(this.x, this.y), false, this.scene.parent.scene);
        Laya.Tween.to(target, { x: pos.x, y: pos.y }, 300, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
            this.isTweening = false;
            this.visible = true;
            target.destroy();
            this.dataSource = this._cardData;
        }));
    }

    /**
     * 成功使用了这张牌
     */
    useCard() {
        this._cdLeft = this._originalCd;
        this.cost = this._cost + this._increaseCost;
        GameDataManager.instance.countStar[1] += this._cost;
    }
    public get isCd() {
        return this._cdLeft > 0;
    }
    public get isMagic() {
        return this._magicType > 0;
    }
    public get magicType() {
        return this._magicType;
    }
}