import { ui } from "../../ui/layaMaxUI";
import { SceneName, EventTypeEnum } from "../GEnum";
import GEvent from "../../GameFrame/event/GEvent";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import GuideManager from "./GuideManager";
import ResManager from "../../gframe-work/manager/ResManager";
import BigMapRuntime from "./BigMapRuntime";
import TaskRuntime from "./TaskRuntime";

export default class ModeRuntime extends ui.modeUI {

    curGroupDataLocal: LocalStorageData = null;
    curGroupNumberLocal: LocalStorageData = null;
    unlockLocal: LocalStorageData = null;
    curLevelLocal: LocalStorageData = null;
    isSelect: boolean = false

    onAwake() {
        this.name = 'mode';
        this.curLevelLocal = DataManager.get<LocalStorageData>('curLevel');
        this.unlockLocal = DataManager.get<LocalStorageData>('unlock');
        this.curGroupNumberLocal = DataManager.get<LocalStorageData>(`curGroup`);
        this.levelLabel.text = `第${this.curLevelLocal.valueStr}关`;
        this.registerPageEvent();
    }

    onDestroy() {

    }

    registerPageEvent() {
        this.levelBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['level']);
        this.pvpBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['pvp']);
        this.signBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['sign']);
        this.lotteryBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['lottery']);
  		this.shopBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['shop']);
        this.collectBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['collect']);
        this.taskBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['task']);
		this.welfareBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ["welfare"]);    }

    onEnable() {
        if (this.curLevelLocal.value == 3 && DataManager.get<LocalStorageData>('signed').value == 0) {
            this.onBtnClick('sign');
        }
    }

    onMouseEvent(name: string, e: Laya.Event) {
        e.stopPropagation();
        //GEvent.event(EventTypeEnum.MainShowOpen, false);
        if (name == 'level') {
            GEvent.event('BreakPoint', ['关卡进度', this.curLevelLocal.value]);
            GEvent.event('BreakPoint', ['关卡开始', this.curLevelLocal.value]);
            this.curGroupDataLocal = DataManager.get<LocalStorageData>(`group${this.curGroupNumberLocal.value}`);
            for (let i = 0; i < this.unlockLocal.value; i++) {
                const data = this.curGroupDataLocal.value[i];
                if (data && data.id != 0) {
                    GuideManager.Instance.finishTask('点击当前关卡');
                    GuideManager.Instance.hideGuide();
                    ResManager.playSound('Click.mp3');
                    Laya.Scene.open(SceneName.bigMap, false, null, Laya.Handler.create(this, (bigMap: BigMapRuntime) => {

                    }))
                    return;
                }
            }
            ResManager.playSound('Error.mp3');
            Laya.Scene.load(SceneName.cardTip, Laya.Handler.create(this, (page: ui.cardTipUI) => {
                Laya.Dialog.manager.maskLayer.alpha = 0.5;
                page.open();
                page.closeHandler = Laya.Handler.create(this, () => {
                    GEvent.event(EventTypeEnum.JumpPage, 3);
                })
            }));
        } else if (name == 'pvp' || name == 'sign' || name == 'lottery' || name == 'shop' || name == 'task' || name == 'collect') {
            this.onBtnClick(name);
        } else if (e.target == this.welfareBtn) {
            Laya.Scene.open(SceneName.welfare);
        }
    }

    onBtnClick(name: string) {
        ResManager.playSound('Click.mp3');
        if (name == 'lottery') {
            GEvent.event(EventTypeEnum.LotteryOpen, true);
        } else if (name == 'shop') {
        } else if (name == 'task') {
            Laya.Scene.open(SceneName.task, false, null, Laya.Handler.create(this, (task: TaskRuntime) => {

            }))
        } else if (name == 'collect') {

        } else {
            this.playBox.visible = false;
            let pageName = SceneName.pvp;
            if (name == 'sign') {
                pageName = SceneName.sign;
            }
            Laya.Scene.load(pageName, Laya.Handler.create(this, (pageRuntime: any) => {
                pageRuntime.isModal = true;
                pageRuntime.width = Laya.stage.width;
                pageRuntime.height = Laya.stage.height;
                pageRuntime.open(false);
                pageRuntime.closeHandler = Laya.Handler.create(this, () => {
                    this.playBox.visible = true;
                    GEvent.event(EventTypeEnum.MainShowOpen, true);
                })
            }))
        }
    }

    onSelect() {
        this.isSelect = true;
        GEvent.event(EventTypeEnum.NextChange, 'hide');
    }
}