import { ui } from "../../ui/layaMaxUI";
import GEvent from "../../GameFrame/event/GEvent";
import { EventTypeEnum, SceneName } from "../GEnum";
import FightCellRuntime from "./FightCellRuntime";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import OutCellRuntime from "./OutCellRuntime";
import BuyConfirmRuntime from "./BuyConfirmRuntime";
import GuideManager from "./GuideManager";
import ResManager from "../../gframe-work/manager/ResManager";
import CardManager from "./CardManager";
import UserDataManager from "../../gframe-work/manager/UserDataManager";
import MagicCardRuntime from "./MagicCardRuntime";

export default class SelectRuntime extends ui.selectUI {

    diamondLocal: LocalStorageData = null;
    curGroupDataLocal: LocalStorageData = null;
    curGroupNumberLocal: LocalStorageData = null;
    houseLocal: LocalStorageData = null;
    unlockLocal: LocalStorageData = null;
    goldLocal: LocalStorageData = null;
    sorting: boolean = false;
    unlockOutPage: ui.unlockOutUI = null;
    curIndex: number = 0;
    isSelect: boolean = false;
    lastSelectIndex: number = -1;
    lastOutIndex: number = -1;
    selectType: number = 0;
    curMagicType: number = 0;
    curPageId: number = -1;

    onAwake() {
        this.curMagicType = 1;
        this.tabList.hScrollBarSkin = null;
        this.houseList.vScrollBarSkin = null;
        this.magicCardList.hScrollBarSkin = null;

        this.allBtn.on(Laya.Event.CLICK, this, this.selectStore, [0]);
        this.standBtn.on(Laya.Event.CLICK, this, this.selectStore, [1]);
        this.climbBtn.on(Laya.Event.CLICK, this, this.selectStore, [2]);
        this.flyBtn.on(Laya.Event.CLICK, this, this.selectStore, [3]);
        this.useBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.upgradeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.magicBox1.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.magicBox2.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.monsterBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.magicBtn.on(Laya.Event.CLICK, this, this.onBtnClick);

        this.houseList.mouseHandler = Laya.Handler.create(this, this.onHouseHandler, null, false);
        this.tabList.mouseHandler = Laya.Handler.create(this, this.onSelectHandler, null, false);
        this.eventList.mouseHandler = Laya.Handler.create(this, this.onEventHandler, null, false);
        this.magicCardList.mouseHandler = Laya.Handler.create(this, this.onMagicHandler, null, false);
        this.houseList.scrollBar.changeHandler = Laya.Handler.create(this, this.onScrollBarHandler, null, false);

        this.diamondLocal = DataManager.get<LocalStorageData>('diamond');
        this.goldLocal = DataManager.get<LocalStorageData>('gold');
        this.unlockLocal = DataManager.get<LocalStorageData>(`unlock`);
        this.houseLocal = DataManager.get<LocalStorageData>(`house`);
        this.curGroupNumberLocal = DataManager.get<LocalStorageData>(`curGroup`);

        this.selectTab(this.curGroupNumberLocal.value - 1);
        this.selectStore(0);
        this.initOutCell();
        this.initEventList();
        this.updateOutCard();
        this.upgradeSelectMagic(1);
        this.registerCustomEvent();
        this.registerPageEvent();
        this.switchPage(0);
        Laya.timer.frameLoop(1, this, this.onFrameLoop);
        this.magicCardList.scrollBar.changeHandler = Laya.Handler.create(this, this.onMagicScrollBarHandler, null, false);
    }

    onBtnClick(e: Laya.Event) {
        let useMagicType = UserDataManager.instance.useMagicType;
        if (e.target == this.useBtn) {
            if (useMagicType.length == 2) {

            } else {
                if (useMagicType.indexOf(this.curMagicType) == -1) {
                    let destCard = this.magicOut1;
                    if (useMagicType.length == 1) {
                        destCard = this.magicOut2;
                    }
                    let flyCard = new Laya.Image();
                    flyCard.skin = `card/Ui_Magic${this.curMagicType}.png`;
                    Laya.stage.addChild(flyCard);
                    let pointStart = this.magicImage.localToGlobal(new Laya.Point, false);
                    flyCard.x = pointStart.x; flyCard.y = pointStart.y; flyCard.width = 159; flyCard.height = 195;
                    let pointDest = destCard.localToGlobal(new Laya.Point, false);
                    Laya.MouseManager.enabled = false;
                    this.flyMagicCard(flyCard, { x: pointDest.x, y: pointDest.y, width: 172, height: 211 }).then(() => {
                        Laya.MouseManager.enabled = true;
                        flyCard.destroy();
                        this.updateOutCard();
                    })
                    useMagicType.push(this.curMagicType);
                    UserDataManager.instance.useMagicType = useMagicType;
                } else {

                }
            }
        } else if (e.target == this.upgradeBtn) {
            let magicCardLevel = UserDataManager.instance.magicCardLevel;
            let magicLevel = magicCardLevel[this.curMagicType - 1];
            let upgradeCondition = this.checkUpgradeCondition(this.curMagicType, magicLevel);
            if (magicLevel < 6 && upgradeCondition) {
                this.diamondLocal.value -= upgradeCondition.diamond;
                this.goldLocal.value -= upgradeCondition.gold;
                let cardNum = UserDataManager.instance.magicCardNum;
                cardNum[this.curMagicType - 1] -= upgradeCondition.card;
                UserDataManager.instance.magicCardNum = cardNum;
                this.updateMagicInfo(this.curMagicType, magicLevel + 1);
                magicCardLevel[this.curMagicType - 1] = magicLevel + 1;
                UserDataManager.instance.magicCardLevel = magicCardLevel;
            }
        } else if (e.target == this.magicBox1) {
            if (!useMagicType[0]) return;
            this.magicOut1.skin = null;
            this.magicOut2.skin = null;
            if (useMagicType[1]) {
                let flyCard = new Laya.Image();
                flyCard.skin = `card/Ui_Magic${useMagicType[1]}.png`;
                Laya.stage.addChild(flyCard);
                let pointStart = this.magicOut2.localToGlobal(new Laya.Point, false);
                flyCard.x = pointStart.x; flyCard.y = pointStart.y; flyCard.width = 172; flyCard.height = 211;
                let pointDest = this.magicOut1.localToGlobal(new Laya.Point, false);
                Laya.MouseManager.enabled = false;
                this.flyMagicCard(flyCard, { x: pointDest.x, y: pointDest.y, width: 172, height: 211 }).then(() => {
                    Laya.MouseManager.enabled = true;
                    flyCard.destroy();
                    this.updateOutCard();
                })
            }
            useMagicType.splice(0, 1);
            UserDataManager.instance.useMagicType = useMagicType;
        } else if (e.target == this.magicBox2) {
            if (!useMagicType[1]) return;
            useMagicType.splice(1, 1);
            UserDataManager.instance.useMagicType = useMagicType;
            this.updateOutCard();
        } else if (e.target == this.monsterBtn) {
            this.switchPage(0);
        } else if (e.target == this.magicBtn) {
            this.switchPage(1);
        }
    }

    flyMagicCard(card: Laya.Image, dest: { x: number, y: number, width: number, height: number }) {
        return new Promise<void>(resolve => {
            Laya.Tween.to(card, dest, 500, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
                resolve();
            }))
        })
    }

    onFrameLoop() {
        this.light.x -= 2;
        if (this.light.x <= -772) {
            this.light.x = 0;
        }
    }

    onDestroy() {
        Laya.timer.clear(this, this.onFrameLoop);
        this.cancelCustomEvent();
    }

    private pointerTweening: boolean = false;
    onMagicScrollBarHandler(value: number) {
        if (this.pointerTweening) return;
        let completeHandler = Laya.Handler.create(this, () => {
            this.pointerTweening = false;
        })
        let destAlpha = 1;
        if (value > (this.magicCardList.scrollBar.max - 201)) {
            destAlpha = 0;
        }
        this.pointerTweening = true;
        Laya.Tween.to(this.morePointer, { alpha: destAlpha }, 100, Laya.Ease.sineOut, completeHandler);
    }

    onScrollBarHandler(value: number) {
        if (this.lastSelectIndex != -1) {
            let lastCell: FightCellRuntime = <any>this.houseList.getCell(this.lastSelectIndex);
            this.infoEventBox.visible = false;
            this.outEventBox.visible = false;
            if (lastCell) {
                lastCell.openInfo(false, 0);
                lastCell.zOrder = 0;
                this.lastSelectIndex = -1;
            }
        }
        if (!this.isSelect) return;
        if (value >= 0) {
            if (value >= this.houseList.scrollBar.max) {
                GEvent.event(EventTypeEnum.NextChange, 'hide');
            } else {
                GEvent.event(EventTypeEnum.NextChange, 'show');
            }
        }
    }

    onHouseDataChange() {
        let houseData = this.houseLocal.value;
        for (let index = 0; index < 1; index++) {
            let groupData: any[] = DataManager.get<LocalStorageData>(`group${index + 1}`).value;
            for (let i = 0; i < groupData.length; i++) {
                const data = groupData[i];
                let isHave = false;
                for (let j = 0; j < houseData.length; j++) {
                    const house = houseData[j];
                    if (data.id == house.id) {
                        isHave = true;
                        break;
                    }
                }
                if (!isHave) {
                    groupData[i] = CardManager.instance.getCardData(-1);
                }
            }

            for (let i = 0; i < groupData.length; i++) {
                let data1 = groupData[i];
                for (let j = i + 1; j < groupData.length; j++) {
                    let data2 = groupData[j];
                    if (data1.id == 0 && data2.id > 0) {
                        let temp = data1;
                        groupData[i] = data2;
                        groupData[j] = temp;
                    }
                }
            }

            DataManager.get<LocalStorageData>(`group${index + 1}`).value = groupData;

            GEvent.event(EventTypeEnum.SelectCardChange);
        }

        this.selectTab(this.curGroupNumberLocal.value - 1);
        this.selectStore(this.curIndex);
    }

    registerPageEvent() {
        this.infoEventBox.on(Laya.Event.CLICK, this, this.onMouseEvent, ['info']);
        this.outEventBox.on(Laya.Event.CLICK, this, this.onMouseEvent, ['out']);
    }

    onMouseEvent(name: string, e: Laya.Event) {
        e.stopPropagation();
        if (this.lastSelectIndex != -1) {
            let lastCell: FightCellRuntime = <any>this.houseList.getCell(this.lastSelectIndex);
            this.infoEventBox.visible = false;
            this.outEventBox.visible = false;
            if (lastCell) {
                lastCell.openInfo(false, 0);
                lastCell.zOrder = 0;
            }
        }
        if (this.lastOutIndex != -1) {
            let lastCell: FightCellRuntime = <any>this.outList.getCell(this.lastOutIndex);
            this.infoEventBox.visible = false;
            this.outEventBox.visible = false;
            if (lastCell) {
                lastCell.openInfo(false, 0);
                lastCell.zOrder = 0;
            }
        }
        if (name == 'info') {
            ResManager.playSound('Open.mp3');
            Laya.Scene.load(SceneName.buyConfirm, Laya.Handler.create(this, (page: BuyConfirmRuntime) => {
                let curCell: FightCellRuntime | OutCellRuntime = null;
                if (this.selectType == 1) {
                    if (this.lastSelectIndex != -1) {
                        curCell = <any>this.houseList.getCell(this.lastSelectIndex);
                        this.lastSelectIndex = -1;
                    }
                } else if (this.selectType == 0) {
                    if (this.lastOutIndex != -1) {
                        curCell = <any>this.outList.getCell(this.lastOutIndex);
                        this.lastOutIndex = -1;
                    }
                }
                if (curCell) {
                    page.cell.dataSource = curCell.dataSource;
                    page.cell.cardBlack.visible = false;
                    page.hpLabel.text = curCell.fightData.hp;
                    page.attackLabel.text = curCell.fightData.atk;
                    page.delayLabel.text = (Math.floor((2000 - curCell.fightData.atkgap) / 10)).toString();
                    page.speedLabel.text = curCell.fightData.speed;
                    page.frameTitle.visible = true;
                    page.buyBtn.visible = false;

                    let titleImage = '';
                    if (curCell.dataSource.type == 1) {
                        titleImage = 'shop/Ui_BuyFont2.png';
                    } else if (curCell.dataSource.type == 2) {
                        titleImage = 'shop/Ui_BuyFont1.png';
                    } else if (curCell.dataSource.type == 3) {
                        titleImage = 'shop/Ui_BuyFont3.png';
                    }
                    page.title.skin = titleImage;
                    page.open();
                }
            }))
        } else if (name == 'out') {
            ResManager.playSound('Click.mp3');
            if (this.selectType == 1) {
                if (this.lastSelectIndex != -1) {
                    let originalCell: FightCellRuntime = null;
                    let outIndex: number = -1;
                    for (let i = 0; i < this.outList.array.length; i++) {
                        let cell: FightCellRuntime = <any>this.outList.getCell(i);
                        if (this.judgeSameCard(cell.dataSource, this.houseList.array[this.lastSelectIndex])) {
                            GEvent.event(EventTypeEnum.SendTip, [['已出战!']]);
                            this.lastSelectIndex = -1;
                            return
                        } else {
                            if (cell.dataSource.id == 0 && outIndex == -1) {
                                originalCell = <any>this.houseList.getCell(this.lastSelectIndex);
                                outIndex = i;
                            }
                        }
                    }

                    if (originalCell) {
                        if (originalCell.isTweening) return;
                        let cell: OutCellRuntime = <any>this.outList.getCell(outIndex);
                        let cellClone = new OutCellRuntime();
                        cellClone.dataSource = JSON.parse(JSON.stringify(originalCell.dataSource));
                        this.outList.array[outIndex] = cellClone.dataSource;
                        this.curGroupDataLocal.value = this.outList.array;
                        let pos = originalCell.globalToLocal(new Laya.Point(e.stageX, e.stageY));
                        cellClone.pos(e.stageX - pos.x, e.stageY - pos.y);
                        cellClone.width = originalCell.width * originalCell.scaleX * this.houseList.scaleX;
                        cellClone.height = originalCell.height * originalCell.scaleY * this.houseList.scaleY;
                        this.cardView.addChild(cellClone);
                        cell.setOutFight(cellClone, outIndex);
                        this.removeOrAddData(originalCell.dataSource, true);
                        this.lastSelectIndex = -1;
                        GuideManager.Instance.finishTask("点击出战");
                        GEvent.event(EventTypeEnum.SelectCardChange)
                        return;
                    }
                    this.lastSelectIndex = -1;
                    GEvent.event(EventTypeEnum.SendTip, [['位置已满!']]);
                }
            } else if (this.selectType == 0) {
                if (this.lastOutIndex != -1) {
                    if (this.lastOutIndex < this.unlockLocal.value) {
                        let cell: FightCellRuntime = <any>this.outList.getCell(this.lastOutIndex);
                        if (cell.isTweening || cell.dataSource == null || cell.dataSource.id == 0) {
                            return;
                        }
                        let selectType = 0;
                        if (this.curIndex == 0) {
                            selectType = 0;
                        } else if (this.curIndex == 1) {
                            selectType = 2;
                        } else if (this.curIndex == 2) {
                            selectType = 1;
                        } else if (this.curIndex == 3) {
                            selectType = 3;
                        }

                        if (cell.dataSource.type == selectType || selectType == 0) {
                            let sortArray: any[] = [];
                            for (let i = 0; i < this.houseList.array.length; i++) {
                                let sortData = this.houseList.array[i];
                                if (sortData && sortData.id > 0) {
                                    sortArray.push(sortData);
                                }
                            }
                            sortArray.push(cell.clone().dataSource);
                            this.sortData(sortArray);
                            this.houseList.array = sortArray;
                            this.houseList.refresh();
                        }
                        let cellClone = cell.clone();
                        let pos = cell.localToGlobal(new Laya.Point(), false, this.cardView);
                        cellClone.pos(pos.x, pos.y);
                        this.cardView.addChild(cellClone);
                        this.flyCenter(cellClone);

                        cell.setEmpty();
                        this.outList.array[this.lastOutIndex] = cell.dataSource;
                        this.sortOutCell(this.lastOutIndex);
                        this.lastOutIndex = -1;
                        GEvent.event(EventTypeEnum.SelectCardChange);
                    }
                }
            }
        }

    }
    registerCustomEvent() {
        GEvent.on(EventTypeEnum.CardHouseChange, this, this.onHouseDataChange);
        GEvent.on(EventTypeEnum.ComposeNewCard, this, this.onHouseDataChange);
        GEvent.on(EventTypeEnum.OutCard, this, this.onMouseEvent);
    }

    cancelCustomEvent() {
        GEvent.off(EventTypeEnum.CardHouseChange, this, this.onHouseDataChange);
        GEvent.off(EventTypeEnum.ComposeNewCard, this, this.onHouseDataChange);
        GEvent.off(EventTypeEnum.OutCard, this, this.onMouseEvent);
    }

    selectStore(index: number) {

        if (this.lastSelectIndex != -1) {
            let lastCell: FightCellRuntime = <any>this.houseList.getCell(this.lastSelectIndex);
            this.infoEventBox.visible = false;
            this.outEventBox.visible = false;
            if (lastCell) {
                lastCell.openInfo(false, 0);
                lastCell.zOrder = 0;
                this.lastSelectIndex = -1;
            }
        }
        if (this.lastOutIndex != -1) {
            let lastCell: FightCellRuntime = <any>this.outList.getCell(this.lastOutIndex);
            this.infoEventBox.visible = false;
            this.outEventBox.visible = false;
            if (lastCell) {
                lastCell.openInfo(false, 0);
                lastCell.zOrder = 0;
                this.lastOutIndex = -1;
            }
        }

        this.curIndex = index;
        this.allBtn.skin = 'card/Btn_bule.png';
        this.standBtn.skin = 'card/Btn_bule.png';
        this.climbBtn.skin = 'card/Btn_bule.png';
        this.flyBtn.skin = 'card/Btn_bule.png';

        let selectDataArray = [];
        if (index == 0) {
            for (let i = 0; i < this.houseLocal.value.length; i++) {
                const data = this.houseLocal.value[i];
                if (!this.inOutList(data))
                    selectDataArray.push(data);
            }
            this.allBtn.skin = 'card/Btn_yellow.png';
        } else if (index == 1) {
            for (let i = 0; i < this.houseLocal.value.length; i++) {
                const data = this.houseLocal.value[i];
                if (data.type == 2) {
                    if (!this.inOutList(data))
                        selectDataArray.push(data);
                }
            }
            this.standBtn.skin = 'card/Btn_yellow.png';
        } else if (index == 2) {
            for (let i = 0; i < this.houseLocal.value.length; i++) {
                const data = this.houseLocal.value[i];
                if (data.type == 1) {
                    if (!this.inOutList(data))
                        selectDataArray.push(data);
                }
            }
            this.climbBtn.skin = 'card/Btn_yellow.png';
        } else if (index == 3) {
            for (let i = 0; i < this.houseLocal.value.length; i++) {
                const data = this.houseLocal.value[i];
                if (data.type == 3) {
                    if (!this.inOutList(data))
                        selectDataArray.push(data);
                }
            }
            this.flyBtn.skin = 'card/Btn_yellow.png';
        }

        this.sortData(selectDataArray);

        this.houseList.array = selectDataArray;
        this.houseList.refresh();

        this.onScrollBarHandler(this.houseList.scrollBar.value);
    }

    onStoreTabHandler(e: Laya.Event, index: number) {
        if (e.type == Laya.Event.CLICK) {
            if (this.sorting || index == this.curIndex) return;
            ResManager.playSound('Click.mp3');
            this.selectStore(index);
        }
    }

    onMagicHandler(e: Laya.Event, index: number) {
        if (e.type == Laya.Event.CLICK) {
            let cell: MagicCardRuntime = <any>this.magicCardList.getCell(index);
            let clickMagicType = cell.dataSource.magicType;
            let unlock = UserDataManager.instance.magicCardUnlock[clickMagicType - 1];
            if (!unlock) return;
            if (clickMagicType != this.curMagicType) {
                this.upgradeMagicList(clickMagicType);
                this.upgradeSelectMagic(clickMagicType);
                this.curMagicType = clickMagicType;
            }
        }
    }

    onEventHandler(e: Laya.Event, index: number) {
        if (e.type == Laya.Event.CLICK) {
            this.onCellHandler(e, index);
            this.onOutHandler(e, index);
        }
    }

    onCellHandler(e: Laya.Event, index: number) {
        if (e.type == Laya.Event.CLICK) {

            if (index != this.unlockLocal.value) {
                return;
            }

            let unlockGold = (this.unlockLocal.value - 3) * 2000 + 2000;
            if (this.goldLocal.value < unlockGold) {
                ResManager.playSound('Error.mp3');
                GEvent.event(EventTypeEnum.SendTip, [['金币不足!']]);
                return;
            }

            ResManager.playSound('Click.mp3');
            this.unlockOutCell();
            this.goldLocal.value -= unlockGold;
        }
    }

    onSelectHandler(e: Laya.Event, index: number) {
        if (e.type == Laya.Event.CLICK) {
            if (this.sorting) return;
            this.selectTab(index);
        }
    }

    onOutHandler(e: Laya.Event, index: number) {
        if (e.type == Laya.Event.CLICK) {
            if (this.sorting) return;

            if (this.lastSelectIndex != -1) {
                let lastCell: FightCellRuntime = <any>this.houseList.getCell(this.lastSelectIndex);
                this.infoEventBox.visible = false;
                this.outEventBox.visible = false;
                if (lastCell) {
                    lastCell.openInfo(false, 0);
                    lastCell.zOrder = 0;
                    this.lastSelectIndex = -1;
                }
            }
            let curCell: FightCellRuntime = <any>this.outList.getCell(index);
            if (!curCell.dataSource || curCell.dataSource.id == 0) return;
            if (this.lastOutIndex != -1) {
                let lastCell: FightCellRuntime = <any>this.outList.getCell(this.lastOutIndex);
                this.infoEventBox.visible = false;
                this.outEventBox.visible = false;
                if (lastCell) {
                    lastCell.openInfo(false, 0);
                    lastCell.zOrder = 0;
                }
                if (index != this.lastOutIndex) {
                    ResManager.playSound('Click.mp3');
                    let infoGlobal = curCell.info.localToGlobal(new Laya.Point, false, this);
                    let outGlobal = curCell.out.localToGlobal(new Laya.Point, false, this);
                    this.infoEventBox.pos(infoGlobal.x, infoGlobal.y);
                    this.outEventBox.pos(outGlobal.x, outGlobal.y);
                    this.infoEventBox.visible = true;
                    this.outEventBox.visible = true;
                    if (curCell) {
                        curCell.openInfo(true, 0);
                        curCell.zOrder = 100;
                    }
                    this.lastOutIndex = index;
                    this.selectType = 0;
                } else {
                    this.lastOutIndex = -1;
                }
            } else {
                ResManager.playSound('Click.mp3');
                let infoGlobal = curCell.info.localToGlobal(new Laya.Point, false, this);
                let outGlobal = curCell.out.localToGlobal(new Laya.Point, false, this);
                this.infoEventBox.pos(infoGlobal.x, infoGlobal.y);
                this.outEventBox.pos(outGlobal.x, outGlobal.y);
                this.infoEventBox.visible = true;
                this.outEventBox.visible = true;
                if (curCell) {
                    curCell.openInfo(true, 0);
                    curCell.zOrder = 100;
                }
                this.lastOutIndex = index;
                this.selectType = 0;
            }
            this.houseList.updateZOrder();
        }
    }

    onHouseHandler(e: Laya.Event, index: number) {
        if (this.sorting) return;
        if (e.type == Laya.Event.CLICK) {
            if (this.lastOutIndex != -1) {
                let lastCell: FightCellRuntime = <any>this.outList.getCell(this.lastOutIndex);
                this.infoEventBox.visible = false;
                this.outEventBox.visible = false;
                if (lastCell) {
                    lastCell.openInfo(false, 0);
                    lastCell.zOrder = 0;
                    this.lastOutIndex = -1;
                }
            }
            let curCell: FightCellRuntime = <any>this.houseList.getCell(index);
            if (this.lastSelectIndex != -1) {
                let lastCell: FightCellRuntime = <any>this.houseList.getCell(this.lastSelectIndex);
                this.infoEventBox.visible = false;
                this.outEventBox.visible = false;
                if (lastCell) {
                    lastCell.openInfo(false, 0);
                    lastCell.zOrder = 0;
                }
                if (index != this.lastSelectIndex) {
                    ResManager.playSound('Click.mp3');
                    let infoGlobal = curCell.info.localToGlobal(new Laya.Point, false, this);
                    let outGlobal = curCell.out.localToGlobal(new Laya.Point, false, this);
                    this.infoEventBox.pos(infoGlobal.x, infoGlobal.y);
                    this.outEventBox.pos(outGlobal.x, outGlobal.y);
                    this.infoEventBox.visible = true;
                    this.outEventBox.visible = true;
                    if (curCell) {
                        curCell.openInfo(true, 1);
                        curCell.zOrder = 100;
                    }
                    this.lastSelectIndex = index;
                    this.selectType = 1;
                } else {
                    this.lastSelectIndex = -1;
                }
            } else {
                ResManager.playSound('Click.mp3');
                let infoGlobal = curCell.info.localToGlobal(new Laya.Point, false, this);
                let outGlobal = curCell.out.localToGlobal(new Laya.Point, false, this);
                this.infoEventBox.pos(infoGlobal.x, infoGlobal.y);
                this.outEventBox.pos(outGlobal.x, outGlobal.y);
                this.infoEventBox.visible = true;
                this.outEventBox.visible = true;
                if (curCell) {
                    GuideManager.Instance.finishTask('点击选择一张卡片');
                    curCell.openInfo(true, 1);
                    curCell.zOrder = 100;
                }
                this.lastSelectIndex = index;
                this.selectType = 1;
            }
            this.houseList.updateZOrder();
        }
    }

    judgeSameCard(data1: any, data2: any) {
        if (data1.id != data2.id
            || data1.attack != data2.attack
            || data1.def != data2.def
            || data1.hp != data2.hp
            || data1.attackDelay != data2.attackDelay
            || data1.attackDistance != data2.attackDistance
            || data1.moveSpeed != data2.moveSpeed
            || data1.color != data2.color
        ) {
            return false;
        }
        return true;
    }

    selectTab(index: number) {
        let array = [];
        for (let i = 0; i < 3; i++) {
            let data = { tabIndex: { text: '' + (i + 1) }, offImage: { visible: false }, onImage: { visible: true } }
            if (i == index) {
                data.offImage.visible = false;
                data.onImage.visible = true;
            } else {
                data.offImage.visible = true;
                data.onImage.visible = false;
            }
            array.push(data);
        }
        this.tabList.array = array;
        this.tabList.refresh();

        this.curGroupNumberLocal.value = index + 1;

        this.curGroupDataLocal = DataManager.get<LocalStorageData>(`group${this.curGroupNumberLocal.value}`);
        this.outList.array = this.curGroupDataLocal.value;
        this.outList.refresh();
    }

    initEventList() {
        this.eventList.array = [{}, {}, {}, {}, {}, {}];
        this.eventList.refresh();
    }

    initOutCell() {
        let array = [];
        let unlockValue = this.unlockLocal.value;
        for (let i = 0; i < 6; i++) {
            let unlockGold = 0;
            if (i > 2) {
                unlockGold = (i - 3) * 2000 + 2000;
            }
            let data = { unlock: { visible: i < unlockValue }, lock: { visible: i >= unlockValue }, moneyUnlock: { visible: i == unlockValue, goldNum: { text: unlockGold.toString() } }, unlockTip: { visible: i > unlockValue } };
            array.push(data);
        }
        this.cellList.array = array;
        this.cellList.refresh();
    }

    unlockOutCell() {
        this.unlockLocal.value++;
        this.addUnlockCell();
        this.initOutCell();
    }

    sortOutCell(index: number) {
        for (let i = index; i < this.outList.array.length; i++) {
            const cell: OutCellRuntime = <any>this.outList.getCell(i);
            let originalCell: OutCellRuntime = <any>this.outList.getCell(i + 1);
            if ((i + 1 < this.outList.array.length) && originalCell && originalCell.dataSource) {
                let cloneCell = originalCell.clone();
                let pos = this.outList.localToGlobal(new Laya.Point(originalCell.x, originalCell.y), false, this);
                cloneCell.pos(pos.x, pos.y);
                this.cardView.addChild(cloneCell);
                cell.setOutFight(cloneCell, i + 1);
            } else {
                cell.setEmpty();
            }
        }
        for (let i = 0; i < this.outList.array.length; i++) {
            const cell: OutCellRuntime = <any>this.outList.getCell(i);
            this.outList.array[i] = cell.dataSource;
        }
        this.curGroupDataLocal.value = this.outList.array;
    }
    addUnlockCell() {
        for (let i = 0; i < 3; i++) {
            let group = DataManager.get<LocalStorageData>(`group${i + 1}`);
            let data = group.value;
            data.push(CardManager.instance.getCardData(-1));
            group.value = data;
        }
        this.outList.array = DataManager.get<LocalStorageData>(`group${this.curGroupNumberLocal.value}`).value;
        this.outList.refresh();
    }

    onSelect() {
        this.isSelect = true;
        GEvent.event(EventTypeEnum.NextChange, 'hide');
    }

    sortData(dataArray: any[]) {
        dataArray.sort((a, b) => {
            return b.attack - a.attack;
        })
    }

    inOutList(data: any) {
        for (let i = 0; i < this.outList.array.length; i++) {
            if (data.id == this.outList.array[i].id) {
                return true;
            }
        }
        return false;
    }

    removeOrAddData(newData: any, isRemove: boolean) {
        let dataArray = [];
        for (let i = 0; i < this.houseList.array.length; i++) {
            let houseData = this.houseList.array[i];
            if (isRemove) {
                if (houseData && houseData.id > 0 && houseData.id != newData.id) {
                    dataArray.push(houseData);
                }
            } else {
                if (houseData && houseData.id > 0) {
                    dataArray.push(houseData);
                }
            }
        }
        if (!isRemove) {
            dataArray.push(newData);
        }
        this.sortData(dataArray);
        this.houseList.array = dataArray;
        this.houseList.refresh();
    }


    flyCenter(card: any) {
        let finalX = (this.cardView.width - card.width) / 2;
        let finalY = (this.cardView.height - card.height) / 2;
        Laya.Tween.to(card, { x: finalX, y: finalY, alpha: 0 }, 200, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
            card.destroy();
        }));
    }

    /**
     * 更新法术牌细节展示
     * @param magicType 
     */
    upgradeSelectMagic(magicType: number) {
        this.upgradeMagicList(magicType);
        this.magicImage.skin = `card/Ui_Magic${magicType}.png`;
        let magicLevel = UserDataManager.instance.magicCardLevel[magicType - 1];
        this.updateMagicInfo(magicType, magicLevel);
    }
    /**
     * 更新法术牌列表
     * @param magicType 
     */
    upgradeMagicList(magicType: number) {
        let dataArray = [];
        let unlockRecord = UserDataManager.instance.magicCardUnlock;
        for (let i = 0; i < 7; i++) {
            let cardNum = UserDataManager.instance.magicCardNum[i];
            if (cardNum > 0) {
                unlockRecord[i] = 1;
            }
            if (i != 5) {
                dataArray.push({ magicType: i + 1, isSelect: i + 1 == magicType });
            }
        }
        UserDataManager.instance.magicCardUnlock = unlockRecord;
        this.magicCardList.array = dataArray;
        this.magicCardList.refresh();
    }
    /**
     * 更新法术牌信息
     * @param magicType 
     * @param magicLevel 
     */
    updateMagicInfo(magicType: number, magicLevel: number) {
        this.magicName.skin = `card/Ui_MagicName${magicType}.png`;
        let magicData = CardManager.instance.getMagicCardData(magicType, magicLevel + 1);
        let desc = magicData.description;
        let paramArr: string[] = (magicData.param + "").split(",");
        if (magicData.description.indexOf("*") != -1) {
            for (let i = 0; i < paramArr.length; i++) {
                if (magicType == 7 && i == 0) continue;
                let param = paramArr[i];
                if ((magicType == 4 && i == 0) ||
                    (magicType == 6 && i == 0)
                ) {
                    param = param + "%";
                }
                desc = desc.replace("*", param);
            }
        }
        this.magicDesc.text = desc;
        let upgradeCost = magicData.upgradeCost.split(",");
        this.moneyCost.text = `X${upgradeCost[0]}`;
        this.diamondCost.text = `X${upgradeCost[1]}`;
        this.cardCost.text = `${UserDataManager.instance.magicCardNum[magicType - 1]}/${upgradeCost[2]}`;
    }
    /**
     * 更新出战卡牌
     */
    updateOutCard() {
        for (let i = 0; i < 2; i++) {
            let card: Laya.Image = null;
            if (i == 0) {
                card = this.magicOut1;
            } else if (i == 1) {
                card = this.magicOut2;
            }
            let magicType = UserDataManager.instance.useMagicType[i];
            if (magicType) {
                card.visible = true;
                card.skin = `card/Ui_Magic${magicType}.png`;
            } else {
                card.visible = false;
                card.skin = null;
            }
        }
    }
    /**
     * 检查卡牌升级条件
     * @param magicType 
     * @param magicLevel 
     */
    checkUpgradeCondition(magicType: number, magicLevel: number) {
        let magicData = CardManager.instance.getMagicCardData(magicType, magicLevel + 1);
        let upgradeCost = magicData.upgradeCost.split(",");
        let moneyNeed = Number(upgradeCost[0]);
        let diamondNeed = Number(upgradeCost[1]);
        let cardNeed = Number(upgradeCost[2]);
        let moneyHave = this.goldLocal.value;
        let diamondHave = this.diamondLocal.value;
        let cardHave = UserDataManager.instance.magicCardNum[magicType - 1];
        if (moneyNeed <= moneyHave && diamondNeed <= diamondHave && cardNeed <= cardHave) {
            return { gold: moneyNeed, diamond: diamondNeed, card: cardNeed };
        }
        return null;
    }
    /**
     * 切换界面
     * @param id 
     */
    switchPage(id: number) {
        if (this.curPageId == id) return;
        this.curPageId = id;
        let tweenTarget = { progress: 0 };
        let updateHandler: Laya.Handler = null;
        let completeHandler: Laya.Handler = null;
        if (id == 0) {
            this.infoEventBox.visible = true;
            this.outEventBox.visible = true;
            this.eventList.visible = true;

            this.title.visible = true;
            this.houseList.visible = true;
            this.cellList.visible = true;
            this.outList.visible = true;
            updateHandler = Laya.Handler.create(this, () => {
                this.magicBox1.alpha = this.magicBox2.alpha = 1 - tweenTarget.progress;
                this.magicTitle.alpha = 1 - tweenTarget.progress;
                this.magicBox.alpha = 1 - tweenTarget.progress;
                this.frame.height = 320 + (500 - 320) * tweenTarget.progress;
                this.switchBg.x = 388 + (63 - 388) * tweenTarget.progress;
                this.title.alpha = tweenTarget.progress;
                this.houseList.alpha = tweenTarget.progress;
                this.cellList.alpha = tweenTarget.progress;
                this.outList.alpha = tweenTarget.progress;
            }, null, false);
            completeHandler = Laya.Handler.create(this, () => {
                this.magicBox1.visible = this.magicBox2.visible = false;
                this.magicTitle.visible = false;
                this.magicBox.visible = false;
                Laya.MouseManager.enabled = true;
            })
        } else {
            this.infoEventBox.visible = false;
            this.outEventBox.visible = false;
            this.eventList.visible = false;

            this.magicBox1.visible = this.magicBox2.visible = true;
            this.magicTitle.visible = true;
            this.magicBox.visible = true;
            updateHandler = Laya.Handler.create(this, () => {
                this.magicBox1.alpha = this.magicBox2.alpha = tweenTarget.progress;
                this.magicTitle.alpha = tweenTarget.progress;
                this.magicBox.alpha = tweenTarget.progress;
                this.frame.height = 500 - (500 - 320) * tweenTarget.progress;
                this.switchBg.x = 63 + (388 - 63) * tweenTarget.progress;
                this.magicTitle.top = 690 - (500 - 320) * tweenTarget.progress;
                this.magicBox.top = 760 - (500 - 320) * tweenTarget.progress;
                this.title.alpha = 1 - tweenTarget.progress;
                this.houseList.alpha = 1 - tweenTarget.progress;
                this.cellList.alpha = 1 - tweenTarget.progress;
                this.outList.alpha = 1 - tweenTarget.progress;
            }, null, false);
            completeHandler = Laya.Handler.create(this, () => {
                this.title.visible = false;
                this.houseList.visible = false;
                this.cellList.visible = false;
                this.outList.visible = false;
                Laya.MouseManager.enabled = true;
            })
        }
        Laya.MouseManager.enabled = false;
        Laya.Tween.to(tweenTarget, { progress: 1, update: updateHandler }, 500, Laya.Ease.sineOut, completeHandler);
    }
}