import UserDataManager from "../../gframe-work/manager/UserDataManager";
import { ui } from "../../ui/layaMaxUI";
import MapCellRuntime from "./MapCellRuntime";

export default class MapPageRuntime extends ui.mapPageUI {
    public set dataSource(chapter: number) {
        this._dataSource = chapter;
        if (chapter) {
            let curStage = UserDataManager.instance.curStage;
            let levelId = curStage[1];
            let chapterId = curStage[0];
            let starRecord = UserDataManager.instance.starRecord;
            this.mapImage.skin = `map/Ui_Map${chapter}.png`;
            for (let i = 0; i < 10; i++) {
                let base = this.baseBox.getChildByName(`base${i + 1}`) as MapCellRuntime;
                let line = this.lineBox.getChildByName(`line${i + 1}`) as Laya.Image;
                let curNormalizeLevel = (chapter - 1) * 10 + i + 1;
                let playNormalizeLevel = (chapterId - 1) * 10 + levelId;
                let canClick = curNormalizeLevel <= playNormalizeLevel;
                if (curNormalizeLevel >= playNormalizeLevel) {
                    base.dataSource = { state: 2, star: 0, level: i + 1, chapter: chapter, canClick: canClick };
                } else {
                    base.dataSource = { state: 1, star: starRecord[curNormalizeLevel - 1], level: i + 1, chapter: chapter, canClick: canClick };
                }
                if (curNormalizeLevel <= playNormalizeLevel) {
                    line.visible = true;
                } else {
                    line.visible = false;
                }
            }
        }
    }
    public get dataSource() {
        return this._dataSource;
    }
}