import GEvent from "../../GameFrame/event/GEvent";
import { EventTypeEnum } from "../GEnum";
import ProgressRuntime from "./ProgressRuntime";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import GuideManager from "./GuideManager";
import ResManager from "../../gframe-work/manager/ResManager";
import MathTool from "../../gframe-work/tool/MathTool";
import GameDataManager from "../data/GameDataManager";


export default class FortBase extends Laya.Script3D {
    public hp: number = 0;
    public fullHp: number = 0;
    public physicsCollider: Laya.PhysicsCollider = null;
    public gameObject: Laya.Sprite3D = null;
    public type: number = 0;
    public isDeath: boolean = false;
    public isPlaced: boolean = true;
    public bloodProgress: ProgressRuntime = null;
    public baseLevelLocal: LocalStorageData = null;
    public curLevelLocal: LocalStorageData = null;
    public baseHpLocal: LocalStorageData = null;
    public upHpLocal: LocalStorageData = null;
    public baseAttackRecord: number[] = null;
    public baseAni: Laya.Animator = null;
    public bornPoints: Laya.Sprite3D = null;
    public fire: Laya.Sprite3D = null;
    recoverySpeed: number = 0;
    onAwake() {
        this.baseLevelLocal = DataManager.get<LocalStorageData>('baseLevel');
        this.curLevelLocal = DataManager.get<LocalStorageData>('curLevel');
        this.baseHpLocal = DataManager.get<LocalStorageData>('wallBaseHp');
        this.upHpLocal = DataManager.get<LocalStorageData>('wallUpHp');
        this.gameObject = this.owner as Laya.Sprite3D;
        this.physicsCollider = this.gameObject.getComponent(Laya.PhysicsCollider);
        Laya.timer.frameLoop(1, this, this.onFrameLoop);
    }

    onFrameLoop() {
        if (this.bloodProgress) {
            if (this.recoverySpeed > 0) {
                if (this.hp < this.fullHp) {
                    this.hp += this.recoverySpeed * Laya.timer.delta / 1000;
                    this.hp = this.hp < this.fullHp ? this.hp : this.fullHp;
                    this.bloodProgress.progress = this.hp / this.fullHp;
                }
            }
        }
    }

    onDestroy() {
    }

    init(type: number, baseAttackRecord: number[]) {
        Laya.Tween.clearAll(this);
        this.baseAttackRecord = baseAttackRecord;
        this.type = type;
        if (type == 0) {
            this.bloodProgress.white.skin = 'game/Ui_White.png';
            //this.fullHp = this.hp = (this.baseLevelLocal.value - 1) * this.upHpLocal.value + this.baseHpLocal.value;
            this.fullHp = this.hp = GameDataManager.instance.getPropInfo(1002).value + this.baseHpLocal.value;
            this.recoverySpeed = GameDataManager.instance.getPropInfo(1003).value;
        } else if (type == 1) {
            this.bloodProgress.white.skin = 'game/Ui_Red.png';
            let baseLevel = 1;
            if (this.curLevelLocal.value <= 10) {
                baseLevel = Math.ceil(this.curLevelLocal.value / 2);
            } else {
                baseLevel = Math.ceil(10 / 2) + this.curLevelLocal.value - 10;
            }
            if (GameDataManager.instance.isPVP) {
                let division = DataManager.get<LocalStorageData>('curDivision');
                baseLevel = (division.value - 1) * 3 + MathTool.getRandom(1, 3);
            }
            baseLevel = baseLevel < 999 ? baseLevel : 999;
            this.fullHp = this.hp = (baseLevel - 1) * this.upHpLocal.value + this.baseHpLocal.value;
        }
        this.bloodProgress.fullValue = this.fullHp;
        this.bloodProgress.progress = 1;
        this.bloodProgress.visible = true;
        this.isDeath = false;
        if (!this.fire) {
            let gameScene = Laya.stage.getChildByName("game");
            this.fire = gameScene.addChild(ResManager.getRes<any>(`CastleFire${this.type + 1}.lh`)) as Laya.Sprite3D;
        }
        this.setFire(1);
    }

    onHurt(value: number) {
        if (this.isDeath || GameDataManager.instance.isGamePause) return;
        if (this.hp == this.fullHp && this.type == 1) {
            GEvent.event('EnemyUseFireBall');
        }
        if (this.hp == this.fullHp && this.type == 0 && GuideManager.Instance.guideData && GuideManager.Instance.guideData.task == "使用火球术提示语") {
            GuideManager.Instance.showGuide('使用火球术提示语', null);
            GameDataManager.instance.isGamePause = true;
        }
        this.hp -= value;
        if (this.hp <= 0) {
            this.hp = 0;
            this.isDeath = true;
            if (!GameDataManager.instance.isGameOver) {
                GameDataManager.instance.isGameOver = true;
                GEvent.event(EventTypeEnum.BaseExplode, this.type);
            }
        } else {

        }
        this.bloodProgress.progress = this.hp / this.fullHp;

        this.setFire(this.bloodProgress.progress)
        this.playDamageAni();
    }

    playDamageAni() {
        if (this.baseAni) {
            if (this.type == 0) {
                this.baseAni.play('SceneGetHit1', 0, 0);
            } else if (this.type == 1) {
                this.baseAni.play('SceneGetHit2', 0, 0);
            }
        }
    }

    addExplode(roadIndex: number, explodeName: string) {
        let explode = ResManager.getRes<any>(explodeName).clone();
        let clearNode = Laya.stage.getChildByName('effect').getChildByName('clearNode');
        clearNode.addChild(explode);
        let explodePos = (<Laya.Sprite3D>this.bornPoints.getChildAt(roadIndex)).transform.position.clone();
        if (this.type == 0) {
            explodePos.z += 2;
        } else {
            explodePos.z -= 2;
        }
        explodePos.Y += 1;
        explode.transform.position = explodePos;
        Laya.timer.once(3000, this, () => {
            explode.destroy();
        })
    }

    setFire(progress: number) {
        let emissionRate = 0;
        if (progress <= 0.1) {
            emissionRate = 90;
        } else if (progress <= 0.2) {
            emissionRate = 60
        } else if (progress <= 0.4) {
            emissionRate = 30
        }
        if (this.fire) {
            for (let i = 0; i < this.fire.numChildren; i++) {
                let partical = this.fire.getChildAt(i) as Laya.ShuriKenParticle3D;
                partical.particleSystem.emission.emissionRate = emissionRate;
            }
        }
    }
} 