import { ActorBaseData } from "../../GameFrame/actor/ActorBaseData";

export class RoleData extends ActorBaseData {

    attack: number = 30;
    attackDelay: number = 0;
    attackDistance: number = 3;
    moveSpeed: number = 1;
    def: number = 0;
    type: number = 0;

    constructor(data: any = null) {
        super(data)
        if (data) {
            this.attack = data.atk;
            this.hp = data.hp;
            this.attackDistance = data.atkRange;
            this.moveSpeed = data.speed;
            this.attackDelay = data.atkgap;
            this.def = data.def;
        }
    }

    clone() {
        let cloneData = new RoleData();
        cloneData.attack = this.attack
        cloneData.hp = this.hp
        cloneData.attackDistance = this.attackDistance
        cloneData.moveSpeed = this.moveSpeed
        cloneData.attackDelay = this.attackDelay
        cloneData.def = this.def
        cloneData.type = this.type;
        return cloneData;
    }
}