import { ui } from "../../ui/layaMaxUI";


export default class ProgressRuntime extends ui.bloodUI {
    private _progress: number = 0;
    private _fullValue: number = 0;
    private _barwidth: number = 0;
    onAwake() {
        this._barwidth = this.bar.width;
    }

    public set progress(value: number) {
        if (this.hpLabel) {
            this.hpLabel.text = `${Math.floor(value * this._fullValue)}/${this._fullValue}`;
        }
        this._progress = value;
        this.bar.width = this._barwidth * this._progress;
        Laya.Tween.to(this.white, { width: this.bar.width }, 1400, Laya.Ease.sineOut);
    }

    public get progress() {
        return this._progress;
    }

    public set fullValue(value: number) {
        this._fullValue = value;
        this.hpLabel.text = `${this._fullValue}/${this._fullValue}`;
    }

    onDestroy() {
        Laya.Tween.clearAll(this);
    }
}