import { ui } from "../../ui/layaMaxUI";

import GEvent from "../../GameFrame/event/GEvent";
import { EventTypeEnum, SceneName } from "../GEnum";
import FightCellRuntime from "./FightCellRuntime";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import GuideManager from "./GuideManager";
import ComposeShowRuntime from "./ComposeShowRuntime";
import ResManager from "../../gframe-work/manager/ResManager";
import MathTool from "../../gframe-work/tool/MathTool";
import CardManager from "./CardManager";

export default class ComposeRuntime extends ui.composeUI {

    curGroupDataLocal: LocalStorageData = null;
    curGroupNumberLocal: LocalStorageData = null;
    curLevelLocal: LocalStorageData = null;
    diamondLocal: LocalStorageData = null;
    goldLocal: LocalStorageData = null;
    houseLocal: LocalStorageData = null;
    newCardIdLocal: LocalStorageData = null;
    diamondCost: number = 0;
    goldCost: number = 0;
    cellArray: FightCellRuntime[] = [];
    cellTipArray: Laya.Image[] = [];
    curIndex: number = 0;
    newCard: any = null;
    isSelect: boolean = false;
    isComposing: boolean = false;
    flyCount: number = 0;
    isDiamondCompose: boolean = false;
    composeTipTween: Laya.Tween = null;

    onAwake() {
        this.houseList.vScrollBarSkin = '';
        this.curGroupNumberLocal = DataManager.get<LocalStorageData>(`curGroup`);
        this.curGroupDataLocal = DataManager.get<LocalStorageData>(`group${this.curGroupNumberLocal.value}`);
        this.curLevelLocal = DataManager.get<LocalStorageData>('curLevel');
        this.diamondLocal = DataManager.get<LocalStorageData>('diamond');
        this.goldLocal = DataManager.get<LocalStorageData>('gold');
        this.houseLocal = DataManager.get<LocalStorageData>('house');
        this.newCardIdLocal = DataManager.get<LocalStorageData>('newCardId');
        this.houseList.mouseHandler = Laya.Handler.create(this, this.onHouseHandler, null, false);
        this.houseList.scrollBar.changeHandler = Laya.Handler.create(this, this.onScrollBarHandler, null, false);
        this.standBtn.on(Laya.Event.CLICK, this, this.selectStore, [0]);
        this.climbBtn.on(Laya.Event.CLICK, this, this.selectStore, [1]);
        this.flyBtn.on(Laya.Event.CLICK, this, this.selectStore, [2]);
        this.registerPageEvent();
        this.calculateDiamondCost();
        this.calculateGoldCost();
        this.diamondCostLabel.text = this.diamondCost.toString();
        this.headCell.dataSource = CardManager.instance.getCardData(-1);
        this.bodyCell.dataSource = CardManager.instance.getCardData(-1);
        this.handCell.dataSource = CardManager.instance.getCardData(-1);
        this.cellArray.push(this.headCell, this.bodyCell, this.handCell);
        this.cellTipArray.push(this.selectTip1, this.selectTip2, this.selectTip3);
        this.headCell.on(Laya.Event.CLICK, this, this.onCellHandler, ['head']);
        this.bodyCell.on(Laya.Event.CLICK, this, this.onCellHandler, ['body']);
        this.handCell.on(Laya.Event.CLICK, this, this.onCellHandler, ['hand']);
        Laya.timer.frameLoop(1, this, this.onFrameLoop);
        this.selectStore(0);
        this.registerCustomEvent();
    }

    onFrameLoop() {
        this.light.x -= 2;
        if (this.light.x <= -612) {
            this.light.x = 0;
        }
    }

    onDestroy() {
        Laya.timer.clear(this, this.onFrameLoop);
        this.cancelCustomEvent();
    }

    onScrollBarHandler(value: number) {
        if (!this.isSelect) return;
        if (value >= 0) {
            if (value >= this.houseList.scrollBar.max) {
                GEvent.event(EventTypeEnum.NextChange, 'hide');
            } else {
                GEvent.event(EventTypeEnum.NextChange, 'show');
            }
        }
    }

    onHouseDataChange() {
        this.selectStore(this.curIndex);
    }

    registerCustomEvent() {
        GEvent.on(EventTypeEnum.CardHouseChange, this, this.onHouseDataChange);
    }

    cancelCustomEvent() {
        GEvent.off(EventTypeEnum.CardHouseChange, this, this.onHouseDataChange);
    }

    onStoreTabHandler(e: Laya.Event, index: number) {
        if (this.isComposing) return;
        if (e.type == Laya.Event.CLICK) {
            ResManager.playSound('Click.mp3');
            if (this.curIndex == index) return;
            this.selectStore(index);
        }
    }

    /**合成牌点击 */
    onCellHandler(name: string, e: Laya.Event) {
        if (e.type = Laya.Event.CLICK) {
            ResManager.playSound('Click.mp3');
            let cell: FightCellRuntime = null;
            if (name == 'head') {
                cell = this.cellArray[0];
            } else if (name == 'body') {
                cell = this.cellArray[1];
            } else if (name == 'hand') {
                cell = this.cellArray[2];
            }

            if (cell.isTweening || cell.dataSource == null || cell.dataSource.id == 0) {
                return;
            }

            if (cell.originalIndex != -1) {
                let originalCell: FightCellRuntime = <any>this.houseList.getCell(cell.originalIndex);
                if (originalCell) {
                    originalCell.bg.disabled = false;
                }
            }

            this.removeOrAddData(JSON.parse(JSON.stringify(cell.dataSource)), false);
            let cellClone = cell.clone();
            let pos = cell.localToGlobal(new Laya.Point(), false, this.cardView);
            cellClone.pos(pos.x, pos.y);
            this.cardView.addChild(cellClone);
            this.flyCenter(cellClone);

            cell.setEmpty();
            this.updateSelectTip();
        }
    }

    onHouseHandler(e: Laya.Event, index: number) {
        if (this.isComposing) return;

        if (e.type == Laya.Event.CLICK) {
            ResManager.playSound('Click.mp3');

            let houseCell: FightCellRuntime = <any>this.houseList.getCell(index);
            let originalCell: FightCellRuntime = null;
            let outIndex: number = -1;
            for (let i = 0; i < this.cellArray.length; i++) {
                let cell: FightCellRuntime = this.cellArray[i];
                if (cell.dataSource.id == houseCell.dataSource.id) {
                    GEvent.event(EventTypeEnum.SendTip, [['已选择!']]);
                    return
                } else {
                    if (cell.dataSource.id == 0 && outIndex == -1) {
                        originalCell = <any>this.houseList.getCell(index);
                        outIndex = i;
                        originalCell.bg.disabled = true;
                    }
                }
            }

            if (originalCell) {
                if (originalCell.isTweening) return;
                GuideManager.Instance.finishTask("点击第一张合成卡牌");
                let cell: FightCellRuntime = <any>this.cellArray[outIndex];
                cell.cardBlack.visible = false;
                let cellClone = originalCell.clone();
                let pos = originalCell.localToGlobal(new Laya.Point());
                cellClone.anchorX = 0.5;
                cellClone.anchorY = 0.5;
                cellClone.pos(pos.x + 0.5 * originalCell.width, pos.y + 0.5 * originalCell.height);
                this.cardView.addChild(cellClone);
                cell.setOutFight(cellClone, index);
                this.removeOrAddData(originalCell.dataSource, true);
                this.updateSelectTip();
                return;
            }
            GEvent.event(EventTypeEnum.SendTip, [['位置已满!']]);
        }
    }

    selectStore(index: number) {
        this.curIndex = index;

        this.standBtn.skin = 'card/Btn_bule.png';
        this.climbBtn.skin = 'card/Btn_bule.png';
        this.flyBtn.skin = 'card/Btn_bule.png';

        let selectDataArray = [];
        if (index == 0) {
            for (let i = 0; i < this.houseLocal.value.length; i++) {
                const data = this.houseLocal.value[i];
                if (data.type == 2) {
                    selectDataArray.push(JSON.parse(JSON.stringify(data)));
                }
            }
            this.standBtn.skin = 'card/Btn_yellow.png';
        } else if (index == 1) {
            for (let i = 0; i < this.houseLocal.value.length; i++) {
                const data = this.houseLocal.value[i];
                if (data.type == 1) {
                    selectDataArray.push(JSON.parse(JSON.stringify(data)));
                }
            }
            this.climbBtn.skin = 'card/Btn_yellow.png';
        } else if (index == 2) {
            for (let i = 0; i < this.houseLocal.value.length; i++) {
                const data = this.houseLocal.value[i];
                if (data.type == 3) {
                    selectDataArray.push(JSON.parse(JSON.stringify(data)));
                }
            }
            this.flyBtn.skin = 'card/Btn_yellow.png';
        }

        this.sortData(selectDataArray);

        this.houseList.array = selectDataArray;
        this.houseList.refresh();

        this.onScrollBarHandler(this.houseList.scrollBar.value);

        for (let index = 0; index < this.houseList.array.length; index++) {
            let cell: FightCellRuntime = <any>this.houseList.getCell(index);
            if (cell) {
                cell.bg.disabled = false;
                cell.cellArray = this.cellArray;
            }
        }

        this.clearComposeCellArray();
        this.updateSelectTip();
    }

    registerPageEvent() {
        this.goldCompose.on(Laya.Event.CLICK, this, this.onMouseEvent, ['goldCompose']);
        this.diamondCompose.on(Laya.Event.CLICK, this, this.onMouseEvent, ['diamondCompose']);
        this.showTip.on(Laya.Event.MOUSE_DOWN, this, this.onMouseEvent, ['showTip']);
        this.showTip.on(Laya.Event.MOUSE_OUT, this, this.onMouseEvent, ['showTip']);
    }

    onMouseEvent(name: string, e: Laya.Event) {
        e.stopPropagation();
        if (this.isComposing) return;
        if (e.type == Laya.Event.CLICK) {
            if (name == 'goldCompose') {
                if (this.goldLocal.value >= this.goldCost) {
                    if (this.cellArray[0].dataSource.id == 0 || this.cellArray[1].dataSource.id == 0 || this.cellArray[2].dataSource.id == 0) {
                        GEvent.event(EventTypeEnum.SendTip, [['卡片不足!']]);
                        ResManager.playSound('Error.mp3');
                        return;
                    }
                    ResManager.playSound('Click.mp3');
                    this.headPos.parent.alpha = 1;
                    this.bodyPos.parent.alpha = 1;
                    this.handPos.parent.alpha = 1;
                    this.isComposing = true;
                    this.flyCount = 0;
                    this.playCardFly();
                    this.isDiamondCompose = false;
                    this.goldLocal.value -= this.goldCost;
                    if (GuideManager.Instance.visible == true) {
                        GuideManager.Instance.finishTask("点击普通合成");
                        GuideManager.Instance.hideGuide();
                    }
                } else {
                    GEvent.event(EventTypeEnum.SendTip, [['金币不足!']]);
                    ResManager.playSound('Error.mp3');
                }
            } else if (name == 'diamondCompose') {
                if (this.diamondLocal.value >= this.diamondCost) {
                    if (this.cellArray[0].dataSource.id == 0 || this.cellArray[1].dataSource.id == 0 || this.cellArray[2].dataSource.id == 0) {
                        GEvent.event(EventTypeEnum.SendTip, [['卡片不足!']]);
                        ResManager.playSound('Error.mp3');
                        return;
                    }
                    this.headPos.parent.alpha = 1;
                    this.bodyPos.parent.alpha = 1;
                    this.handPos.parent.alpha = 1;
                    this.isComposing = true;
                    this.flyCount = 0;
                    this.playCardFly();
                    this.isDiamondCompose = true;
                    this.diamondLocal.value -= this.diamondCost;
                    ResManager.playSound('Click.mp3');
                } else {
                    GEvent.event(EventTypeEnum.SendTip, [['钻石不足!']]);
                    ResManager.playSound('Error.mp3');
                }
            }
        }
        if (e.target == this.showTip) {
            if (this.composeTipTween) {
                this.composeTipTween.recover();
                this.composeTipTween = null;
            }
            if (e.type == Laya.Event.MOUSE_DOWN) {
                this.composeTip.visible = true;
                this.composeTipTween = Laya.Tween.to(this.composeTip, { alpha: 1 }, 250, Laya.Ease.sineOut);
            } else if (e.type == Laya.Event.MOUSE_OUT) {
                this.composeTipTween = Laya.Tween.to(this.composeTip, { alpha: 0 }, 250, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
                    this.composeTip.visible = false;
                }))
            }
        }
    }

    onComposeResultClosed() {
        this.blackMask.visible = false;
        this.isComposing = false;
        this.headCell.visible = true;
        this.bodyCell.visible = true;
        this.handCell.visible = true;
        this.removeOrAddData(JSON.parse(JSON.stringify(this.newCard)), false);
    }

    compose(headUnitCard: any, bodyUnitCard: any, footUnitCard: any, isDiamond: boolean) {
        let newCard = CardManager.instance.getCardData(-1);
        newCard.id = this.newCardIdLocal.value;
        this.newCardIdLocal.value++;
        let headArray = headUnitCard.skin.split('-');
        let bodyArray = bodyUnitCard.skin.split('-');
        let footArray = footUnitCard.skin.split('-');
        newCard.skin = `${headArray[0]}-${bodyArray[1]}-${footArray[2]}`;
        newCard.type = headUnitCard.type;
        let cardArr = [headUnitCard, bodyUnitCard, footUnitCard];
        cardArr.sort((a, b) => {
            return a.exp - b.exp;
        })
        newCard.datatype = cardArr[0].datatype;
        newCard.exp = cardArr[0].exp + cardArr[1].exp + cardArr[1].exp;
        if (isDiamond) {
            newCard.exp = Math.floor(newCard.exp * (1 + 1.5));
        }
        this.newCard = newCard;
        let fightLevel = CardManager.instance.getFightLevelByEXP(cardArr[0].exp);
        return CardManager.instance.getFightData(cardArr[0].datatype, fightLevel);
    }

    calculateDiamondCost() {
        this.diamondCost = Math.floor(this.curLevelLocal.value / 50) + 4;
    }

    calculateGoldCost() {
        this.goldCost = 0;
    }

    updateCardStore() {
        let allCardData: any[] = this.houseLocal.value;
        let out = this.curGroupDataLocal.value;
        for (let j = 0; j < 3; j++) {
            let composeCard = this.cellArray[j];
            for (let i = 0; i < allCardData.length; i++) {
                const data = allCardData[i];
                if (data.id == composeCard.dataSource.id) {
                    allCardData.splice(i, 1);
                    break;
                }
            }
            for (let i = 0; i < out.length; i++) {
                const data = out[i];
                if (data.id == composeCard.dataSource.id) {
                    out[i] = CardManager.instance.getCardData(-1);
                    break;
                }
            }
        }

        let doubleArray = [];
        let fourArray = [];
        let flyArray = [];
        for (let i = 0; i < allCardData.length; i++) {
            const data = allCardData[i];
            if (data.type == 1) {
                fourArray.push(data);
            } else if (data.type == 2) {
                doubleArray.push(data);
            } else if (data.type == 3) {
                flyArray.push(data);
            }
        }

        if (this.newCard.type == 1) {
            fourArray.push(this.newCard);
            this.sortData(fourArray);
        } else if (this.newCard.type == 2) {
            doubleArray.push(this.newCard);
            this.sortData(doubleArray);
        } else if (this.newCard.type == 3) {
            flyArray.push(this.newCard);
            this.sortData(flyArray);
        }

        let newAllData = doubleArray.concat(fourArray, flyArray)
        this.houseLocal.value = newAllData;

        this.curGroupDataLocal.value = out;
    }

    clearComposeCellArray() {
        for (let i = 0; i < this.cellArray.length; i++) {
            const cell = this.cellArray[i];
            cell.setEmpty();
        }
    }
    onSelect() {
        this.isSelect = true;
        this.selectStore(this.curIndex);
    }

    /**合成牌飞行 */
    playCardFly() {
        let originalCell: FightCellRuntime = null;
        let destCell: FightCellRuntime = null;
        if (this.flyCount == 0) {
            originalCell = this.headCell;
            destCell = this.headPos;
        } else if (this.flyCount == 1) {
            originalCell = this.bodyCell;
            destCell = this.bodyPos;
        } else if (this.flyCount == 2) {
            originalCell = this.handCell;
            destCell = this.handPos;
        }
        if (originalCell) {
            originalCell.visible = false;
            let cellClone = originalCell.clone();
            let pos = originalCell.localToGlobal(new Laya.Point());
            cellClone.pos(pos.x, pos.y);
            cellClone.anchorX = 0.5;
            cellClone.anchorY = 0.5;
            cellClone.rotation = (<Laya.Image>originalCell.parent).rotation;
            this.cardView.addChild(cellClone);
            destCell.setOutFight(cellClone, 0);
        }
        this.flyCount++;
        if (this.flyCount >= 3) {
            ResManager.playSound('Compose.mp3');
            this.blackMask.visible = true;
            this.blackMask.alpha = 0;
            Laya.Tween.to(this.blackMask, { alpha: 1 }, 300, Laya.Ease.linearNone);
            Laya.timer.once(300, this, () => {
                this.loadSpine();
            })
        } else {
            Laya.timer.once(300, this, () => {
                this.playCardFly();
            })
        }
    }

    loadSpine() {
        return new Promise<void>(resolve => {
            ResManager.loadSpines(['Fusion.sk']).then((spines: Laya.Skeleton[]) => {
                let skeleton = spines[0];
                skeleton.name = 'fusion';
                skeleton.pos(Laya.stage.width / 2, Laya.stage.height / 2);
                let beforData = this.compose(this.cellArray[0].dataSource, this.cellArray[1].dataSource, this.cellArray[2].dataSource, this.isDiamondCompose);
                Laya.Scene.load(SceneName.composeShow, Laya.Handler.create(this, (composeShow: ComposeShowRuntime) => {
                    composeShow.isShowEffect = false;
                    composeShow.closeEffect = null;
                    composeShow.popupEffect = null;
                    composeShow.mouseEnabled = true;
                    composeShow.open(false);
                    composeShow.closeHandler = Laya.Handler.create(this, () => {
                        this.onComposeResultClosed()
                        this.updateCardStore();
                        this.clearComposeCellArray();
                        this.updateSelectTip();
                        GEvent.event(EventTypeEnum.ComposeNewCard);
                    })
                    composeShow.setModel(this.newCard, beforData, skeleton);
                    skeleton.play(0, false);
                }))
                Laya.Tween.to(this.headPos.parent, { alpha: 0 }, 80, Laya.Ease.linearNone);
                Laya.Tween.to(this.bodyPos.parent, { alpha: 0 }, 80, Laya.Ease.linearNone);
                Laya.Tween.to(this.handPos.parent, { alpha: 0 }, 80, Laya.Ease.linearNone);
                resolve();
            })
        })
    }

    removeOrAddData(newData: any, isRemove: boolean) {
        let dataArray = [];
        for (let i = 0; i < this.houseList.array.length; i++) {
            let houseData = this.houseList.array[i];
            if (isRemove) {
                if (houseData && houseData.id > 0 && houseData.id != newData.id) {
                    dataArray.push(houseData);
                }
            } else {
                if (houseData && houseData.id > 0) {
                    dataArray.push(houseData);
                }
            }
        }
        if (!isRemove) {
            dataArray.push(newData);
        }
        this.sortData(dataArray);
        this.houseList.array = dataArray;
        this.houseList.refresh();
    }

    sortData(dataArray: any[]) {
        dataArray.sort((a, b) => {
            return b.attack - a.attack;
        })
    }

    updateSelectTip() {
        let index = -1;
        for (let i = 0; i < this.cellArray.length; i++) {
            let cell = this.cellArray[i];
            if (cell.dataSource.id == 0 && index == -1) {
                index = i;
                this.cellTipArray[i].visible = true;
            } else {
                this.cellTipArray[i].visible = false;
            }
        }
    }

    flyCenter(card: any) {
        let finalX = (this.cardView.width - card.width) / 2;
        let finalY = (this.cardView.height - card.height) / 2;
        Laya.Tween.to(card, { x: finalX, y: finalY, alpha: 0 }, 200, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
            card.destroy();
        }));
    }
}