export default class AnimatorStateScript extends Laya.AnimatorStateScript {
    public name: string;
    public stateEnterHandler: Laya.Handler;
    public stateUpdateHandler: Laya.Handler;
    public stateExitHandler: Laya.Handler;
    constructor() {
        super();
    }
    onStateEnter() {
        if (this.stateEnterHandler)
            this.stateEnterHandler.runWith(this.name);
    }

    onStateUpdate() {
        if (this.stateUpdateHandler)
            this.stateUpdateHandler.runWith(this.name);
    }

    onStateExit() {
        if (this.stateExitHandler)
            this.stateExitHandler.runWith(this.name);
    }
}