import { ui } from "../../ui/layaMaxUI";

export default class DivisionRuntime extends ui.divisionUI {
    private _division: number;
    public set division(value: number) {
        this._division = value;
        let big = Math.ceil(this._division / 3);
        let small = this._division % 3;
        this.icon.skin = `rank/Ui_level${big}.png`;
        this.labelIcon.skin = `rank/Ui_duanwei${big}.png`;
        if (small == 1) {
            this.star1.visible = true;
            this.star2.visible = false;
            this.star3.visible = false;
        } else if (small == 2) {
            this.star1.visible = true;
            this.star2.visible = true;
            this.star3.visible = false;
        } else if (small == 0) {
            this.star1.visible = true;
            this.star2.visible = true;
            this.star3.visible = true;
        }
    }
}