import ResManager from "../../gframe-work/manager/ResManager";
import { ui } from "../../ui/layaMaxUI";
import GameDataManager from "../data/GameDataManager";
import { SceneName } from "../GEnum";

export default class MatchRuntime extends ui.matchUI {
    vsSpine: Laya.Skeleton;
    diSpine: Laya.Skeleton;
    isSkeletonPlaying: boolean = false;
    timeRecord: number = 0;
    seconds: number = 0;
    minutes: number = 0;
    onAwake() {
        this.playerName.text = '自己';
        this.playerScore.text = '100';
        this.playerDivision.division = 1;

        this.playerName.text = window['UserInfo'].nickName;
        this.playerHead.skin = window['UserInfo'].avatarUrl;

        this.enemyName.text = '敌人';
        this.enemyScore.text = '100';
        this.enemyDivision.division = 1;
        this.timeLabel.text = this.dateFtt('mm:ss', { "m+": this.minutes, "s+": this.seconds });
        this.loadSpine2().then(() => {

        })
        Laya.timer.frameLoop(1, this, this.onFrameLoop);
    }

    setEnemyInfo(info: any) {
        this.enemyName.text = info.nickName;
        this.enemyHead.skin = info.avatarUrl;
    }

    onFrameLoop() {
        this.timeRecord += Laya.timer.delta;
        if (this.timeRecord >= 1000) {
            this.timeRecord = 0;
            this.seconds++;
            if (this.seconds == 60) {
                this.seconds = 0;
                this.minutes++;
            }
            this.timeLabel.text = this.dateFtt('mm:ss', { "m+": this.minutes, "s+": this.seconds });
            if (this.seconds >= 5) {
                this.setEnemyInfo({ nickName: 'god', avatarUrl: 'match/Ui_vshead.png' })
                Laya.timer.clear(this, this.onFrameLoop);
                this.loadSpine1().then(() => {
                    GameDataManager.instance.isPVP = true;
                    Laya.Scene.open(SceneName.gameLoad);
                })
            }
        }
    }
    loadSpine1() {
        return new Promise<void>((resolve, reject) => {
            ResManager.loadSpines(['vs.sk']).then((spines: Laya.Skeleton[]) => {
                this.vsSpine = spines[0];
                this.spineFront.addChild(this.vsSpine);
                this.vsSpine.scale(1.2, 1.2);
                this.vsSpine.pos(Laya.stage.width / 2, Laya.stage.height / 2);
                this.vsSpine.play(0, false);
                this.isSkeletonPlaying = true;
                this.vsSpine.on(Laya.Event.STOPPED, this, () => {
                    this.isSkeletonPlaying = false;
                    resolve();
                });
            })
        })
    }
    loadSpine2() {
        return new Promise<void>((resolve, reject) => {
            ResManager.loadSpines(['di.sk']).then((spines: Laya.Skeleton[]) => {
                this.diSpine = spines[0];
                this.spineBack.addChild(this.diSpine);
                this.diSpine.scale(1.2, 1.2);
                this.diSpine.pos(Laya.stage.width / 2, Laya.stage.height / 2);
                this.diSpine.play(0, false);
                this.isSkeletonPlaying = true;
                this.diSpine.on(Laya.Event.STOPPED, this, () => {
                    this.isSkeletonPlaying = false;
                });
                resolve();
            })
        })
    }


    dateFtt(fmt: string, o: { "m+": number, "s+": number }) { //author: meizz   
        for (var k in o)
            if (new RegExp("(" + k + ")").test(fmt))
                fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
        return fmt;
    }

    onDestroy() {
        Laya.timer.clearAll(this);
    }
}