import { ActorBaseData } from "./ActorBaseData";

export class ActorState {
    public static none = 'None';
    public static stand = 'Stand';
    public static death = 'Death';
    public static damage = 'Damage';
    public static run = 'Run';
    public static attack = 'Attack';
}

export class ActorBase extends Laya.Script3D {

    public gameObject: Laya.Sprite3D = null;
    protected animator: Laya.Animator = null;
    protected physicsCollider: Laya.PhysicsCollider = null;
    protected rigidbody3D: Laya.Rigidbody3D = null;
    protected curState: string = ActorState.none;
    public isDeath: boolean = false;
    public actorData: ActorBaseData = null;

    onAwake() {
        this.gameObject = this.owner as Laya.Sprite3D;
        this.animator = this.gameObject.getComponent(Laya.Animator);
        this.physicsCollider = this.gameObject.getComponent(Laya.PhysicsCollider);
        this.rigidbody3D = this.gameObject.getComponent(Laya.Rigidbody3D);
        if (!this.physicsCollider && !this.rigidbody3D) {
            //console.error('未添加碰撞');
        }
        this.registerCustomEvent();
    }

    onUpdate() {
        if (this.isDeath) return;
        this.onActorStateStay(this.curState);
    }

    onDestroy() {
        this.cancelCustomEvent();
    }

    onCollisionEnter(collision: Laya.Collision): void { }
    onCollisionStay(collision: Laya.Collision): void { }
    onCollisionExit(collision: Laya.Collision): void { }

    onTriggerEnter(other: Laya.PhysicsComponent): void { }
    onTriggerStay(other: Laya.PhysicsComponent): void { }
    onTriggerExit(other: Laya.PhysicsComponent): void { }

    init(actorData: ActorBaseData) {
        this.actorData = actorData;
    }

    Ani_Attack(param1: number, param2: number, param3: string): void {
        this.onAttack(param1, param2, param3);
    }
    Ani_AttackEnd(param1: number, param2: number, param3: string): void {
        this.onAttackEnd(param1, param2, param3);
    }
    onAttack(param1: number, param2: number, param3: string): void { }
    onAttackEnd(param1: number, param2: number, param3: string): void { }

    onActorStateEnter(state: string): void { }
    onActorStateStay(state: string): void { }
    onActorStateExit(state: string): void { }

    changeActorState(newState: string, id: string) {
        if (newState == this.curState) return
        this.onActorStateExit(this.curState);
        this.onAnimationChange(newState + id);
        this.onActorStateEnter(newState);
        this.curState = newState;
    }

    onAnimationChange(name: string) {
        if (this.animator) this.animator.crossFade(name, 0.1, 0, 0);
    }

    registerCustomEvent(): void { }

    cancelCustomEvent(): void { }

    onHurt(value: number) {
        if (this.isDeath) return;
        this.actorData.hp -= value;
        if (this.actorData.hp <= 0) {
            this.actorData.hp == 0;
            this.isDeath = true;
            this.changeActorState(ActorState.death, '');
        } else {
            this.changeActorState(ActorState.damage, '');
        }
    }
}