export default class MathTool {
    /**
     * 保留指定位数小数点
     * @param num 源数值
     * @param fractionDigits 保留小数点位数
     */
    static fixNumber(num: number, fractionDigits: number) {
        if (!num) return 0;
        if (num.toString().indexOf(".") != -1) {
            return Number(num.toFixed(fractionDigits));
        }
        return num;
    }
    /**
     * 获取区间随机数[min,max]
     * @param min 最小数
     * @param max 最大数
     */
    static getRandom(min: number, max: number) {
        return Math.floor(min + Math.random() * (max - min + 1));
    }
}