import PlatFormManager from "./PlatFormManager";

export default class ResManager {
    private static _instance: ResManager = null;
    /**
     * 资源管理中心
     */
    public static init() {
        return new Promise<void>(resolve => {
            if (ResManager._instance == null) {
                ResManager._instance = new ResManager;
                let zipVersion = Laya.LocalStorage.getItem("zipVersion");
                if (zipVersion) {
                    ResManager._zipVersion = JSON.parse(zipVersion);
                }
                let resconfigUrl = 'resconfig.json';
                if (ResManager._remote.length > 0) {
                    resconfigUrl = ResManager._remote + 'resconfig.json' + "?ran=" + Math.random() * 1000000
                }
                Laya.loader.load(resconfigUrl, Laya.Handler.create(this, (resconfig: any) => {
                    if (typeof resconfig == "string") resconfig = JSON.parse(resconfig);
                    for (const key in resconfig) {
                        if (resconfig.hasOwnProperty(key)) {
                            let respackage = resconfig[key];
                            let files = respackage[0];
                            for (let i = 0; i < files.length; i++) {
                                let file = files[i];
                                if (respackage[1] == 'local' || respackage[1] == 'sub') {
                                    let url = 'res/' + key + "/" + file;
                                    if (PlatFormManager.CUR_PLATFORM == PlatFormManager.OPPO && respackage[1] == 'sub') {
                                        url = key + "/" + file;
                                    }
                                    ResManager._resMap.set(file, new Res(file, key, respackage[1], url));
                                } else {
                                    let url = ResManager._resBasePath + file;
                                    ResManager._resMap.set(file, new Res(file, key + "_" + respackage[2], respackage[1], url));
                                }
                            }
                        }
                    }
                    console.log(ResManager._resMap);
                    resolve();
                }))
            }
        })
    }
    private static _fileSystemManager: any = null;
    private static _resBasePath: string = null;
    private static _downloadFile: any = null;
    private static _loadSubpackage: any = null;
    private static _remote: string = "";
    private static _resMap: Map<string, Res> = null;
    private static _loadedSubNames: string[] = [];
    private static _zipVersion: object = {};
    private constructor() {
        ResManager._resMap = new Map;
        if (PlatFormManager.CUR_PLATFORM == PlatFormManager.WX) {
            ResManager._remote = "https://cdn.xxtch.com/monsterwx/";
            ResManager._resBasePath = (<any>wx).env.USER_DATA_PATH + '/cache/res/';
            ResManager._fileSystemManager = (<any>wx).getFileSystemManager();
            ResManager._downloadFile = (<any>wx).downloadFile;
            ResManager._loadSubpackage = (<any>wx).loadSubpackage;
            ResManager._fileSystemManager.access({
                path: ResManager._resBasePath,
                fail: () => {
                    ResManager._fileSystemManager.mkdirSync(ResManager._resBasePath, true);
                }
            })
        } else if (PlatFormManager.CUR_PLATFORM == PlatFormManager.OPPO || PlatFormManager.CUR_PLATFORM == PlatFormManager.VIVO) {
            ResManager._remote = "https://cdn.xxtch.com/monster/";
            ResManager._resBasePath = (<any>qg).env.USER_DATA_PATH + '/cache/res/';
            ResManager._fileSystemManager = (<any>qg).getFileSystemManager();
            ResManager._downloadFile = (<any>qg).downloadFile;
            ResManager._loadSubpackage = (<any>qg).loadSubpackage;
            ResManager._fileSystemManager.access({
                path: ResManager._resBasePath,
                fail: () => {
                    ResManager._fileSystemManager.mkdirSync(ResManager._resBasePath, true);
                }
            })
        }
    }
    /**
     * 获取资源
     * @param name 资源名
     */
    static getRes<T>(name: string): T {
        return Laya.loader.getRes(ResManager._resMap.get(name).url);
    }
    /**
     * 获取资源克隆
     * @param name 资源名
     */
    static getResClone<T>(name: string): T {
        return Laya.loader.getRes(ResManager._resMap.get(name).url).clone();
    }
    /**
     * 下载资源包
     */
    static download(names: string[], progressCb: (res: { type: string, progress: number, location?: string, name?: string, totalBytesWritten?: number, totalBytesExpectedToWrite?: number }) => void = null) {
        return new Promise<void>(resolve => {
            let subnames = [];
            let zipnames = [];
            for (let j = 0; j < names.length; j++) {
                let name = names[j];
                let res = ResManager._resMap.get(name);
                if (!res) {
                    console.error("找不到资源", name);
                }
                if (res.location == 'sub') {
                    if (subnames.indexOf(res.bundleName) == -1) {
                        if (ResManager._loadedSubNames.indexOf(res.bundleName) == -1) {
                            subnames.push(res.bundleName);
                        }
                    }
                } else if (res.location == 'net') {
                    if (zipnames.indexOf(res.bundleName) == -1) {
                        let dirname = res.bundleName.split("_")[0];
                        let version = res.bundleName.split("_")[1];
                        if (ResManager._zipVersion[dirname] != version) {
                            zipnames.push(res.bundleName);
                        }
                    }
                }
            }
            ResManager._loadSubpackages(subnames, progressCb).then(() => {
                ResManager._downloadFiles(zipnames, progressCb).then(() => {
                    resolve();
                })
            })
        })
    }
    /**
   * 创建资源
   */
    static create(names: string[], progressCb: (res: { type: string, progress: number, location?: string, name?: string, totalBytesWritten?: number, totalBytesExpectedToWrite?: number }) => void = null) {
        return new Promise<void>(resolve => {
            ResManager.download(names, progressCb).then(() => {
                let ress = [];
                for (let j = 0; j < names.length; j++) {
                    ress.push(ResManager._resMap.get(names[j]));
                }
                ResManager._createRes(ress, ress.length, progressCb).then(() => {
                    resolve();
                })
            })
        })
    }
    private static _createRes(ress: Res[], originalLength: number, progressCb: (res: { type: string, progress: number, location?: string, name?: string, totalBytesWritten?: number, totalBytesExpectedToWrite?: number }) => void = null) {
        return new Promise<void>(resolve => {
            if (ress.length > 0) {
                let res = ress.shift();
                let baseName = res.name.split(".")[0];
                let progressVal = (originalLength - ress.length - 1) / originalLength;
                progressCb && progressCb({ type: "create", progress: progressVal, name: baseName });
                Laya.loader.create(res.url, Laya.Handler.create(this, () => {
                    progressVal = (originalLength - ress.length) / originalLength;
                    progressCb && progressCb({ type: "create", progress: progressVal, name: baseName });
                    ResManager._createRes(ress, originalLength, progressCb).then(() => {
                        resolve();
                    })
                }), Laya.Handler.create(this, (value: number) => {
                    progressCb && progressCb({ type: "create", progress: progressVal, name: baseName });
                }))
            } else {
                resolve();
            }
        })
    }
    /**
     * 加载骨骼动画
     * @param names 
     */
    static loadSpines(names: string[]) {
        return new Promise(resolve => {
            let spines = [];
            if (names && names.length > 0) {
                let name = names.shift();
                ResManager.download([name]).then(() => {
                    let skeleton = new Laya.Skeleton();
                    spines.push(skeleton);
                    skeleton.load(ResManager._resMap.get(name).url, Laya.Handler.create(this, () => {
                        ResManager.loadSpines(names).then((spine: Laya.Skeleton[]) => {
                            resolve(spines.concat(spine));
                        })
                    }));
                })
            } else {
                resolve(spines);
            }
        });
    }
    /**
     * 加载字体
     * @param names 
     */
    static loadFonts(names: string[]) {
        return new Promise<void>(resolve => {
            if (names && names.length > 0) {
                let name = names.shift();
                ResManager.download([name]).then(() => {
                    let font: Laya.BitmapFont = new Laya.BitmapFont();
                    font.loadFont(ResManager._resMap.get(name).url, Laya.Handler.create(this, (bitmapFont: Laya.BitmapFont) => {
                        bitmapFont.setSpaceWidth(10);
                        bitmapFont.autoScaleSize = true;
                        Laya.Text.registerBitmapFont(name.split(".")[0], bitmapFont);
                        ResManager.loadFonts(names).then(() => {
                            resolve();
                        })
                    }, [font]));
                })
            } else {
                resolve();
            }
        });
    }
    /**
     * 播放音效
     * @param url 
     * @param loops 
     * @param complete 
     * @param soundClass 
     * @param startTime 
     */
    static playSound(url: string, loops: number = 1, complete: laya.utils.Handler = null, soundClass: new () => any = null, startTime: number = 0) {
        ResManager.download([url]).then(() => {
            return Laya.SoundManager.playSound(ResManager._resMap.get(url).url, loops, complete, soundClass, startTime);
        })
    }
    /**
     * 播放音乐
     * @param url 
     * @param loops 
     * @param complete 
     * @param startTime 
     */
    static playMusic(url: string, loops: number = 0, complete: laya.utils.Handler = null, startTime: number = 0) {
        ResManager.download([url]).then(() => {
            return Laya.SoundManager.playMusic(ResManager._resMap.get(url).url, loops, complete, startTime);
        })
    }
    private static _loadSubpackages(names: string[], progressCb: (res: { type: string, progress: number, location?: string, name?: string, totalBytesWritten?: number, totalBytesExpectedToWrite?: number }) => void = null) {
        return new Promise<void>(resolve => {
            if (names.length > 0) {
                let name = names.shift();
                let progressVal = 0;
                progressCb && progressCb({ type: "download", progress: progressVal, location: "sub", name: name });
                const loadTask = ResManager._loadSubpackage({
                    name: name,
                    success: (res: any) => {
                        ResManager._loadedSubNames.push(name);
                        progressVal = 1;
                        progressCb && progressCb({ type: "download", progress: progressVal, location: "sub", name: name });
                        ResManager._loadSubpackages(names, progressCb).then(() => {
                            resolve();
                        })
                    },
                    fail: (res: any) => { console.log("loadSubpackageFail", res) }
                })
                loadTask.onProgressUpdate((res: any) => {
                    progressVal = res.progress > progressVal ? res.progress : progressVal;
                    progressVal = progressVal < 1 ? progressVal : 1;
                    progressCb && progressCb({ type: "download", progress: progressVal, location: "sub", name: name, totalBytesWritten: res.totalBytesWritten, totalBytesExpectedToWrite: res.totalBytesExpectedToWrite });
                })
            } else {
                resolve();
            }
        })
    }

    private static _downloadFiles(names: string[], progressCb: (res: { type: string, progress: number, location?: string, name?: string, totalBytesWritten?: number, totalBytesExpectedToWrite?: number }) => void = null) {
        return new Promise<void>(resolve => {
            if (names.length > 0) {
                let name = names.shift();
                let dirname = name.split("_")[0];
                let version = name.split("_")[1];
                let progressVal = 0;
                progressCb && progressCb({ type: "download", progress: progressVal, location: "net", name: dirname });
                let zipPath = ResManager._resBasePath + name + ".zip";
                const loadTask = ResManager._downloadFile({
                    url: ResManager._remote + name + ".zip",
                    filePath: zipPath,
                    success: (res: any) => {
                        if (res.statusCode === 200) {
                            ResManager._fileSystemManager.unzip({
                                zipFilePath: zipPath,
                                targetPath: ResManager._resBasePath,
                                success: (res: any) => {
                                    ResManager._zipVersion[dirname] = version;
                                    Laya.LocalStorage.setItem("zipVersion", JSON.stringify(ResManager._zipVersion));
                                    progressVal = 1;
                                    progressCb && progressCb({ type: "download", progress: progressVal, location: "net", name: dirname });
                                    ResManager._fileSystemManager.unlink({ filePath: zipPath })
                                    ResManager._downloadFiles(names, progressCb).then(() => {
                                        resolve();
                                    })
                                },
                                fail: (res: any) => { }
                            });
                        }
                    },
                    fail: (res: any) => { }
                })
                loadTask.onProgressUpdate((res: any) => {
                    let curProgressVal = res.progress / 100;
                    progressVal = curProgressVal > progressVal ? curProgressVal : progressVal;
                    progressVal = progressVal < 1 ? progressVal : 1;
                    progressCb && progressCb({ type: "download", progress: progressVal, location: "net", name: dirname });
                })
            } else {
                resolve();
            }
        })
    }
}
class Res {
    private _location: string = null;
    private _bundleName: string = null;
    private _url: string = null;
    private _name: string = null;
    constructor(name: string, bundleName: string, location: string, url: string) {
        this._name = name;
        this._location = location;
        this._bundleName = bundleName;
        this._url = url;
    }
    /**资源所在位置 */
    get location() {
        return this._location;
    }
    /**资源包的名字 */
    get bundleName() {
        return this._bundleName;
    }
    /**资源路径 */
    get url() {
        return this._url;
    }
    /**资源名 */
    get name() {
        return this._name;
    }
}