export default class LayaTool {
    /**
     * 根据路径找到节点
     * @param parent 父节点
     * @param url 节点路径
     */
    static findNodeByUrl<T>(parent: any, url: string): T {
        let names = url.split('/');
        let node = parent;
        for (let i = 0; i < names.length; i++) {
            node = node.getChildByName(names[i]);
        }
        return <unknown>node as T;
    }
    /**
     * 根据名字找到节点
     * @param parent 父节点
     * @param name 节点名字
     */
    static findNodeByName<T>(parent: any, name: string): T {
        for (let i = 0; i < parent.numChildren; i++) {
            let child = parent.getChildAt(i);
            if (child.name == name) {
                return <unknown>child as T
            } else {
                return this.findNodeByName(child, name);
            }
        }
    }
    /**
    * 根据类型找到节点
    * @param parent 父节点
    * @param name 节点名字
    */
    static findNodeByType<T>(parent: any, type: { new(): T; }): T {
        for (let i = 0; i < parent.numChildren; i++) {
            let child = parent.getChildAt(i);
            if (child instanceof type) {
                return child;
            } else {
                return this.findNodeByType(child, type);
            }
        }
    }
    /**
   * 根据类型找到所有节点
   * @param parent 父节点
   * @param name 节点名字
   */
    static findNodesByType<T>(parent: any, type: { new(): T; }): T[] {
        let nodes = [];
        for (let i = 0; i < parent.numChildren; i++) {
            let child = parent.getChildAt(i);
            if (child instanceof type) {
                nodes.push(child);
            }
            if (child.numChildren > 0) {
                nodes = nodes.concat(this.findNodeByType(child, type));
            }
        }
        return nodes;
    }
    /**
     * 获取节点上的组件
     * @param node 节点
     */
    static getComponent<T>(node: any, componentType: typeof laya.components.Component): T {
        return node.getComponent(componentType);
    }

    static getComponents<T>(node: any, componentType: typeof laya.components.Component): T[] {
        return node.getComponents(componentType);
    }

    static getComponentInChildren<T>(node: any, componentType: typeof laya.components.Component): T {
        for (let i = 0; i < node.numChildren; i++) {
            let child = node.getChildAt(i);
            let component = this.getComponent<T>(child, componentType);
            if (component) {
                return component;
            } else {
                return this.getComponentInChildren<T>(child, componentType);
            }
        }
    }

    static getComponentsInChildren<T>(node: any, componentType: typeof laya.components.Component) {
        for (let i = 0; i < node.numChildren; i++) {
            let child = node.getChildAt(i);
            let components = this.getComponents<T>(child, componentType);
            if (components) {
                return components;
            } else {
                return this.getComponentsInChildren<T>(child, componentType);
            }
        }
    }
    /**3d物体的缓动 */
    static tweenTo(target: any, props: any, duration: number, ease?: Function, complete?: laya.utils.Handler) {
        let tweenObj = {};
        for (const key in props) {
            if (Object.prototype.hasOwnProperty.call(props, key)) {
                tweenObj[key + 'Start'] = target.transform[key].clone();
                let diff = new Laya.Vector3();
                Laya.Vector3.subtract(props[key], target.transform[key], diff);
                tweenObj[key + 'Diff'] = diff;
            }
        }
        let tweenTarget = { value: 0 };
        return Laya.Tween.to(tweenTarget, {
            value: 1, update: Laya.Handler.create(this, () => {
                for (const key in props) {
                    if (Object.prototype.hasOwnProperty.call(props, key)) {
                        let now = new Laya.Vector3();
                        let scaleDiff = new Laya.Vector3();
                        Laya.Vector3.scale(tweenObj[key + 'Diff'], tweenTarget.value, scaleDiff);
                        Laya.Vector3.add(tweenObj[key + 'Start'], scaleDiff, now);
                        target.transform[key] = now;
                    }
                }
            }, null, false)
        }, duration, ease, complete);
    }

    static projectPos(camera: Laya.Camera, pos: Laya.Vector3) {
        let out = new Laya.Vector4;
        camera.viewport.project(pos, camera.projectionViewMatrix, out);
        return out;
    }
}