import { ui } from "../../ui/layaMaxUI";
import { SceneName } from "../GEnum";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import BuyConfirmRuntime from "./BuyConfirmRuntime";
import GuideManager from "./GuideManager";
import ResManager from "../../gframe-work/manager/ResManager";

export default class ShopCellRuntime extends ui.shopCellUI {
    curLevelLocal: LocalStorageData = null;
    onAwake() {
        this.curLevelLocal = DataManager.get<LocalStorageData>('curLevel');
        this.cell.on(Laya.Event.CLICK, this, this.onCellClick);
    }

    onMouseEvent() {

    }

    onCellClick() {
        ResManager.playSound('Open.mp3');
        if (this.cell.dataSource.type == 2) {
            GuideManager.Instance.finishTask("点击第一张双足卡片", 500);
        }
        Laya.Scene.load(SceneName.buyConfirm, Laya.Handler.create(this, (page: BuyConfirmRuntime) => {
            page.cell.dataSource = this.cell.dataSource;
            page.cell.cardBlack.visible = false;
            page.cost = this.cell.fightData.price;
            page.moneyLabel.text = this.cell.fightData.price.toString();
            page.hpLabel.text = this.cell.fightData.hp;
            page.attackLabel.text = this.cell.fightData.atk;
            page.delayLabel.text = (Math.floor((2000 - this.cell.fightData.atkgap) / 10)).toString();
            page.speedLabel.text = this.cell.fightData.speed;
            page.frameTitle.visible = false;
            page.buyBtn.visible = true;

            let titleImage = '';
            if (this.cell.dataSource.type == 1) {
                titleImage = 'shop/Ui_BuyFont2.png';
            } else if (this.cell.dataSource.type == 2) {
                titleImage = 'shop/Ui_BuyFont1.png';
            } else if (this.cell.dataSource.type == 3) {
                titleImage = 'shop/Ui_BuyFont3.png';
            }
            page.title.skin = titleImage;
            page.open();
        }))
    }

    public set dataSource(data: any) {
        if (!data) return
        this.cell.dataSource = data;
        let offset = 0;
        if (data.color == 'purple') {
            offset = 4;
        }
        if (data.id == 0) {
            this.visible = false;
        } else {
            this.visible = true;
        }
    }
}