import GEvent from "../../GameFrame/event/GEvent";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import ResManager from "../../gframe-work/manager/ResManager";
import MathTool from "../../gframe-work/tool/MathTool";
import { ui } from "../../ui/layaMaxUI";
import { CardData } from "../data/CardData";
import { EventTypeEnum, SceneName } from "../GEnum";
import CardManager from "./CardManager";
import LotteryResultRuntime from "./LotteryResultRuntime";
import PackageRuntime from "./PackageRuntime";
import SignCellRuntime from "./SignCellRuntime";

export default class SignRuntime extends ui.signUI {
    signCellArr: SignCellRuntime[] = [];
    enemyPoolLocal: LocalStorageData = null;
    curLevelLocal: LocalStorageData = null;
    shopIdLocal: LocalStorageData = null;
    signDataArr = null;
    onAwake() {
        this.curLevelLocal = DataManager.get<LocalStorageData>('curLevel');
        this.enemyPoolLocal = DataManager.get<LocalStorageData>('enemyPool');
        this.shopIdLocal = DataManager.get<LocalStorageData>('shopId');
        let buff = 0;
        if (this.curLevelLocal.value <= 3) {
            buff = 0;
        } else if (this.curLevelLocal.value <= 10) {
            buff = 1 / 10;
        } else if (this.curLevelLocal.value <= 60) {
            buff = 1 / 30;
        } else if (this.curLevelLocal.value <= 160) {
            buff = 1 / 25;
        } else if (this.curLevelLocal.value <= 320) {
            buff = 1 / 20;
        } else if (this.curLevelLocal.value <= 560) {
            buff = 1 / 15;
        } else {
            buff = 1 / 10;
        }
        this.signDataArr = [
            { desc: '普通卡包', value: [1], type: 'package' },
            { desc: '钻石卡包', value: [3], type: 'package' },
            {
                desc: '卡牌 X3', value: [
                    { id: 1, type: 3, skin: 'FireDragon-FireDragon-FireDragon', datatype: 8, exp: 9 },
                    { id: 1, type: 1, skin: 'Lion-Lion-Lion', datatype: 5, exp: 9 },
                    { id: 1, type: 1, skin: 'Tiger-Tiger-Tiger', datatype: 6, exp: 9 }
                ], type: 'card'
            },
            { desc: '+688', value: 688, type: 'gold' },
            { desc: '钻石卡包', value: [3], type: 'package' },
            { desc: '+888', value: 888, type: 'gold' },
            { desc: '钻石卡包 X3', value: [3, 3, 3], type: 'package' },
        ]
        this.signCellArr.push(this.day1Cell);
        this.signCellArr.push(this.day2Cell);
        this.signCellArr.push(this.day3Cell);
        this.signCellArr.push(this.day4Cell);
        this.signCellArr.push(this.day5Cell);
        this.signCellArr.push(this.day6Cell);
        this.signCellArr.push(this.day7Cell);
        let signTimeStap = DataManager.get<LocalStorageData>('signTimeStap');
        let signedLocal = DataManager.get<LocalStorageData>('signed');
        let signDayLocal = DataManager.get<LocalStorageData>('signDay');

        let date1 = new Date(signTimeStap.value);
        let date2 = new Date();
        if (
            signTimeStap.value > 0 &&
            (date2.getFullYear() > date1.getFullYear() ||
                date2.getMonth() > date1.getMonth() ||
                date2.getDay() > date1.getDay())
        ) {
            if (signedLocal.value == 1) {
                signDayLocal.value++;
                signedLocal.value = 0;
                if (signDayLocal.value > 7) {
                    signDayLocal.value = 1;
                }
            }
        }
        this.refreshCell();
    }

    refreshCell() {
        let signedLocal = DataManager.get<LocalStorageData>('signed');
        let signDayLocal = DataManager.get<LocalStorageData>('signDay');
        for (let i = 0; i < this.signCellArr.length; i++) {
            let cell = this.signCellArr[i];
            let state = 0;
            if (i + 1 < signDayLocal.value) {
                state = 2;
            } else if (i + 1 == signDayLocal.value) {
                if (signedLocal.value == 0) {
                    state = 1;
                    this.receiveBtn.visible = true;
                    this.receivedBtn.visible = false;
                } else {
                    state = 2;
                    this.receiveBtn.visible = false;
                    this.receivedBtn.visible = true;
                }
            }
            cell.setData(i + 1, this.signDataArr[i], state);
        }
    }

    onOpened() {
        this.backBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['back']);
        this.receiveBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['receive']);
    }
    onMouseEvent(name: string) {
        ResManager.playSound('Click.mp3');
        if (name == 'back') {
            GEvent.event(EventTypeEnum.MainShowOpen, false);
            this.close();
        } else if (name == 'receive') {
            this.receiveBtn.visible = false;
            this.receivedBtn.visible = true;
            DataManager.get<LocalStorageData>('signTimeStap').value = Date.now();
            let signedLocal = DataManager.get<LocalStorageData>('signed');
            signedLocal.value = 1;
            let signDayLocal = DataManager.get<LocalStorageData>('signDay');
            let signdata = this.signDataArr[signDayLocal.value - 1];
            Laya.Scene.load(SceneName.lotteryResult, Laya.Handler.create(this, (lotteryResult: LotteryResultRuntime) => {
                lotteryResult.open(false);
                lotteryResult.setData(signdata);
            }))
            this.refreshCell();
        }
    }

    openPackage(packageTypeArr: number[]) {
        let packageDataArr = [];
        let cardPackConfig = ResManager.getRes<any>("gacha.json");

        for (let i = 0; i < packageTypeArr.length; i++) {
            let cardPackData = cardPackConfig[packageTypeArr[i] - 1];
            let dataArr = this.getCardPackData(cardPackData);
            packageDataArr.push(dataArr);
            CardManager.instance.addNewCard(dataArr);
        }
        Laya.Scene.load(SceneName.package, Laya.Handler.create(this, (packageRuntime: PackageRuntime) => {
            packageRuntime.isModal = true;
            packageRuntime.isShowEffect = false;
            packageRuntime.width = Laya.stage.width;
            packageRuntime.height = Laya.stage.height;
            packageRuntime.open(false);
            packageRuntime.showBook(packageTypeArr, packageDataArr);
        }))
    }
    getCardPackData(packData: any) {
        let cardNum = packData.totalCard;
        let pool1 = packData.pool1;
        let amount1: number[] = JSON.parse(packData.amount1);
        let pool2 = packData.pool2;
        let amount2: number[] = JSON.parse(packData.amount2);
        let pool3 = packData.pool3;
        let dataArr = [];
        for (let i = 0; i < 3; i++) {
            let poolId = 0;
            let poolCardNum = 0;
            if (i == 0) {
                poolId = pool1;
                poolCardNum = amount1[MathTool.getRandom(0, amount1.length - 1)];
            } else if (i == 1) {
                poolId = pool2;
                poolCardNum = amount2[MathTool.getRandom(0, amount2.length - 1)];
            } else if (i == 2) {
                poolId = pool3;
                poolCardNum = cardNum;
            }
            cardNum -= poolCardNum;
            dataArr = dataArr.concat(this.getCardPoolData(poolCardNum, poolId));
        }
        return dataArr;
    }
    getCardPoolData(cardNum: number, poolId: number) {
        let cardPoolConfig = ResManager.getRes<any>("gachapool.json");
        let cardPoolData = cardPoolConfig[this.getCardPoolIndex(poolId)];
        let poolContent: number[] = [...JSON.parse(cardPoolData.content)];
        let unitLevel = cardPoolData.unitLevel;
        let unitIdArr = [];
        let fightLevelArr = [];
        let ifRepetitive = cardPoolData.ifRepetitive;
        for (let i = 0; i < cardNum; i++) {
            let randomIndex = MathTool.getRandom(0, poolContent.length - 1);
            let unitId = poolContent[randomIndex];
            unitIdArr.push(unitId);
            fightLevelArr.push(unitLevel);
            if (!ifRepetitive) {
                poolContent.splice(randomIndex, 1);
            }
        }
        return CardManager.instance.getCardDataArr(unitIdArr, fightLevelArr);
    }
    getCardPoolIndex(poolId: number) {
        let offset = 0, base = 1000;
        let arr = [4, 4, 4];
        for (let i = 0; i < 3; i++) {
            let newbase = (i + 2) * 1000;
            if (poolId > newbase) {
                base = newbase;
                offset += arr[i];
            }
        }
        return poolId % base + offset - 1;
    }
}