import ResManager from "../../gframe-work/manager/ResManager";
import LayaTool from "../../gframe-work/tool/LayaTool";
import { ui } from "../../ui/layaMaxUI";
import FightCellRuntime from "./FightCellRuntime";

export default class PackageRuntime extends ui.packageUI {
    cardScene: Laya.Scene3D = null;
    cardList: Laya.Sprite3D[] = [];
    uiCardList: FightCellRuntime[] = [];
    glow1: Laya.Sprite3D = null;
    glow2: Laya.Sprite3D = null;
    glow3: Laya.Sprite3D = null;
    book1: Laya.Sprite3D = null;
    book2: Laya.Sprite3D = null;
    book3: Laya.Sprite3D = null;
    pointStart: Laya.Sprite3D = null;
    point1: Laya.Sprite3D = null;
    point2: Laya.Sprite3D = null;
    point3: Laya.Sprite3D = null;
    buyBook: Laya.Sprite3D = null;
    inPositonCardNum: number = 0;
    touchName: string = 'OpenBook';
    openCardRecord: Laya.Node[] = [];
    camera: Laya.Camera = null;
    ray: Laya.Ray = new Laya.Ray(new Laya.Vector3, new Laya.Vector3);
    cardNum: number = 0;
    packageDataArr: any[] = null;
    packageTypeArr: number[] = null;
    onAwake() {
        this.cardScene = ResManager.getRes<any>('Card.ls');
        if (!this.cardScene.getChildByName('Card10')) {
            this.cardScene.name = 'cardScene';
            let card = this.cardScene.getChildByName('Card') as Laya.Sprite3D;
            card.name = 'Card1';
            for (let i = 0; i < 9; i++) {
                let cloneCard = card.clone();
                cloneCard.name = `Card${i + 2}`;
                this.cardScene.addChild(cloneCard);
            }
        }
        this.node3d.addChild(this.cardScene);
        for (let i = 0; i < 10; i++) {
            let card = this.cardScene.getChildByName(`Card${i + 1}`) as Laya.Sprite3D;
            this.cardList.push(card);
        }
        this.glow1 = this.getChildByUrl(this.cardScene, 'CardBook/CardBook/Bone009/Bone001/Bone006/Bone007/Bone008/Glow1') as Laya.ShuriKenParticle3D;
        this.glow2 = this.getChildByUrl(this.cardScene, 'CardBook/CardBook/Bone009/Bone001/Bone006/Bone007/Bone008/Glow2') as Laya.ShuriKenParticle3D;
        this.glow3 = this.getChildByUrl(this.cardScene, 'CardBook/CardBook/Bone009/Bone001/Bone006/Bone007/Bone008/Glow3') as Laya.ShuriKenParticle3D;
        this.book1 = this.getChildByUrl(this.cardScene, 'CardBook/CardBook/CardBook1') as Laya.Sprite3D;
        this.book2 = this.getChildByUrl(this.cardScene, 'CardBook/CardBook/CardBook2') as Laya.Sprite3D;
        this.book3 = this.getChildByUrl(this.cardScene, 'CardBook/CardBook/CardBook3') as Laya.Sprite3D;
        this.point1 = this.cardScene.getChildByName('CardPoint1') as Laya.Sprite3D;
        this.point2 = this.cardScene.getChildByName('CardPoint2') as Laya.Sprite3D;
        this.point3 = this.cardScene.getChildByName('CardPoint3') as Laya.Sprite3D;
        this.pointStart = this.cardScene.getChildByName('CardPointStart') as Laya.Sprite3D;
        this.camera = this.cardScene.getChildByName('Camera') as Laya.Camera;
    }
    /**
* 
* @param parent 
* @param url 
*/
    getChildByUrl(parent: Laya.Node, url: string): Laya.Node {
        let node: Laya.Node = parent;
        let names = url.split('/')
        for (let i = 0; i < names.length; i++) {
            if (node == null) {
                break
            }
            node = node.getChildByName(names[i])
        }
        return node
    }

    onOpened() {
        this.cardScene.active = true;
        this.on(Laya.Event.CLICK, this, this.onMouseEvent);
    }

    onMouseEvent(e: Laya.Event) {
        if (this.touchName == 'OpenBook') {
            ResManager.playSound('OpenCardBook.mp3');
            let animator = this.cardScene.getChildByName('CardBook').getComponent(Laya.Animator) as Laya.Animator;
            animator.play('CardBookOpen', 0, 0);
            this.inPositonCardNum = 0;
            this.touchName = 'PickCard';
            Laya.timer.once(1000, this, () => {
                this.glow1.active = false;
                if (this.buyBook.name == 'CardBook1') {
                    for (let i = 0; i < 5; i++) {
                        let timeDelay = 300 + 200 * i;
                        let card = this.cardList[i];
                        let cardEndPoint = this.point1.getChildByName(`CardPoint${i + 1}`) as Laya.Sprite3D
                        Laya.timer.once(timeDelay, this, () => {
                            this.flyCard(card, cardEndPoint.transform.position);
                        })
                    }
                } else if (this.buyBook.name == 'CardBook2') {
                    this.glow2.active = false;
                    for (let i = 0; i < 8; i++) {
                        let timeDelay = 300 + 200 * i;
                        let card = this.cardList[i];
                        let cardEndPoint = this.point2.getChildByName(`CardPoint${i + 1}`) as Laya.Sprite3D
                        Laya.timer.once(timeDelay, this, () => {
                            this.flyCard(card, cardEndPoint.transform.position);
                        })
                    }
                } else if (this.buyBook.name == 'CardBook3') {
                    this.glow3.active = false;
                    for (let i = 0; i < 10; i++) {
                        let timeDelay = 300 + 200 * i;
                        let card = this.cardList[i];
                        let cardEndPoint = this.point3.getChildByName(`CardPoint${i + 1}`) as Laya.Sprite3D
                        Laya.timer.once(timeDelay, this, () => {
                            this.flyCard(card, cardEndPoint.transform.position);
                        })
                    }
                }
            })
        } else if (this.touchName == 'PickCard') {
            if (this.inPositonCardNum < this.cardNum) return;
            let touchCard = this.colliderCard(e);
            if (touchCard && this.openCardRecord.indexOf(touchCard) == -1) {
                this.openCardRecord.push(touchCard);
                let animator = touchCard.getComponent(Laya.Animator) as Laya.Animator;
                animator.play('CardOpen', 0, 0);
                ResManager.playSound('OpenCard.mp3');
                this.show2dCard(touchCard as Laya.Sprite3D);
                if (this.openCardRecord.length == this.cardNum) {
                    this.touchName = 'Close';
                }
            }
        } else if (this.touchName == 'Close') {
            if (this.packageDataArr.length > 0) {
                this.showBook(this.packageTypeArr, this.packageDataArr);
            } else {
                this.node3d.removeChildByName('cardScene');
                this.close();
            }
        }
    }

    onDestroy() {

    }

    showBook(packageTypeArr: number[], packageDataArr: any[]) {
        this.touchName = 'OpenBook';
        this.openCardRecord.length = 0;
        this.packageDataArr = packageDataArr;
        this.packageTypeArr = packageTypeArr;
        let index = this.packageTypeArr.shift();
        let cardData = this.packageDataArr.shift();
        for (let i = 0; i < 10; i++) {
            let card = this.cardList[i];
            card.transform.position = this.pointStart.transform.position.clone();
            Laya.timer.once(Math.random() * 200, this, () => {
                let animator = card.getComponent(Laya.Animator) as Laya.Animator;
                animator.play('CardStand', 0, 0);
            })
        }
        if (index == 1) {
            this.book1.active = true;
            this.book2.active = false;
            this.book3.active = false;
            this.glow1.active = true;
            this.glow2.active = false;
            this.glow3.active = false;
            this.buyBook = this.book1;
            this.cardNum = 5;
        } else if (index == 2) {
            this.book1.active = false;
            this.book2.active = true;
            this.book3.active = false;
            this.glow1.active = false;
            this.glow2.active = true;
            this.glow3.active = false;
            this.buyBook = this.book2;
            this.cardNum = 8;
        } else {
            this.book1.active = false;
            this.book2.active = false;
            this.book3.active = true;
            this.glow1.active = false;
            this.glow2.active = false;
            this.glow3.active = true;
            this.buyBook = this.book3;
            this.cardNum = 10;
        }
        let animator = this.cardScene.getChildByName('CardBook').getComponent(Laya.Animator) as Laya.Animator;
        animator.play('CardBookStand', 0, 0);
        this.create2dCard(cardData);
    }

    flyCard(card: Laya.Sprite3D, endPosition: Laya.Vector3) {
        let tweenTarget = { value: 0 };
        let vector = new Laya.Vector3;
        let startPosition = card.transform.position.clone();
        Laya.Vector3.subtract(endPosition, startPosition, vector);
        Laya.Tween.to(tweenTarget, {
            value: 1, update: Laya.Handler.create(this, () => {
                let translation = new Laya.Vector3;
                let nowPosition = new Laya.Vector3;
                Laya.Vector3.scale(vector, tweenTarget.value, translation);
                Laya.Vector3.add(startPosition, translation, nowPosition);
                card.transform.position = nowPosition;
            }, null, false)
        }, 200, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
            this.inPositonCardNum++;
        }))
    }

    colliderCard(e: Laya.Event) {
        let halfWidth = 198 * 0.8 / 2;
        let halfHeight = 244 * 0.8 / 2;
        for (let i = 0; i < this.cardNum; i++) {
            let card3d = this.cardList[i];
            let out = LayaTool.projectPos(this.camera, card3d.transform.position);
            if (e.stageX >= out.x - halfWidth &&
                e.stageX <= out.x + halfWidth &&
                e.stageY >= out.y - halfHeight &&
                e.stageY <= out.y + halfHeight
            ) {
                return card3d;
            }
        }
    }

    create2dCard(data: any[]) {
        this.node2d.destroyChildren();
        this.uiCardList.length = 0;
        for (let i = 0; i < data.length; i++) {
            let card = new FightCellRuntime();
            card.dataSource = data[i];
            this.uiCardList.push(card);
            this.node2d.addChild(card);
            card.alpha = 0;
        }
    }
    show2dCard(card3d: Laya.Sprite3D) {
        let index = Number(card3d.name.replace('Card', ''));
        let card = this.uiCardList[index - 1];
        card.scaleX = card.scaleY = 0.8;
        //转换坐标
        let out = LayaTool.projectPos(this.camera, card3d.transform.position);
        //赋值给2D
        card.pos((out.x - card.width * card.scaleX / 2) / Laya.stage.clientScaleX, (out.y - (card.height - 75) * card.scaleY / 2) / Laya.stage.clientScaleY);
        Laya.timer.once(500, this, () => {
            Laya.Tween.to(card, { alpha: 1 }, 500, Laya.Ease.sineOut);
        })
    }
}