import GEvent from "../../GameFrame/event/GEvent";
import ResManager from "../../gframe-work/manager/ResManager";
import GameDataManager from "../data/GameDataManager";
import { EventTypeEnum } from "../GEnum";
import CannonBase from "./CannonBase";
import FortBase from "./FortBase";
import RoleBase from "./RoleBase";

export default class WeaponBase extends Laya.Script3D {
    public gameObject: Laya.Sprite3D = null;
    public type: number = 0;
    public damage: number = 0;
    public moveDir: Laya.Vector3 = null;
    public translation: Laya.Vector3 = null;
    public sign: string = 'weapon';
    public roadIndex: number = 0;
    public moveDistance: number = 0;
    public isDragon: boolean = false;
    private moveSpeed: number = 8;
    private roadEnemyArr: RoleBase[] = null;
    private roadTowerArr: CannonBase[] = null;
    private enemyFort: FortBase = null;
    private isTower: boolean = false;
    private towerTarget: RoleBase | CannonBase = null;
    onAwake() {
        this.gameObject = this.owner as Laya.Sprite3D;
        this.translation = new Laya.Vector3;
    }
    onDestroy() {
    }
    onUpdate() {
        if (GameDataManager.instance.isGameOver) return;
        if (!this.isDragon && this.gameObject.name != 'MissileSizu1' && this.gameObject.name != 'MissileSizu2') {
            Laya.Vector3.scale(this.moveDir, this.moveSpeed * Laya.timer.delta / 1000, this.translation);
            this.moveDistance -= this.translation.z;
            this.gameObject.transform.translate(this.translation);
            if (this.moveDistance <= 0) {
                GEvent.event(EventTypeEnum.RecoveryWeapon, this);
                return;
            }
            this.detectCollision();
        }
    }
    hitThing(thing: RoleBase | FortBase | CannonBase) {
        let explodeName = '';
        if (
            this.gameObject.name == 'MissileShuangzu1' ||
            this.gameObject.name == 'MissileTower1' ||
            this.gameObject.name == 'MissileTower2'
        ) {
            explodeName = 'ExplodeShuangzu1.lh';
        } else if (this.gameObject.name == 'MissileSizu1' || this.gameObject.name == 'MissileSizu2') {
            explodeName = 'ExplodeSizu1.lh';
        } else if (this.gameObject.name == 'MissileDragon1') {
            explodeName = 'ExplodeDragon1.lh';
        } else if (this.gameObject.name == 'MissileDragon2') {
            explodeName = 'ExplodeDragon2.lh';
        } else if (this.gameObject.name == 'MissileDragon3') {
            explodeName = 'ExplodeDragon3.lh';
        }
        let weaponDamage = this.damage;
        let roadIndex = this.roadIndex;
        if (this.isDragon) {
            Laya.timer.once(200, this, () => {
                ResManager.playSound('Hit.mp3');
                if (thing instanceof RoleBase) {
                    thing.addExplode(explodeName);
                    thing.onHurt(weaponDamage);
                } else if (thing instanceof FortBase) {
                    if (thing.type == 1) thing.baseAttackRecord[roadIndex] = 1;
                    thing.addExplode(roadIndex, explodeName);
                    thing.onHurt(weaponDamage);
                } else if (thing instanceof CannonBase) {
                    thing.addExplode(explodeName);
                    thing.onHurt(weaponDamage);
                }
            })
        } else {
            if (this.gameObject.name != 'MissileSizu1' && this.gameObject.name != 'MissileSizu2') {
                ResManager.playSound('Hit.mp3');
                if (thing instanceof RoleBase) {
                    thing.addExplode(explodeName);
                    thing.onHurt(weaponDamage);
                } else if (thing instanceof FortBase) {
                    if (thing.type == 1) thing.baseAttackRecord[roadIndex] = 1;
                    thing.addExplode(roadIndex, explodeName);
                    thing.onHurt(weaponDamage);
                } else if (thing instanceof CannonBase) {
                    thing.addExplode(explodeName);
                    thing.onHurt(weaponDamage);
                }
                GEvent.event(EventTypeEnum.RecoveryWeapon, this);
            } else {
                Laya.timer.once(600, this, () => {
                    ResManager.playSound('Hit1.mp3');
                    if (thing instanceof RoleBase) {
                        thing.addExplode(explodeName);
                        thing.onHurt(weaponDamage);
                    } else if (thing instanceof FortBase) {
                        if (thing.type == 1) thing.baseAttackRecord[roadIndex] = 1;
                        thing.addExplode(roadIndex, explodeName);
                        thing.onHurt(weaponDamage);
                    } else if (thing instanceof CannonBase) {
                        thing.addExplode(explodeName);
                        thing.onHurt(weaponDamage);
                    }
                })
            }
        }
    }

    init(isDragon: boolean, type: number, damage: number, sign: string, roadIndex: number, moveDistance: number, roadEnemyArr: RoleBase[], roadTowerArr: CannonBase[], enemyBase: FortBase, isTower: boolean = false, towerTarget: RoleBase | CannonBase = null) {
        this.isTower = isTower;
        this.towerTarget = towerTarget;
        this.roadEnemyArr = roadEnemyArr;
        this.roadTowerArr = roadTowerArr;
        this.enemyFort = enemyBase;
        this.moveDistance = moveDistance;
        this.isDragon = isDragon;
        this.sign = sign;
        this.type = type;
        this.roadIndex = roadIndex;
        this.damage = damage;
        this.moveDir = new Laya.Vector3(0, 0, 1);
        if (this.isDragon) {
            this.detectCollision();
            Laya.timer.once(1000, this, () => {
                GEvent.event(EventTypeEnum.RecoveryWeapon, this);
            })
        } else {
            if (sign == 'MissileSizu1' || sign == 'MissileSizu2') {
                this.detectCollision();
                Laya.timer.once(2000, this, () => {
                    GEvent.event(EventTypeEnum.RecoveryWeapon, this);
                })
            }
        }
    }
    detectCollision() {
        if (!this.isTower) {
            let targetArr: { z: number, index: number, targetArr: CannonBase[] | RoleBase[] }[] = [];
            for (let i = 0; i < this.roadEnemyArr.length; i++) {
                const enemy = this.roadEnemyArr[i];
                targetArr.push({ z: enemy.gameObject.transform.position.z, index: i, targetArr: this.roadEnemyArr });
            }
            for (let i = 0; i < this.roadTowerArr.length; i++) {
                const tower = this.roadTowerArr[i];
                targetArr.push({ z: tower.gameObject.transform.position.z, index: i, targetArr: this.roadTowerArr });
            }
            if (this.type == 0) {
                targetArr.sort((a, b) => {
                    return b.z - a.z;
                })
            } else {
                targetArr.sort((a, b) => {
                    return a.z - b.z;
                })
            }
            if (this.isDragon) {
                for (let i = 0; i < targetArr.length; i++) {
                    let targetObj = targetArr[i];
                    if ((this.type == 0 && targetObj.z >= this.gameObject.transform.position.z - this.moveDistance) ||
                        (this.type == 1 && targetObj.z <= this.gameObject.transform.position.z + this.moveDistance)
                    ) {
                        let enemy = targetObj.targetArr[targetObj.index];
                        this.hitThing(enemy);
                    }
                }
                if ((this.type == 0 && this.enemyFort.gameObject.transform.position.z >= this.gameObject.transform.position.z - this.moveDistance) ||
                    (this.type == 1 && this.enemyFort.gameObject.transform.position.z <= this.gameObject.transform.position.z + this.moveDistance)
                ) {
                    this.hitThing(this.enemyFort);
                }
            } else {
                if (this.gameObject.name != 'MissileSizu1' && this.gameObject.name != 'MissileSizu2') {
                    if (targetArr.length > 0) {
                        let targetObj = targetArr[0];
                        if ((this.type == 0 && (targetObj.z > this.gameObject.transform.position.z || (this.gameObject.transform.position.z - targetObj.z) <= 0.3))) {
                            let enemy = targetObj.targetArr[targetObj.index];
                            this.hitThing(enemy);
                            return;
                        }
                        if ((this.type == 1 && (targetObj.z < this.gameObject.transform.position.z || (targetObj.z - this.gameObject.transform.position.z) <= 0.3))) {
                            let enemy = targetObj.targetArr[targetObj.index];
                            this.hitThing(enemy);
                            return;
                        }
                    }
                    if ((this.type == 0 && (this.enemyFort.gameObject.transform.position.z > this.gameObject.transform.position.z || (this.gameObject.transform.position.z - this.enemyFort.gameObject.transform.position.z) <= 0.3))) {
                        this.hitThing(this.enemyFort);
                        return;
                    }
                    if ((this.type == 1 && (this.enemyFort.gameObject.transform.position.z < this.gameObject.transform.position.z || (this.enemyFort.gameObject.transform.position.z - this.gameObject.transform.position.z) <= 0.3))) {
                        this.hitThing(this.enemyFort);
                        return;
                    }
                } else {
                    if (targetArr.length > 0) {
                        let targetObj = targetArr[0];
                        let enemy = targetObj.targetArr[targetObj.index];
                        this.hitThing(enemy);
                        return;
                    }
                    this.hitThing(this.enemyFort);
                    return;
                }
            }
        } else {
            if (this.towerTarget && this.towerTarget.destroyed == false && this.towerTarget.isDeath == false) {
                let distance = Math.abs(this.gameObject.transform.position.z - this.towerTarget.gameObject.transform.position.z)
                if (distance <= 0.3) {
                    this.hitThing(this.towerTarget);
                }
            }
        }
    }
}