import { ui } from "../../ui/layaMaxUI";
import GEvent from "../../GameFrame/event/GEvent";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import { EventTypeEnum, SceneName } from "../GEnum";
import GuideManager from "./GuideManager";
import SelectRuntime from "./SelectRuntime";
import FightCellRuntime from "./FightCellRuntime";
import ModeRuntime from "./ModeRuntime";
import ShopRuntime from "./ShopRuntime";
import ShopCellRuntime from "./ShopCellRuntime";
import ComposeRuntime from "./ComposeRuntime";
import UnlitBlinnPhongMaterial from "../../GameFrame/material/UnlitBlinnPhoneMaterial";
import AddMoneyRuntime from "./AddMoneyRuntime";
import ResManager from "../../gframe-work/manager/ResManager";
import ShaderManager from "../../gframe-work/manager/ShaderManager";
import EventManager from "../../gframe-work/manager/EventManager";
import EventType from "../../gframe-work/manager/EventType";
import UserDataManager from "../../gframe-work/manager/UserDataManager";

export default class MainRuntime extends ui.mainUI {
    private moveSpeed: number = 1;

    private tabNum: number = 0;

    private tabWidth: number = 0;

    private tabArray: Laya.Box[] = [];

    private tabIconArray: Laya.Image[] = [];

    private diArray: Laya.Image[] = [];

    private tabNameArray: Laya.Image[] = [];

    private pageArray: any[] = [];

    public curIndex: number = 2;

    private tweenTarget: { progress: number } = null;

    private isTweening: boolean = false;

    private lastIndex: number = 0;

    private signPosDiff: number = 0;

    private signStartX: number = 0;

    private pageStartX: number = 0;
    diamondLocal: LocalStorageData = null;
    goldLocal: LocalStorageData = null;
    mainShow: Laya.Scene3D = null;
    isLotteryOpen: boolean = false;
    onAwake() {
        UserDataManager.instance;
        ShaderManager.init();
        window['UserInfo'] = { nickName: '快乐的男孩', avatarUrl: 'guide/Ui_bear.png' };
        this.getUserInfo();
        if (GuideManager.Instance.guideFinish) {
            this.getAuthorize();
        }
        this.mainShow = ResManager.getRes<any>('Main.ls');
        this.mainShow.name = 'MainShow';
        this.mainShowBox.addChild(this.mainShow);
        this.changeSceneShader();
        if (this.curIndex == 2) {
            this.mainShow.active = true;
        } else {
            this.mainShow.active = false;
        }
        this.initMainShow();
        this.diamondLocal = DataManager.get<LocalStorageData>('diamond');
        this.goldLocal = DataManager.get<LocalStorageData>('gold');

        Laya.timer.loop(60, this, this.onFrameLoop);

        this.tabArray.push(this.tab1);
        this.tabArray.push(this.tab2);
        this.tabArray.push(this.tab3);
        this.tabArray.push(this.tab4);
        this.tabArray.push(this.tab5);

        this.diArray.push(this.di1);
        this.diArray.push(this.di2);
        this.diArray.push(this.di3);
        this.diArray.push(this.di4);

        this.pageArray.push(this.page_0);
        this.pageArray.push(this.page_1);
        this.pageArray.push(this.page_2);
        this.pageArray.push(this.page_3);
        this.pageArray.push(this.page_4);

        this.tabIconArray.push(this.tab1.getChildByName('icon') as Laya.Image);
        this.tabIconArray.push(this.tab2.getChildByName('icon') as Laya.Image);
        this.tabIconArray.push(this.tab3.getChildByName('icon') as Laya.Image);
        this.tabIconArray.push(this.tab4.getChildByName('icon') as Laya.Image);
        this.tabIconArray.push(this.tab5.getChildByName('icon') as Laya.Image);

        this.tabNameArray.push(this.tab1.getChildByName('name') as Laya.Image);
        this.tabNameArray.push(this.tab2.getChildByName('name') as Laya.Image);
        this.tabNameArray.push(this.tab3.getChildByName('name') as Laya.Image);
        this.tabNameArray.push(this.tab4.getChildByName('name') as Laya.Image);
        this.tabNameArray.push(this.tab5.getChildByName('name') as Laya.Image);

        this.tab1.on(Laya.Event.CLICK, this, this.onTabEvent, [0]);
        this.tab2.on(Laya.Event.CLICK, this, this.onTabEvent, [1]);
        this.tab3.on(Laya.Event.CLICK, this, this.onTabEvent, [2]);
        this.tab4.on(Laya.Event.CLICK, this, this.onTabEvent, [3]);
        this.tab5.on(Laya.Event.CLICK, this, this.onTabEvent, [4]);
        this.addMoney.on(Laya.Event.CLICK, this, this.onMouseEvent, ['addMoney']);
        this.addDiamond.on(Laya.Event.CLICK, this, this.onMouseEvent, ['addDiamond']);
        this.addPower.on(Laya.Event.CLICK, this, this.onMouseEvent, ['addPower']);

        this.tabNum = this.tabArray.length;

        this.tabWidth = Laya.stage.width / (this.tabNum + 1);

        this.moveSign.width = this.tabWidth * 2;

        this.initTab();

        this.goldLabel.text = this.goldLocal.valueStr;
        this.diamondLabel.text = this.diamondLocal.valueStr;
        this.powerLabel.text = UserDataManager.instance.power + "";

        this.registerCustomEvent();
        this.onLotteryOpen(false);
        ResManager.playMusic('MainBGM.mp3', 0);
    }

    onMouseEvent(name: string, e: Laya.Event) {
        e.stopPropagation();
        if (name == 'addMoney') {
            Laya.Scene.load(SceneName.addMoney, Laya.Handler.create(this, (addMoneyRuntime: AddMoneyRuntime) => {
                addMoneyRuntime.setMoneyType(1);
                addMoneyRuntime.open(false);
            }))
        } else if (name == 'addDiamond') {
            Laya.Scene.load(SceneName.addMoney, Laya.Handler.create(this, (addMoneyRuntime: AddMoneyRuntime) => {
                addMoneyRuntime.setMoneyType(2);
                addMoneyRuntime.open(false);
            }))
        } else if (name == 'addPower') {
            Laya.Scene.load(SceneName.addMoney, Laya.Handler.create(this, (addMoneyRuntime: AddMoneyRuntime) => {
                addMoneyRuntime.setMoneyType(3);
                addMoneyRuntime.open(false);
            }))
        }
    }

    getMonsterNames(carddatas: any[]) {
        let names = [];
        let record = [];
        for (let i = 0; i < carddatas.length; i++) {
            const carddata = carddatas[i];
            let skin = carddata.skin;
            let skinarr = skin.split('-');
            for (let j = 0; j < skinarr.length; j++) {
                const skinname = skinarr[j];
                if (record.indexOf(skinname) == -1) {
                    record.push(skinname);
                    if (carddata.type == 1) {
                        names.push(`Player2_${skinname}1.lh`)
                        names.push(`Player2_${skinname}2.lh`)
                        names.push(`Player2_${skinname}3.lh`)
                        names.push(`Player2.lh`);
                    } else if (carddata.type == 2) {
                        names.push(`Player1_${skinname}1.lh`)
                        names.push(`Player1_${skinname}2.lh`)
                        names.push(`Player1_${skinname}3.lh`)
                        names.push(`Player1.lh`);
                    } else if (carddata.type == 3) {
                        names.push(`Player3_${skinname}1.lh`)
                        names.push(`Player3_${skinname}2.lh`)
                        names.push(`Player3_${skinname}3.lh`)
                        names.push(`Player3.lh`);
                    }
                }
            }
        }
        return names;
    }

    onNewGuideTaskEvent(task: string) {
        if (task == "开始欢迎语") {
            GuideManager.Instance.showGuide(task, null);
        } else if (task == "点击卡组") {
            GuideManager.Instance.showGuide(task, this.tab4);
        } else if (task == "点击选择一张卡片") {
            Laya.timer.once(400, this, () => {
                let selestCard = this.page_3 as SelectRuntime;
                let firseCell: FightCellRuntime = <any>selestCard.houseList.getCell(0);
                selestCard.houseList.scrollBar.touchScrollEnable = false;
                selestCard.houseList.scrollBar.mouseWheelEnable = false;
                GuideManager.Instance.showGuide(task, firseCell);
            })
        } else if (task == "点击出战") {
            let selestCard = this.page_3 as SelectRuntime;
            let firseCell: FightCellRuntime = <any>selestCard.houseList.getCell(0);
            GuideManager.Instance.showGuide(task, firseCell.out);
        } else if (task == "战斗前激励语") {
            GuideManager.Instance.showGuide(task, null);
        } else if (task == "点击战斗") {
            GuideManager.Instance.showGuide(task, this.tab3);
        } else if (task == "点击当前关卡") {
            Laya.timer.once(800, this, () => {
                let modeRuntime = this.page_2 as ModeRuntime;
                GuideManager.Instance.showGuide(task, modeRuntime.levelBtn);
            })
        } else if (task == "购买灵兽提示语") {
            GuideManager.Instance.showGuide(task, null);
        } else if (task == "点击商店") {
            GuideManager.Instance.showGuide(task, this.tab1);
        } else if (task == "点击第一张双足卡片") {
            Laya.timer.once(400, this, () => {
                let selestCard = this.page_0 as ShopRuntime;
                for (let i = 0; i < selestCard.houseList.length; i++) {
                    let firseCell: ShopCellRuntime = <any>selestCard.houseList.getCell(i);
                    if (firseCell.cell.dataSource.type == 2) {
                        GuideManager.Instance.showGuide(task, firseCell);
                        return;
                    }
                }
            })
        } else if (task == "提示解锁合成系统") {
            GuideManager.Instance.showGuide(task, null);
        } else if (task == "点击合成") {
            GuideManager.Instance.showGuide(task, this.tab2);
        } else if (task == "点击第一张合成卡牌") {
            Laya.timer.once(400, this, () => {
                let selestCard = this.page_1 as ComposeRuntime;
                let firseCell: FightCellRuntime = <any>selestCard.houseList.getCell(0);
                GuideManager.Instance.showGuide(task, firseCell);
            })
        } else if (task == "点击普通合成") {
            let selestCard = this.page_1 as ComposeRuntime;
            GuideManager.Instance.showGuide(task, selestCard.goldCompose);
        } else if (task == "使用合成卡牌提示语") {
            GuideManager.Instance.showGuide(task, null);
        }
    }

    onOpened() {
        if (GuideManager.Instance.guideFinish == false) {
            GuideManager.Instance.sendNewGuideTaskEvent();
        }
    }

    destroy() {
        this.mainShowBox.removeChild(this.mainShow);
        super.destroy();
    }

    onDestroy() {
        this.cancleCustomEvent();
        this.clearMainShow();
    }

    registerCustomEvent() {
        GEvent.on('DataChange', this, this.onDataChange);
        GEvent.on(EventTypeEnum.NextChange, this, this.onNextChange);
        GEvent.on(EventTypeEnum.JumpPage, this, this.onTabEvent);
        GEvent.on(EventTypeEnum.SelectCardChange, this, this.initMainShow);
        GEvent.on(EventTypeEnum.MainShowOpen, this, this.onMainShowOpen);
        GEvent.on(EventTypeEnum.LotteryOpen, this, this.onLotteryOpen);
        GEvent.on(EventTypeEnum.OnShow, this, this.onShow);
        EventManager.on(EventType.NEWGUIDETASK, this, this.onNewGuideTaskEvent);
        EventManager.on(EventType.UserDataChange, this, this.onUserDataChange);
    }
    onUserDataChange(key: string) {
        if (key == "_power") {
            this.powerLabel.text = UserDataManager.instance.power + "";
        }
    }
    onShow() {
        ResManager.playMusic('MainBGM.mp3', 0);
    }

    cancleCustomEvent() {
        GEvent.off('DataChange', this, this.onDataChange);
        GEvent.off(EventTypeEnum.NextChange, this, this.onNextChange);
        GEvent.off(EventTypeEnum.JumpPage, this, this.onTabEvent);
        GEvent.off(EventTypeEnum.SelectCardChange, this, this.initMainShow);
        GEvent.off(EventTypeEnum.MainShowOpen, this, this.onMainShowOpen);
        GEvent.off(EventTypeEnum.LotteryOpen, this, this.onLotteryOpen);
        GEvent.off(EventTypeEnum.OnShow, this, this.onShow);
        EventManager.off(EventType.NEWGUIDETASK, this, this.onNewGuideTaskEvent);
        EventManager.off(EventType.UserDataChange, this, this.onUserDataChange);
    }
    onMainShowOpen(bool: boolean) {
        this.mainShow.active = bool;
    }
    onLotteryOpen(bool: boolean) {
        if (bool) {
            this.lotteryPageBox.visible = true;
            this.isLotteryOpen = true;
            Laya.Scene.load(SceneName.lottery, Laya.Handler.create(this, (pageRuntime: any) => {
                this.lotteryPageBox.addChild(pageRuntime);
                pageRuntime.closeHandler = Laya.Handler.create(this, () => {
                    this.isLotteryOpen = false;
                    this.lotteryPageBox.visible = false;
                })
            }))
        }
    }
    onNextChange(name: string) {
        if (name == 'show') {
            if (this.nextArrow.alpha == 1 ||
                (this.nextShow.wrapMode == Laya.FrameAnimation.WRAP_POSITIVE && this.nextShow.isPlaying)) {
                return;
            }
            this.nextJump.wrapMode = Laya.FrameAnimation.WRAP_PINGPONG;
            this.nextJump.play(0, true);

            this.nextShow.wrapMode = Laya.FrameAnimation.WRAP_POSITIVE;
            this.nextShow.play(0, false);
        } else if (name == 'hide') {
            if (this.nextArrow.alpha == 0 ||
                (this.nextShow.wrapMode == Laya.FrameAnimation.WRAP_REVERSE && this.nextShow.isPlaying)) {
                return;
            }
            if (this.nextJump.isPlaying) {
                this.nextJump.stop();
            }
            this.nextShow.wrapMode = Laya.FrameAnimation.WRAP_REVERSE;
            this.nextShow.play(0, false);
        }
    }

    onDataChange(name: string) {
        if (name == 'gold') {
            this.goldLabel.text = this.goldLocal.valueStr;
        } else if (name == 'diamond') {
            this.diamondLabel.text = this.diamondLocal.valueStr;
        }
    }

    initTab() {
        let diffX = 0;
        for (let i = 0; i < this.tabArray.length; i++) {
            const tab = this.tabArray[i];
            const icon = this.tabIconArray[i];
            const name = this.tabNameArray[i];
            const page = this.pageArray[i];
            const di = this.diArray[i];

            tab.x = diffX;
            if (i == this.curIndex) {
                tab.width = this.tabWidth * 2;
                this.moveSign.x = tab.x;
                icon.centerY = -60;
                icon.scale(1.2, 1.2);
                name.alpha = 1;
            } else {
                tab.width = this.tabWidth;
                icon.centerY = 0;
                icon.scale(1, 1);
                name.alpha = 0;
            }

            diffX += tab.width;

            if (di) {
                di.x = diffX - di.width / 2;
            }

            page.y = 0;
            page.x = (i - this.curIndex) * Laya.stage.width;
        }
    }

    onTabEvent(tabIndex: number, e: Laya.Event) {
        if (this.curIndex == tabIndex || this.isTweening || this.isLotteryOpen) return;
        ResManager.playSound('Click.mp3');
        if (tabIndex == 2) {
            this.initMainShow();
            this.mainShow.active = true;
        } else {
            this.mainShow.active = false;
        }
        if (tabIndex == 3) {
            GuideManager.Instance.finishTask("点击卡组");
        } else if (tabIndex == 2) {
            GuideManager.Instance.finishTask("点击战斗");
        } else if (tabIndex == 0) {
            GuideManager.Instance.finishTask("点击商店");
        } else if (tabIndex == 1) {
            GuideManager.Instance.finishTask("点击合成");
        }
        this.lastIndex = this.curIndex;
        this.curIndex = tabIndex;
        this.isTweening = true;
        this.signPosDiff = this.curIndex * this.tabWidth - this.moveSign.x;
        this.signStartX = this.moveSign.x;
        this.pageStartX = this.pageArray[this.curIndex].x;
        this.tweenTarget = { progress: 0 };
        for (let i = 0; i < this.pageArray.length; i++) {
            const page = this.pageArray[i];
            if (this.curIndex == i) {
                page.onSelect();
            } else {
                page.isSelect = false;
            }
        }
        Laya.Tween.to(this.tweenTarget, { progress: 1, update: Laya.Handler.create(this, this.onTweenUpdate, null, false) }, 380, Laya.Ease.backOut, Laya.Handler.create(this, this.onTweenComplete));
    }

    onTweenUpdate() {
        let diffX = 0;
        for (let i = 0; i < this.tabArray.length; i++) {
            const tab = this.tabArray[i];
            const icon = this.tabIconArray[i];
            const name = this.tabNameArray[i];
            const page = this.pageArray[i];
            const di = this.diArray[i];
            tab.x = diffX;
            if (i == this.curIndex) {
                tab.width = this.tabWidth + this.tabWidth * this.tweenTarget.progress;
                this.moveSign.x = this.tweenTarget.progress * this.signPosDiff + this.signStartX;
                icon.scale(1 + this.tweenTarget.progress * 0.2, 1 + this.tweenTarget.progress * 0.2);
                icon.centerY = -60 * this.tweenTarget.progress;
                name.alpha = this.tweenTarget.progress;
            } else if (i == this.lastIndex) {
                tab.width = this.tabWidth * 2 - this.tabWidth * this.tweenTarget.progress;
                icon.scale(1.2 - this.tweenTarget.progress * 0.2, 1.2 - this.tweenTarget.progress * 0.2);
                icon.centerY = -60 + 60 * this.tweenTarget.progress;
                name.alpha = 1 - this.tweenTarget.progress;
            } else {
                tab.width = this.tabWidth;
            }

            if (i == this.curIndex) {
                page.x = this.pageStartX - this.pageStartX * this.tweenTarget.progress;
            } else {
                page.x = this.pageArray[this.curIndex].x + (i - this.curIndex) * Laya.stage.width;
            }

            diffX += tab.width;

            if (di) {
                di.x = diffX - di.width / 2;
            }

        }
    }

    onTweenComplete() {
        this.isTweening = false;
    }

    onFrameLoop() {
        this.bg.x -= this.moveSpeed;
        this.bg.y -= this.moveSpeed;
        if (this.bg.x <= -168) {
            this.bg.x = 0;
            this.bg.y = 0;
        }
    }

    clearMainShow() {
        if (this.mainShow) {
            for (let i = 0; i < 6; i++) {
                let node = this.mainShow.getChildByName(`MianPoint${i + 1}`);
                node.destroyChildren();
            }
        }
    }

    initMainShow() {
        if (DataManager.get<LocalStorageData>("curLevel").value == 1) return;
        this.clearMainShow();
        let group = DataManager.get<LocalStorageData>('group1').value;
        let names = this.getMonsterNames(group);
        ResManager.create(names).then(() => {
            for (let i = 0; i < group.length; i++) {
                const data = group[i];
                if (data.id != 0) {
                    let roleRuntime = this.createRole(data);
                    for (let j = 0; j < 6; j++) {
                        let node = this.mainShow.getChildByName(`MianPoint${j + 1}`);
                        if (node.numChildren == 0) {
                            if (j != 0 && roleRuntime.name == 'Player3') {
                                roleRuntime.transform.localPosition = new Laya.Vector3(0, 3, 0);
                            }
                            node.addChild(roleRuntime);
                            this.playStandAnimation(roleRuntime);
                            break;
                        }
                    }
                }
            }
        })
    }

    playStandAnimation(target: Laya.Sprite3D) {
        if (target) {
            for (let i = 0; i < target.numChildren; i++) {
                let animator = target.getChildAt(i).getComponent(Laya.Animator) as Laya.Animator;
                if (animator) animator.play("Stand", 0, 0);
            }
        }
    }

    createRole(data: any): Laya.Sprite3D {
        let unitArray = data.skin.split('-');
        let shellName = '';
        if (data.type == 2) {
            shellName = 'Player1';
        } else if (data.type == 1) {
            shellName = 'Player2';
        } else if (data.type == 3) {
            shellName = 'Player3';
        }

        let rolePrefab0 = ResManager.getRes<any>(`${shellName}.lh`) as Laya.Sprite3D;
        let rolePrefab1 = ResManager.getRes<any>(`${shellName}_${unitArray[0]}1.lh`) as Laya.Sprite3D;
        let rolePrefab2 = ResManager.getRes<any>(`${shellName}_${unitArray[1]}3.lh`) as Laya.Sprite3D;
        let rolePrefab3 = ResManager.getRes<any>(`${shellName}_${unitArray[2]}2.lh`) as Laya.Sprite3D;

        let cloneRole = Laya.Sprite3D.instantiate(rolePrefab0)
        let prefab1 = Laya.Sprite3D.instantiate(rolePrefab1);
        let prefab2 = Laya.Sprite3D.instantiate(rolePrefab2);
        let prefab3 = Laya.Sprite3D.instantiate(rolePrefab3);

        cloneRole.addChild(prefab1);
        cloneRole.addChild(prefab2);
        cloneRole.addChild(prefab3);

        prefab1.transform.localPosition = new Laya.Vector3;
        prefab2.transform.localPosition = new Laya.Vector3;
        prefab3.transform.localPosition = new Laya.Vector3;
        return cloneRole
    }

    changeSceneShader() {
        if (this.mainShow) {
            let meshNode = this.mainShow.getChildByName('MainScene');
            for (let i = 0; i < meshNode.numChildren; i++) {
                let child = meshNode.getChildAt(i);
                if (child instanceof Laya.MeshSprite3D) {
                    if (child.meshRenderer.sharedMaterial instanceof Laya.BlinnPhongMaterial) {
                        let unlitBlinnPhong = new UnlitBlinnPhongMaterial;
                        child.meshRenderer.sharedMaterial.cloneTo(unlitBlinnPhong);
                        unlitBlinnPhong.albedoTexture = child.meshRenderer.sharedMaterial.albedoTexture;
                        child.meshRenderer.sharedMaterial = unlitBlinnPhong;
                    }
                }
            }
        }
    }

    getUserInfo() {
        if (!window['wx']) return;
        (<any>wx).getSetting({
            success: (res: any) => {
                if (res.authSetting["scope.userInfo"]) {
                    // 必须是在用户已经授权的情况下调用
                    (<any>wx).getUserInfo({
                        success: (res: any) => {
                            window['UserInfo'] = res.userInfo;
                            this.playerName.text = window['UserInfo'].nickName;
                            this.playerHead.skin = window['UserInfo'].avatarUrl;
                        }
                    })
                }
            }
        })
    }

    getAuthorize() {
        if (window.wx) {
            if (Laya.LocalStorage.getItem('getUserInfo') != '1') {
                Laya.LocalStorage.setItem('getUserInfo', '1');
                const button = (<any>wx).createUserInfoButton({
                    type: 'text',
                    text: '',
                    style: {
                        left: 0,
                        top: 0,
                        width: Laya.stage.width,
                        height: Laya.stage.height
                    }
                })
                button.onTap((res: any) => {
                    if (res.userInfo) {
                        window['UserInfo'] = res.userInfo;
                        // 此处可以获取到用户信息
                        this.playerName.text = window['UserInfo'].nickName;
                        this.playerHead.skin = window['UserInfo'].avatarUrl;
                        mpsdk.Account.setAccountInfo(res.userInfo);
                    }
                    button.destroy();
                })
            }
        }
    }
}