import { ui } from "../../ui/layaMaxUI";
import WelfareRuntime from "./WelfareRuntime";

export default class WelfareCellRuntime extends ui.welfareCellUI {
    public set dataSource(data: any) {
        if (!data) return;
        this._dataSource = data;
        this.gameIcon.skin = data.icon;
        this.gameName.text = data.title;
        this.desc.text = "试玩30秒，返回即可领取";
        this.rewardNum.text = "10";
    }
    onAwake() {
        this.playBtn.on(Laya.Event.CLICK, this, () => {
            mpsdk.Ad.click(this._dataSource);
            WelfareRuntime.isTry = true;
            localStorage.setItem("tryPlayTimestamp", Date.now().toString());
        })
    }
}