import RoleBase from "./RoleBase";

export default class AnimationEventRuntime extends Laya.Script3D {
    roleBase: RoleBase = null;
    gameObject: Laya.Sprite3D = null;
    onAwake() {
        this.gameObject = this.owner as Laya.Sprite3D;
        this.roleBase = this.gameObject.parent.getComponent(RoleBase);
    }

    Ani_Attack(param1: number, param2: number, param3: string): void {
        this.roleBase && this.roleBase.onAttack(param1, param2, param3);
    }
    Ani_AttackEnd(param1: number, param2: number, param3: string): void {
        this.roleBase && this.roleBase.onAttackEnd(param1, param2, param3);
    }
}