import { ui } from "../../ui/layaMaxUI";

export default class SurrenderRuntime extends ui.surrenderUI {
    onAwake() {
        this.yesBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['yes'])
        this.noBtn.on(Laya.Event.CLICK, this, this.onMouseEvent, ['no'])
    }

    onMouseEvent(name: string, e: Laya.Event) {
        if (name == 'no') {
            this.close('no');
        } else if (name == 'yes') {
            this.close('yes');
        }
    }
}