import RoleBase from "./RoleBase";
import { RoleData } from "../data/GameData";

export default class RolePlayer extends RoleBase {

    onAwake() {
        super.onAwake();
        this.type = 0;
        this.moveDir = new Laya.Vector3(0, 0, 1);
        this.gameObject.transform.lookAt(new Laya.Vector3(0, 0, 1), new Laya.Vector3(0, 1, 0), true);
    }

    init(actorData: RoleData) {
        super.init(actorData);
    }
}