import GEvent from "../../GameFrame/event/GEvent";
import { ui } from "../../ui/layaMaxUI";
import { EventTypeEnum, SceneName } from "../GEnum";
import LotteryResultRuntime from "./LotteryResultRuntime";

export default class WelfareRuntime extends ui.welfareUI {
    public static isTry: boolean = false;
    public static jumpSuccess: boolean = false;
    private slideDir: number = 0;
    onAwake() {
        this.adList.vScrollBarSkin = null;
        //获取激励广告列表，不限数量，排序固定
        mpsdk.Ad.getExcitationList(true, 0, 100).then(adList => {
            //这里返回的是一个ad列表
            console.log('激励广告列表', adList);
            this.adList.dataSource = adList;
        });
        this.slideDir = 1;
        Laya.timer.frameLoop(1, this, this.onFrameLoop);
    }
    onFrameLoop() {
        if (this.adList.scrollBar.max > 0) {
            if (this.slideDir == 1) {
                this.adList.scrollBar.value += 0.1;
                if (this.adList.scrollBar.value >= this.adList.scrollBar.max) {
                    this.slideDir = 2;
                }
            } else if (this.slideDir == 2) {
                this.adList.scrollBar.value -= 0.1;
                if (this.adList.scrollBar.value <= this.adList.scrollBar.min) {
                    this.slideDir = 1;
                }
            }
        }
    }
    onOpened() {
        GEvent.on(EventTypeEnum.OnShow, this, this.onShow);
        GEvent.on(EventTypeEnum.OnShow, this, this.onHide);
    }
    onClosed() {
        GEvent.event(EventTypeEnum.MainShowOpen, true);
    }
    onDestroy() {
        GEvent.off(EventTypeEnum.OnShow, this, this.onShow);
        GEvent.off(EventTypeEnum.OnShow, this, this.onHide);
        Laya.timer.clearAll(this);
    }
    onHide() {
        if (WelfareRuntime.isTry) {
            WelfareRuntime.jumpSuccess = true;
        }
    }
    onShow() {
        if (WelfareRuntime.isTry) {
            WelfareRuntime.isTry = false;
            WelfareRuntime.jumpSuccess = false;
            let outTime = Number(localStorage.getItem("tryPlayTimestamp"));
            if (outTime && outTime > 0) {
                if (Date.now() - outTime >= 30 * 1000) {
                    let data = { desc: '+10', value: 10, type: 'diamond', isDirect: true };
                    Laya.Scene.load(SceneName.lotteryResult, Laya.Handler.create(this, (lotteryResult: LotteryResultRuntime) => {
                        lotteryResult.open(false);
                        lotteryResult.setData(data);
                    }))
                }
            }
        }
    }
}