    onEnemyUseFireBallSkill() {
        if (!GameDataManager.instance.isPVP) return;
        let delayTime = MathTool.getRandom(1, 10);
        Laya.timer.once(delayTime * 1000, this, () => {
            let num = -1;
            let index = -1;
            for (let i = 0; i < this.playerMonsterArray.length; i++) {
                const element = this.playerMonsterArray[i];
                if (element.length > num) {
                    index = i;
                    num = element.length;
                }
            }
            this.useFireBallSkill(1, index)
        })
    }

    skill(sector: number, road: number, type: number) {
        ResManager.playSound('Fireball2.mp3');
        let playerPoint1 = this.getRevivePointPos(0, road);
        let enemyPoint1 = this.getRevivePointPos(1, road);
        let allDis = playerPoint1.z - enemyPoint1.z;
        let sectorLength = allDis / 8;
        let enemys = this.enemyMonsterArray[road];
        if (type == 1) {
            enemys = this.playerMonsterArray[road];
        }
        for (let i = 0; i < enemys.length; i++) {
            const enemy = enemys[i];
            if (type == 0) {
                if (enemy.gameObject.transform.position.z > playerPoint1.z - sector * sectorLength) {
                    enemy.onHurt(GameDataManager.instance.fireballDamage);
                }
            } else {
                if (enemy.gameObject.transform.position.z < enemyPoint1.z + sector * sectorLength) {
                    enemy.onHurt(GameDataManager.instance.fireballDamage);
                }
            }
        }
    }
    onDestroy() {
        Laya.timer.clearAll(this);
        this.clearBattleGround();
        this.clearNode.destroy();
        this.effectScene3D.destroy();
        this.playerFort.setFire(1);
        this.enemyFort.setFire(1);
        this.playerFort.destroy();
        this.enemyFort.destroy();
        this.removeWeatherEffect();
        this.releaseCustomEvent();
    }

    playShowCard(bool: boolean) {
        if (!this.pickCard) return;
        if (this.standerdY == -1) {
            this.standerdY = this.pickCard.original.y;
        }
        if (bool) {
            this.isMoving = true;
            this.pickCard.original.zOrder = 1000;
            Laya.Tween.to(this.pickCard.original, { y: this.standerdY - 20 }, 200, Laya.Ease.backOut);
        } else {
            this.isDestroying = true;
            this.pickCard.original.lightFrame.visible = false;
            this.refreshListZOrder();
            this.cardList.updateZOrder();
            Laya.Tween.to(this.pickCard.original, { y: this.standerdY }, 200, Laya.Ease.backOut, Laya.Handler.create(this, () => {
                this.isMoving = false;
                this.isDestroying = false;
                if (this.pickCard) {
                    this.pickCard.destroy();
                    this.pickCard = null;
                }
            }));
        }
    }

    destroyPickRole() {
        if (this.pickRole) {
            this.pickRole.destroy();
            this.pickRole = null;
        }
    }

    refreshListZOrder() {
        for (let i = 0; i < this.cardList.length; i++) {
            let cellRuntime = this.cardList.getCell(i);
            cellRuntime.zOrder = i;
        }
    }

    setMineProgress(progress: number) {
        this.mineProgress.width = 657 * progress;
    }

