import { ui } from "../../ui/layaMaxUI";
import { SceneName } from "../GEnum";
import MainRuntime from "./MainRuntime";
import DataManager, { LocalStorageData } from "../../GameFrame/manager/DataManager";
import GuideManager from "./GuideManager";
import { AllPlatformAdMgr } from "../../GameFrame/advert/AllPlatformAdMgr";
import GEvent from "../../GameFrame/event/GEvent";
import ResManager from "../../gframe-work/manager/ResManager";
import GameDataManager from "../data/GameDataManager";
import UserDataManager from "../../gframe-work/manager/UserDataManager";
import MathTool from "../../gframe-work/tool/MathTool";
import CardManager from "./CardManager";

export default class CountRuntime extends ui.countUI {
    curLevelLocal: LocalStorageData = null;
    diamondLocal: LocalStorageData = null;
    moneyLocal: LocalStorageData = null;
    houseLocal: LocalStorageData = null;
    enemyPoolLocal: LocalStorageData = null;
    isSuccess: boolean = false;
    unitConfig: any[] = null;
    firstReward: number[] = null;
    reward: number[] = null;
    isFirst: boolean = false;
    starCondition: number[] = null;
    onAwake() {
        this.curLevelLocal = DataManager.get<LocalStorageData>('curLevel');
        this.diamondLocal = DataManager.get<LocalStorageData>('diamond');
        this.moneyLocal = DataManager.get<LocalStorageData>('gold');
        this.enemyPoolLocal = DataManager.get<LocalStorageData>('enemyPool');
        this.unitConfig = DataManager.get<any>('unit');
        this.closeEffect = null;
        this.back.on(Laya.Event.CLICK, this, this.onMouseEvent, ['back']);
        this.next.on(Laya.Event.CLICK, this, this.onMouseEvent, ['next']);
        this.kuang.on(Laya.Event.CLICK, this, this.onMouseEvent, ['kuang']);
        this.continue.on(Laya.Event.CLICK, this, this.onMouseEvent, ['continue']);
        this.goCompose.on(Laya.Event.CLICK, this, this.onMouseEvent, ['goCompose']);
        this.goGift.on(Laya.Event.CLICK, this, this.onMouseEvent, ['goGift']);
        this.goMagic.on(Laya.Event.CLICK, this, this.onMouseEvent, ['goMagic']);
        this.goShop.on(Laya.Event.CLICK, this, this.onMouseEvent, ['goShop']);
    }
    onOpened() {
        AllPlatformAdMgr.Instance.showBannerAd();
    }

    onClosed() {
        AllPlatformAdMgr.Instance.hideBannerAd();
        AllPlatformAdMgr.Instance.initBannerAd();
    }

    setSuccess(bool: boolean) {
        GEvent.event('BreakPoint', ['关卡结束', this.curLevelLocal.value]);
        this.isSuccess = bool;
        this.curLevelLocal = DataManager.get<LocalStorageData>('curLevel');
        this.updateLevelData();
        if (bool) {
			this.upgradeStage();
			this.popInsertAd();
            this.kuang.visible = true;
            this.gou.visible = true;
            this.winBox.visible = true;
            this.composeTitle.visible = false;
            let diamondNum = 0;
            let goldNum = this.reward[1];
            if (this.isFirst) {
                diamondNum += this.firstReward[1];
            }
            let rewardExtraPer = GameDataManager.instance.getPropInfo(1005).value;
            goldNum = Math.floor(goldNum * (1 + rewardExtraPer));
            diamondNum = Math.floor(diamondNum * (1 + rewardExtraPer));
            this.goldLabel.text = goldNum.toString();
            this.diamondLabel.text = diamondNum.toString();
            ResManager.playSound('GameSuccess.mp3');
            this.title.skin = 'count/Ui_Victory.png';
            this.light.skin = 'count/Ui_VictoryLight.png';
            this.lostFrame.visible = false;
            this.next.visible = true;
            this.continue.visible = false;
            if (GameDataManager.instance.isPVP) {
                let pvpWinCountLocal = DataManager.get<LocalStorageData>('pvpWinCount');
                let pvpConfig = DataManager.get<any>('pvp');
                let curDivisionLocal = DataManager.get<LocalStorageData>('curDivision');
                pvpWinCountLocal.value++;
                let nextDivisionData = pvpConfig[curDivisionLocal.value];
                if (nextDivisionData && pvpWinCountLocal.value == nextDivisionData.winCount) {
                    pvpWinCountLocal.value = 0;
                    curDivisionLocal.value++;
                }
                this.continue.visible = true;
                this.next.visible = false;
            } else {
                GEvent.event('BreakPoint', ['关卡进度', this.curLevelLocal.value + 1]);
            }
        } else {
            if (!GameDataManager.instance.isPVP) {
                GEvent.event('BreakPoint', ['关卡失败', this.curLevelLocal.value]);
            }
            this.kuang.visible = false;
            this.winBox.visible = false;
            this.composeTitle.visible = true;
            ResManager.playSound('GameLose.mp3');
            this.lostFrame.visible = true;
            this.next.visible = false;
            this.continue.visible = false;
            this.title.skin = 'count/Ui_Fail.png';
            this.light.skin = 'count/Ui_FailLight.png';
        }
        if (this.curLevelLocal.value <= 2) {
            this.next.visible = false;
            this.lostFrame.visible = false;
            this.continue.visible = false;
            this.back.visible = true;
            this.back.x = 258;
            this.kuang.visible = false;
        }
        if (this.isSuccess) this.curLevelLocal.value++;
        if (this.isSuccess && !GameDataManager.instance.isPVP) this.unlockCard();
    }

    onMouseEvent(name: string, e: Laya.Event) {
        if (e.type == Laya.Event.CLICK) {
            ResManager.playSound('Open.mp3');
            if (name == 'back' || name == 'goCompose' || name == "goMagic" || name == "goShop" || name == "goGift") {
                if (this.isSuccess) {
                    if (this.kuang.visible == true && this.gou.visible == true) {
                        AllPlatformAdMgr.Instance.showVideoAd(() => {
                            this.receiveReward(true);
                            this.backToMainEvent(name);
                        })
                    } else {
                        this.receiveReward(false);
                        this.backToMainEvent(name);
                    }
                } else {
                    this.backToMainEvent(name);
                }
            } else if (name == 'next') {
                if (GuideManager.Instance.guideFinish == false) {
                    return;
                }
                if (this.kuang.visible == true && this.gou.visible == true) {
                    AllPlatformAdMgr.Instance.showVideoAd(() => {
                        this.receiveReward(true);
                        this.nextBtnEvent();
                    })
                } else {
                    this.receiveReward(false);
                    this.nextBtnEvent();
                }
            } else if (name == 'kuang') {
                this.gou.visible = !this.gou.visible;
            } else if (name == 'continue') {
                if (this.kuang.visible == true && this.gou.visible == true) {
                    AllPlatformAdMgr.Instance.showVideoAd(() => {
                        this.receiveReward(true);
                        this.close();
                        Laya.Scene.open(SceneName.match);
                    })
                } else {
                    this.receiveReward(false);
                    this.close();
                    Laya.Scene.open(SceneName.match);
                }
            }
        }
    }
    popInsertAd() {
        if (this.curLevelLocal.value >= 4 && this.curLevelLocal.value % 2 == 0) {
            AllPlatformAdMgr.Instance.showInterstitialAd(null);
        }
    }

    unlockCard() {
        let pool: number[] = this.enemyPoolLocal.value;
        let unlockIdArr = [];
        for (let i = 0; i < 16; i++) {
            if (pool.indexOf(i) == -1) unlockIdArr.push(i);
        }
        if (unlockIdArr.length > 0) {
            let unlockId = unlockIdArr[MathTool.getRandom(0, unlockIdArr.length - 1)];
            pool.push(unlockId);
            this.enemyPoolLocal.value = pool;
        }
    }
    receiveReward(isDouble: boolean) {
        let diamondValue = Number(this.diamondLabel.text);
        let goldValue = Number(this.goldLabel.text);
        let pvpCoinValue = 0;
        let pvpCoinLocal = DataManager.get<LocalStorageData>('pvpCoin');
        let pvpConfig = DataManager.get<any>('pvp');
        let curDivisionLocal = DataManager.get<LocalStorageData>('curDivision');
        let curDivisionData = pvpConfig[curDivisionLocal.value - 1];
        pvpCoinValue = curDivisionData.pvpCoin;
        if (isDouble) {
            diamondValue *= 2;
            goldValue *= 2;
            pvpCoinValue *= 2;
        }
        pvpCoinLocal.value += pvpCoinValue;
        this.diamondLocal.value = this.diamondLocal.value + diamondValue;
        this.moneyLocal.value = this.moneyLocal.value + goldValue;
        if (this.isFirst) {
            let specialRewardId = this.firstReward[2];
            if (specialRewardId >= 50000 && specialRewardId <= 50019) {
                let unitId = specialRewardId % 10000;
                let fightLevel = this.firstReward[3];
                let cardData = CardManager.instance.getCardData(unitId);
                cardData.exp = CardManager.instance.getBaseExpByFightLevel(fightLevel);
                CardManager.instance.addNewCard([cardData]);
            } else if (specialRewardId >= 1101 && specialRewardId <= 1706) {
                let rewardNum = this.firstReward[3];
                let cardIndex = (specialRewardId - specialRewardId % 100) / 100 % 10 - 1;
                let magicCardNum = UserDataManager.instance.magicCardNum;
                magicCardNum[cardIndex] += rewardNum;
                UserDataManager.instance.magicCardNum = magicCardNum;
            }
        }
    }

    nextBtnEvent() {
        this.close();
        Laya.Scene.open(SceneName.gameLoad);
    }

    backToMainEvent(name: string) {
        Laya.Scene.load(SceneName.main, Laya.Handler.create(this, (main: MainRuntime) => {
            this.close('back');
            if (name == 'goCompose') {
                main.curIndex = 1;
            } else if (name == 'goMagic') {
                main.curIndex = 2;
            } else if (name == 'goGift') {
                main.curIndex = 2;
            } else if (name == 'goShop') {
                main.curIndex = 0;
            }
            main.open();
        }))
    }

    upgradeStage() {
        let playStage = GameDataManager.instance.playStage;
        let levelId = playStage[1];
        let chapterId = playStage[0];
        let starRecord = UserDataManager.instance.starRecord;
        let playNormalizeLevel = (chapterId - 1) * 10 + levelId;
        let starRec = starRecord[playNormalizeLevel - 1];
        let starNow = this.countStar();
        let curStage = UserDataManager.instance.curStage;
        if (starRec == undefined) {
            //没玩过
            levelId++;
            if (levelId > 10 && chapterId < 3) {
                levelId = 1;
                chapterId++;
                chapterId = chapterId < 3 ? chapterId : 3;
            }
            levelId = levelId < 10 ? levelId : 10;
            curStage[0] = chapterId;
            curStage[1] = levelId;
            starRecord.push(starNow);
            UserDataManager.instance.curStage = curStage;
            this.isFirst = true;
        } else {
            this.isFirst = false;
            if (starNow > starRec) {
                starRecord[playNormalizeLevel - 1] = starNow;
            }
        }
        UserDataManager.instance.starRecord = starRecord;
    }
    countStar() {
        let star = 1;
        let score = GameDataManager.instance.countStar[this.starCondition[0]];
        if (score <= this.starCondition[1]) {
            star++;
        }
        if (score <= this.starCondition[0]) {
            star++;
        }
        return star;
    }

    updateLevelData() {
        this.isFirst = false;
        let mapConfig = ResManager.getRes<any[]>("map.json");
        let playLevel = GameDataManager.instance.playStage;
        let normalizedLevel = (playLevel[0] - 1) * 10 + playLevel[1];
        let levelData = mapConfig[normalizedLevel - 1];
        this.firstReward = JSON.parse(levelData.firstReward);
        this.reward = JSON.parse(levelData.reward);
        this.starCondition = JSON.parse(levelData.fullStarCond);
    }
}